import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';

const CART_URL = 'https://www.lax.com/checkout/cart/';

// Behavior description:
// we can apply only 1 coupon
// we can remove applied code

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;

		this.STACKABLE = false;
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		return Promise.each(codes, code =>
			// Codes replace each other that is why we don't need _removeCode() method
			this._applyCode(code, this._getApplyRequestData(this.cartPageHTML)).then(responseString => {
				const finalPrice = this._parseTotal(responseString);
				const finalDiscount = originalPrice - finalPrice;
				return bus.$emit('code-checked', { code, finalPrice, finalDiscount });
			}),
		);
	}

	// for cleaning unnecessary messages
	beforeApplyCodes() {
		const iframeElem = document.createElement('iframe');
		iframeElem.src = 'https://www.lax.com/';
		Object.assign(iframeElem.style, { display: 'none' });

		document.documentElement.appendChild(iframeElem);
	}
};

export default {
	name: 'Lax',
	domain: 'lax.com',
	Driver,
	isCartEmpty() {
		return Boolean(document.querySelector('.no-items h1'));
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_URL);
	},
};
