import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { DOM, format, log } from '../../../utils';

const { elemToFloat } = format;
const { extractElement } = DOM;

const CART_URL = 'https://totokaelo.com/checkout/cart';
const REQUEST_URL = `${CART_URL}/couponPost`;

/** Driver behavior description:
    Existing code: yes
    Remove code: yes
    Stackable: no
    Free gift support: no
    isValid implemented: yes
    Manually apply code: no
    Can apply only on cart page
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.APPLIED_CODES_SELECTOR = '#coupon_code';
		this.TOTAL_SELECTOR = '#shopping-cart-totals-table > tbody > tr:nth-child(1) > td:nth-child(2) > span';
		this.TOTAL_DISCOUNT = '#shopping-cart-totals-table > tbody > tr:nth-child(2) > td:nth-child(2) > span';
		this.ERROR_MESSAGE = '.error-msg';
		this.ITEM_CONTAINER_SELECTOR = null;
	}

	beforeTestCodes() {
		this.PARAM_BODY = new URLSearchParams();
		return Promise.resolve();
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				isValid: false,
			};
			return this._applyCode(code).then(responseHTML => {
				result.isValid = Boolean(!extractElement(this.ERROR_MESSAGE, responseHTML));
				if (!result.isValid) {
					return bus.$emit('code-checked', result);
				}
				result.finalDiscount = this._getDiscountValue(responseHTML);

				result.finalPrice = originalPrice - result.finalDiscount;
				return this._removeCode(code).then(() => bus.$emit('code-checked', result));
			});
		});
	}

	_applyCode(code) {
		this.PARAM_BODY.set('remove', 0);
		this.PARAM_BODY.set('coupon_code', code);
		return fetch(REQUEST_URL, {
			method: 'POST',
			credentials: 'include',
			body: this.PARAM_BODY,
		}).then(response => response.text());
	}

	_getDiscountValue(data) {
		const discountEl = extractElement(this.TOTAL_DISCOUNT, data);
		return discountEl ? elemToFloat(discountEl) : 0;
	}
};

export default {
	name: 'Totokaelo',
	domain: 'totokaelo.com',
	maxCoupons: 5,
	Driver,
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_URL);
	},
};
