import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';

import { elemToFloat } from '../../../utils/format';
import { extractElement } from '../../../utils/DOM';

const BASE_URL = 'https://www.hydroflask.com';
const CART_URL = `${BASE_URL}/checkout/cart`;

/*
Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ isValid implemented: yes
+ Codes can be replaced by another code: yes
+ Pages, where extension works: cart
+ Is driver SPA: no
+ Gather codes on site pages: yes (on cart page: https://www.hydroflask.com/checkout/cart)
*/

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.BASE_API_URL = `${BASE_URL}/discountpopup/index`;
		this.TOTAL_SELECTOR = '.grandtotal span';
		this.COUPONS_BANNER_SELECTOR = '.sf-announcement-banner';
		this.MERCHANT_CODES_REGEX = /use code: ([a-z0-9-]{2,})/i;
	}

	getMerchantCodes() {
		const bannerElement = document.querySelector(this.COUPONS_BANNER_SELECTOR);
		const matchedCode = bannerElement && bannerElement.textContent.match(this.MERCHANT_CODES_REGEX);
		return matchedCode ? [matchedCode[1]] : [];
	}

	removeCodes() {
		return this._removeCode()
			.then(() => this._getUpdatedCart())
			.then(responseJSON => this._parseTotal(responseJSON));
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				isValid: false,
			};
			return this._applyCode(code).then(response => {
				if (response.error) {
					return bus.$emit('code-checked', result);
				}
				return this._getUpdatedCart().then(responseJSON => {
					result.finalPrice = this._parseTotal(responseJSON);
					result.finalDiscount = originalPrice - result.finalPrice;
					if (originalPrice > result.finalPrice) {
						result.isValid = true;
					}
					return this._removeCode().then(() => bus.$emit('code-checked', result));
				});
			});
		});
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	_applyCode(code) {
		return this._manageCode(`${this.BASE_API_URL}/apply/?coupon_code=${code}`);
	}

	_removeCode() {
		return this._manageCode(`${this.BASE_API_URL}/apply/?remove=1`);
	}

	_getUpdatedCart() {
		return this._manageCode(`${this.BASE_API_URL}/reloadtotal/`);
	}

	_manageCode(url) {
		return fetch(url, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.json());
	}

	_parseTotal(data = false) {
		return elemToFloat(extractElement(this.TOTAL_SELECTOR, data && data.block));
	}
};

export default {
	name: 'Hudro Flask',
	domain: 'hydroflask.com',
	Driver,
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) && document.querySelector('#coupon_code');
	},
};
