import Promise from 'bluebird';
import MagentoTemplate from './_base';
import { sessionTimer } from '../../lib';

const BASE_URL = 'https://www.clearbags.com/checkout';

/* Behaviour description:
-    Existing code: yes / no
-    Remove code: yes
-    Stackable: no
-    Free gift support: yes
-    isValid implemented: no
Optional:
-	 sessionTimer 15 min
-    Is driver SPA and add the condition to show popup: yes (run on cart and checkout(user data will be erased),
		no empty cart, total and code input field are present)
 */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = `${BASE_URL}/cart/`;

		this.APPLIED_CODES_SELECTOR = '#coupon_code, #discount-code';
		this.TOTAL_SELECTOR = '.grand .price, .grand.totals .price';
		this.ITEM_CONTAINER_SELECTOR = '#shopping-cart-table .cart, #opc-sidebar .minicart-items .product-item';

		this.INPUT_CODE_SELECTOR = '.cart-discount, #discount-code';
		this.EMPTY_CART_SELECTOR = '.cart-empty';

		this.totalRegExp = {
			regexObj: /grand_total":"(\d+\.\d+)/,
			groupNumber: 1,
		};

		this.afterCodeCheck = () => {
			// Clear this cookie to remove 'applied code' messages
			document.cookie = '';
		};

		this.NEED_REMOVE_CODE = false;
	}

	checkCondition() {
		const totalValue = document.querySelector(this.TOTAL_SELECTOR);

		const isInputElement = document.querySelector(this.INPUT_CODE_SELECTOR);

		const emptyCart = document.querySelector(this.EMPTY_CART_SELECTOR);
		const correctPage = window.location.href.startsWith(BASE_URL);

		const currentState = totalValue && isInputElement && correctPage && !emptyCart;
		return currentState;
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();
		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	applyCodes(codes) {
		this.afterCodeCheck();
		return this._applyCode(codes[0], this._getApplyRequestData(this.cartPageHTML)).then(() => {
			// for cleaning error messages after applying code
			const iframeElem = document.createElement('iframe');
			iframeElem.src = 'https://www.clearbags.com/';
			Object.assign(iframeElem.style, { display: 'none' });
			document.documentElement.appendChild(iframeElem);
			return Promise.resolve();
		});
	}
};

export default {
	name: 'ClearBags',
	domain: 'clearbags.com',
	Driver,
	isSinglePageApp: true,
};
