import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { log, format, DOM } from '../../../utils';

const { toFloat, elemToFloat } = format;
const { extractElementFromHTML, extractElement, extractFormValues } = DOM;

const BASE_URL = 'https://www.jackrabbit.com';
const CART_URL = `${BASE_URL}/checkout/cart`;
const CHECKOUT_URL = `${BASE_URL}/onestepcheckout`;
const CHECKOUT_REQUEST = `${CHECKOUT_URL}/ajax/add_coupon/`;
const IS_CART_PAGE = window.location.href.startsWith(CART_URL);

/* Driver behavior description:
    Existing code: yes
    Remove code: yes
    Stackable: no
    Free gift support: yes
    isValid implemented: yes
    Manually apply code: no
Optional:
    Show message about free shipping on the cart page (without discount info): no
    Is driver SPA and add the condition to show popup: yes
    Gather codes on site pages: no
    test codes on the cart page and payment step

Rewrote some base methods, because on the checkout page merchant has not
same to cart page behaviour (another requests, return json response) */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.APPLIED_CODES_SELECTOR = '#coupon_code, #id_couponcode';
		this.TOTAL_SELECTOR =
			'#shopping-cart-totals-table strong .price, .onestepcheckout-totals strong:last-child .price';
		this.CHECKOUT_SECTION_SELECTOR = '#paymentfobtntext.active';

		this.ITEM_CONTAINER_SELECTOR = '.cart-top-sec.clearfix';
		this.EMPTY_CART_SELECTOR = '.cart-empty';

		this.APPLY_CODE_PARAM_NAME = IS_CART_PAGE ? 'coupon_code' : 'code';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.VALID_CHECKOUT_PAGE = true;
		this.CHARGES_REGEX = /(shipping|tax)/i;
		this.CHARGES_SELECTOR = '.onestepcheckout-totals td:last-child, #shopping-cart-totals-table tr td:last-child';

		this.MANAGE_CODE_HEADERS = {
			accept: 'text/javascript, text/html, application/xml, text/xml, */*',
			'content-type': 'application/x-www-form-urlencoded; charset=UTF-8',
			'x-requested-with': 'XMLHttpRequest',
			'x-prototype-version': 1.7,
		};
	}

	checkCondition() {
		const cartEmpty = document.querySelector(this.EMPTY_CART_SELECTOR);
		const currentState =
			document.querySelector(this.APPLIED_CODES_SELECTOR) &&
			((window.location.href.startsWith(CART_URL) || document.querySelector(this.CHECKOUT_SECTION_SELECTOR)) &&
				!cartEmpty);
		return currentState;
	}

	getExistingCodes() {
		const codesElement = this.cartPageHTML
			? extractElementFromHTML(this.cartPageHTML, this.APPLIED_CODES_SELECTOR)
			: document.querySelector(this.APPLIED_CODES_SELECTOR);
		const codes = [codesElement.value].filter(Boolean);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	checkCodes(codes, originalPrice = this._parseTotal(), originalItemsCount = this._parseItemsCount()) {
		log('originalPrice: %s', originalPrice);
		log('originalItemsCount: %s', originalItemsCount);

		return Promise.each(codes, code =>
			this._applyCode(code, this._getApplyRequestData(this.cartPageHTML)).then(responseString => {
				const finalPrice = this._parseTotal(responseString);
				const finalDiscount = originalPrice - finalPrice;
				const freeGifts = this._parseItemsCount(responseString) - originalItemsCount;
				const isValid = typeof responseString === 'object' ? responseString.success : true;
				const result = { code, finalPrice, finalDiscount, freeGifts, isValid };
				return this._removeCode(code, this._getRemoveRequestData(responseString)).then(() =>
					bus.$emit('code-checked', result),
				);
			}),
		);
	}

	removeCodes(codes) {
		return this._removeCode(codes[0], this._getRemoveRequestData(this.cartPageHTML)).then(responseString =>
			this._parseTotal(responseString),
		);
	}

	applyCodes(codes) {
		[this.bestCode] = codes;
		return this._applyCode(codes[0], this._getApplyRequestData(this.cartPageHTML));
	}

	async getPreTaxShippingTotal() {
		const cartStr = await this._applyCode(this.bestCode || 'fake', this._getApplyRequestData(this.cartPageHTML));
		const total = this._parseTotal(cartStr);
		const taxes = [...extractElement(this.CHARGES_SELECTOR, cartStr.summary, true)]
			.filter(el => el.parentElement.textContent.match(this.CHARGES_REGEX))
			.map(elemToFloat)
			.filter(Boolean)
			.reduce((charges, charge) => charges + charge, 0); // shipping & tax might not be counted on cart
		return total - taxes;
	}

	_getApplyRequestData(htmlString) {
		if (!this.applyRequestData && IS_CART_PAGE) {
			// Assumed that this form always exists
			this.applyRequestData = extractFormValues(this.APPLY_COUPON_FORM_SELECTOR, htmlString);
			Object.entries(this.APPLY_REQUEST_EXTRA_VALUES).forEach(([key, value]) => {
				this.applyRequestData.params.set(key, value);
			});
		} else if (!this.applyRequestData && !IS_CART_PAGE) {
			this.applyRequestData = {
				action: CHECKOUT_REQUEST,
				params: new URLSearchParams(),
			};
		}
		return this.applyRequestData;
	}

	_getRemoveRequestData(htmlString) {
		if (!this.removeRequestData && IS_CART_PAGE) {
			// Form may not exists if there is no code applied
			this.removeRequestData = extractFormValues(this.REMOVE_COUPON_FORM_SELECTOR, htmlString);
			//  If we succeeded, add extra values
			Object.entries(this.REMOVE_REQUEST_EXTRA_VALUES).forEach(([key, value]) => {
				this.removeRequestData.params.set(key, value);
			});
		} else if (!this.removeRequestData && !IS_CART_PAGE) {
			this.removeRequestData = {
				action: CHECKOUT_REQUEST,
				params: new URLSearchParams({
					remove: 1,
				}),
			};
		}
		return this.removeRequestData;
	}

	_manageCode(requestURL, bodyObj) {
		return fetch(requestURL, {
			method: 'POST',
			credentials: 'include',
			headers: this.MANAGE_CODE_HEADERS,
			body: bodyObj,
		}).then(response => (IS_CART_PAGE ? response.text() : response.json()));
	}

	_parseTotal(htmlString) {
		let totalElement;
		if (htmlString) {
			if (!IS_CART_PAGE) {
				totalElement = extractElementFromHTML(htmlString.summary, this.TOTAL_SELECTOR).textContent;
			} else if (this.totalRegExp) {
				totalElement = htmlString.match(this.totalRegExp.regexObj)[this.totalRegExp.groupNumber];
			} else {
				totalElement = extractElementFromHTML(htmlString, this.TOTAL_SELECTOR).textContent;
			}
		} else {
			totalElement = document.querySelector(this.TOTAL_SELECTOR).textContent;
		}
		return toFloat(totalElement);
	}

	_parseItemsCount(data) {
		return this._isOnCart()
			? super._parseItemsCount()
			: extractElement(this.ITEM_CONTAINER_SELECTOR, data ? data.summary : null, true).length;
	}
};

export default {
	name: 'JackRabbit',
	domain: 'jackrabbit.com',
	isSinglePageApp: true,
	maxCoupons: 3, // due to time
	Driver,
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) || href.includes(CHECKOUT_URL);
	},
};
