import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { log, browserUtils } from '../../../utils';

const { getCookie } = browserUtils;

const CHECKOUT_URL = 'https://www.the-cover-store.com/checkout';
const CART_URL = `${CHECKOUT_URL}/cart`;
const IS_ON_CART = window.location.href.startsWith(CART_URL);

/** Behavior description:
 * - can get existing codes
 * - can remove codes
 * - valid code can be replaced with another valid code
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;

		this.APPLIED_CODES_SELECTOR = '#coupon_code';
		this.TOTAL_SELECTOR = '.grand.totals strong .price';

		this.totalRegExp = {
			regexObj: /grand_total":"([\d.,]+)/,
			groupNumber: 1,
		};

		this.NEED_REMOVE_CODE = false;
	}

	watchForCartUrl() {
		// activate extension when total appers
		let previousState = false;
		setInterval(() => {
			const totalValue = document.querySelector(this.TOTAL_SELECTOR);
			const windowLocation = window.location.href;
			const correctPage = windowLocation.startsWith(CART_URL) || windowLocation.startsWith(CHECKOUT_URL);

			const currentState = totalValue && correctPage;

			if (currentState !== previousState) {
				bus.$emit(currentState ? 'cart-active' : 'cart-inactive');
			}
			previousState = currentState;
		}, 1000);
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log('originalPrice: %s', originalPrice);

		return Promise.each(codes, code =>
			this._applyCode(code, this._getApplyRequestData(this.cartPageHTML)).then(responseString => {
				const finalPrice = this._parseTotal(responseString);
				const finalDiscount = originalPrice - finalPrice;
				const isValid = finalDiscount > 0;

				return bus.$emit('code-checked', { code, finalPrice, finalDiscount, isValid });
			}),
		);
	}

	beforeApplyCodes() {
		// to remove error messages after code testing
		if (getCookie('mage-messages')) {
			document.cookie = 'mage-messages=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
			return Promise.resolve();
		}
		const iframeElem = document.createElement('iframe');
		iframeElem.src = `${IS_ON_CART ? CART_URL : CHECKOUT_URL}/`;
		iframeElem.styledisplay = 'none';
		document.documentElement.appendChild(iframeElem);
		return Promise.resolve();
	}

	applyCodes(codes) {
		return this._applyCode(codes[0], this._getApplyRequestData(this.cartPageHTML));
	}
};

export default {
	name: 'The Cover Store',
	domain: 'the-cover-store.com',
	// decreased number of coupons, because site can be very slow
	maxCoupons: 10,
	Driver,
	isSinglePageApp: true,
	isOnCartUrl(href) {
		return href.startsWith(CHECKOUT_URL);
	},
};
