import Promise from 'bluebird';
import bus from '../../bus';
import MagentoTemplate from './_base';
import { format, log } from '../../../utils';

const { toFloat } = format;

/*
Driver behavior description:
+ IMPORTANT: Cannot get existing code, thats why, if user already has applied code, we show that he already has the best discount;
+ Gather codes on site pages: depends on site updates (yes); In general on the top of the page.
+ Existing code: no
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ Codes can be replaced by another code: no, need to remove code before applying a new one.
+ Manually apply code: no
+ Pages, where extension works:
https://www.betabrand.com/cart/checkout;
Also in the menu that is sliding after the clicking on cart item.
+ Is driver SPA yes
*/

const BASE_URL = 'https://www.betabrand.com';

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = `${BASE_URL}/cart/checkout`;
		this.APPLIED_CODES_SELECTOR = '#cart-discount';
		this.SIDEBAR_SELECTOR = 'body.has-cart';
		this.CART_COUNT_SELECTOR = '#cart-toggle :last-child';
		this.TOTAL_SELECTOR = '.justified-list.ng-scope .fancy.h4';
		this.DISCOUNT_LABEL_SELECTOR = '.discount-label';
		this.MERCHANT_CODE_SELECTOR = '.skinny-nav .skinny-nav--right :first-child, .skinny-nav--message a';
		this.CART_BUTTON_SELECTOR = '#cart-toggle';
		this.MERCHANT_CODE_REGEX = /[A-Z]{2,}/;
		this.USER_HAS_DISCOUNT = false;
		this.CODE_TESTING_INPROGRESS = false;
		this.interval = null;
	}

	checkCondition() {
		const totalAmountElement = document.querySelector(this.TOTAL_SELECTOR);
		const sideBarElement = document.querySelector(this.SIDEBAR_SELECTOR);
		const applyCodeInputElement = document.querySelector(this.APPLIED_CODES_SELECTOR);
		const cartCount = document.querySelector(this.CART_COUNT_SELECTOR);
		return (
			(sideBarElement &&
				cartCount &&
				cartCount.textContent !== '0' &&
				applyCodeInputElement &&
				totalAmountElement &&
				totalAmountElement.textContent) ||
			(window.location.href.startsWith(this.CART_URL) &&
				applyCodeInputElement &&
				totalAmountElement &&
				totalAmountElement.textContent)
		);
	}

	watchForCartUrl() {
		super.watchForCartUrl();
		this.interval = setInterval(() => this._checkUserTestingPlaceOnExisting(), 1000);
	}

	beforeTestCodes() {
		const sidebarElement = document.querySelector(this.SIDEBAR_SELECTOR);
		const discountElement = document.querySelector(this.DISCOUNT_LABEL_SELECTOR);
		this.CODE_TESTING_INPROGRESS = true;
		if (discountElement) {
			this.USER_HAS_DISCOUNT = true;
		}
		if (sidebarElement) {
			localStorage.setItem('CodeTestingPlace', JSON.stringify({ sidebar: true }));
		}
	}

	getMerchantCodes() {
		const promoCodeText = document.querySelector(this.MERCHANT_CODE_SELECTOR).textContent;
		const codes = [];
		if (promoCodeText && promoCodeText.length && promoCodeText.match(this.MERCHANT_CODE_REGEX)) {
			codes.push(promoCodeText.match(this.MERCHANT_CODE_REGEX)[0]);
		}
		return codes;
	}

	getExistingCodes() {
		return {
			codes: [],
			codeCount: 0,
		};
	}

	removeCodes() {
		return this._removeCode().then(responseJSON => ({ originalPrice: this._parseTotal(responseJSON) }));
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log('originalPrice: %o', originalPrice);
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				isValid: true,
			};
			if (this.USER_HAS_DISCOUNT) {
				result.isValid = false;
				return Promise.delay(1500).then(() => bus.$emit('code-checked', result));
			}
			return this._applyCode(code).then(resJSON => {
				if (resJSON && resJSON.messages && resJSON.messages.error) {
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}
				result.finalPrice = this._parseTotal(resJSON);
				result.finalDiscount = originalPrice - result.finalPrice;
				return this.removeCodes().then(() => bus.$emit('code-checked', result));
			});
		});
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	_applyCode(code) {
		return this._manageCode(`${BASE_URL}/api/rest/coupon`, 'POST', JSON.stringify({ code }));
	}

	_removeCode() {
		return this._manageCode(`${BASE_URL}/api/rest/coupon/delete`, 'DELETE', null);
	}

	_manageCode(url, type, body) {
		return fetch(url, {
			method: type,
			body,
			headers: {
				accept: ' application/json, text/plain, */*',
				'content-type': 'application/json;charset=UTF-8',
			},
		}).then(result => result.json());
	}

	_parseTotal(data) {
		if (data) {
			let totalAmount = 0;
			if (data && data.totals) {
				data.totals.forEach(object => {
					if (object.code === 'grand_total') {
						totalAmount = object.value;
					}
				});
			}
			return totalAmount;
		}
		return toFloat(document.querySelector(this.TOTAL_SELECTOR).textContent);
	}

	_checkUserTestingPlaceOnExisting() {
		const codeTestingPlace = localStorage.getItem('CodeTestingPlace');
		if (codeTestingPlace && !this.CODE_TESTING_INPROGRESS) {
			localStorage.removeItem('CodeTestingPlace');
			document.querySelector(this.CART_BUTTON_SELECTOR).click();
			clearInterval(this.interval);
		}
	}
};
export default {
	name: 'Betabrand',
	domain: 'betabrand.com',
	Driver,
	isSinglePageApp: true,
};
