import MagentoTemplate from './_base';
import bus from '../../bus';
import { format } from '../../../utils';

const { toFloat, elemToFloat } = format;

const BASE_URL = 'https://www.tidyliving.com/checkout';
const CART_URL = `${BASE_URL}/cart`;

// Behavior description:
// we can apply only one code;
// we can remove code;
// we can't apply new coupon without removing previous;
// code can be applied on cart page and on checkout page;
// site is very slow, so we check only 10 codes;

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;

		this.APPLIED_CODES_SELECTOR = '.discount.coupon';
		this.TOTAL_SELECTOR = '.grand.totals .price';
		this.CODE_INPUT_SELECTOR = '[name="coupon_code"]';

		this.TOTAL_REGEX = new RegExp(/"grand_total":(.*?),/);

		this.afterCodeCheck = () => {
			// Clear this cookie to remove 'applied code' messages
			document.cookie = 'mage-messages=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
		};
	}

	watchForCartUrl() {
		const inrerval = setInterval(() => {
			const onCartPage = window.location.href.startsWith(BASE_URL);
			const totalElement = document.querySelector(this.TOTAL_SELECTOR);
			const codeInput = document.querySelector(this.CODE_INPUT_SELECTOR);
			if (onCartPage && totalElement && codeInput) {
				bus.$emit('cart-active');
				clearInterval(inrerval);
			}
		}, 500);
	}

	getExistingCodes() {
		const codes = [document.querySelector(this.APPLIED_CODES_SELECTOR)]
			.filter(el => Boolean(el))
			.map(el => el.innerText);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	_parseTotal(html) {
		if (html) {
			return toFloat(html.match(this.TOTAL_REGEX)[1].replace(/"/g, ''));
		}
		return elemToFloat(document.querySelector(this.TOTAL_SELECTOR));
	}
};

export default {
	name: 'Tidy Living',
	domain: 'tidyliving.com',
	Driver,
	maxCoupons: 10,
	isSinglePageApp: true,
	isOnCartUrl(href) {
		return href.startsWith(BASE_URL);
	},
};
