import MagentoTemplate from './_base';
import bus from '../../bus';
import { sendXhr, format } from '../../../utils';

const { toFloat } = format;

// Behavior description:
// we can apply only 1 coupon
// we can remove applied code
// requests of this site are very slow

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = 'https://www.mophie.com/shop/checkout/cart';
		this.CART_URL_HTTP = 'http://www.mophie.com/shop/checkout/cart';
		this.REQUEST_URL = this._isOnCart() ? this.CART_URL : this.CART_URL_HTTP;
		this.APPLIED_CODE_SELECTOR = '.promo-code strong';
		this.TOTAL_SELECTOR = this._isOnCart()
			? '#shopping-cart-totals-table strong:first-child .price'
			: '.order-total .price';
		this.ITEM_SELECTOR = '.item, .product-info';
		this.STACKABLE = false;
		this.MANAGE_CODE_HEADERS = {
			'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
			'X-Requested-With': 'XMLHttpRequest',
		};
		this.bodyData = new URLSearchParams();
		this.stringIsObj = true;
	}

	watchForCartUrl() {
		const interval = setInterval(() => {
			const currentState = document
				.querySelector('body')
				.getAttribute('class')
				.includes('open-now');
			const item = Boolean(document.querySelector(this.ITEM_SELECTOR));
			if (item && (this._isOnCart() || currentState)) {
				bus.$emit('cart-active');
				clearInterval(interval);
			}
		}, 1000);
	}

	beforeTestCodes() {
		return false;
	}

	getExistingCodes() {
		const codes = [document.querySelector(this.APPLIED_CODE_SELECTOR)]
			.filter(el => Boolean(el))
			.map(element => element.innerText);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes() {
		// 'beforeCheckCodes()' does the same
		return false;
	}

	beforeCheckCodes() {
		// there isn't always opportunity to get existing code
		return this._removeCode().then(responseJSON => this._parseTotal(responseJSON));
	}

	_applyCode(code) {
		this.bodyData.set('coupon_code', code);
		this.bodyData.delete('remove');
		return this._manageCode();
	}

	_removeCode() {
		this.bodyData.set('remove', '1');
		this.bodyData.delete('coupon_code');
		return this._manageCode();
	}

	_getUpdatedCart() {
		return false;
	}

	_getApplyRequestData() {
		return false;
	}

	_getRemoveRequestData() {
		return false;
	}

	_manageCode() {
		return sendXhr(`${this.REQUEST_URL}/couponPost`, {
			method: 'POST',
			credentials: 'include',
			headers: this.MANAGE_CODE_HEADERS,
			body: this.bodyData,
		}).then(response => response.json());
	}

	_parseTotal(json) {
		let totalValue;
		if (json) {
			totalValue = json.cart_obj.cart_grandtotal;
		} else {
			totalValue = document.querySelector(this.TOTAL_SELECTOR).textContent;
		}
		return toFloat(totalValue);
	}
};

export default {
	name: 'Mophie',
	domain: 'mophie.com',
	maxCoupons: 5, // requests are very slow
	isSinglePageApp: true,
	Driver,
	isOnCartUrl(href) {
		return href.includes('mophie.com');
	},
};
