import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { browserUtils, completionActions, log, format } from '../../../utils';

const { setCookie, getCookie } = browserUtils;
const { toFloat } = format;
const CART_URL = 'https://www.zagg.com/en_us/checkout/cart';
const isOnCart = () => window.location.href.includes(CART_URL);

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: no
+ Codes can be replaced by another code: yes
+ Manually apply code: no
+ Pages, where extension works: Cart, cart on sidebar
+ Is driver SPA: yes
+ Gather codes on site pages: no
 ====Special cases====
	- Sometimes site gives default discount, 
	and it can be bigger than tested codes.
*/

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = `${CART_URL}/`;
		this.APPLIED_CODES_SELECTOR = 'input[name="coupon_code"]';
		this.APPLY_COUPON_FORM_SELECTOR = '.discount-coupon-form';
		this.REMOVE_COUPON_FORM_SELECTOR = this.APPLY_COUPON_FORM_SELECTOR;
		this.TOTAL_ELEMENT_CONFIG = { selector: `${isOnCart() ? '.grand.totals .price' : '.grand-total .price'}` };
		this.TOTAL_SELECTOR = this.TOTAL_ELEMENT_CONFIG.selector;
		this.SIDEBAR_BTN_SELECTOR = '#mobileMiniCartToggle';
		this.SIDEBAR_CART_SELECTOR = '.block-minicart';
		this.LOADER_SELECTOR = '.loader';
		this.EMPTY_CART_SELECTOR = '#ui-id-1 .empty';
		this.MANAGE_CODE_HEADERS = {
			'Content-Type': 'application/x-www-form-urlencoded',
			referer: this.CART_URL,
		};
		this.totalRegExp = {
			regexObj: /"grand_total":"?([0-9.]+)"?|price">\$?([0-9.]+)/,
			groupNumber: 1,
		};
		this.PRETAX_REGEX = /subtotal_with_discount":"?([0-9.]+)"?/;
		this.afterCodeCheck = () => {
			// Clear this cookie to remove 'applied code' messages
			document.cookie = 'mage-messages=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
		};
	}

	checkCondition() {
		const isGenieUsed = getCookie('is-genie-used');
		const isCartEmpty = document.querySelector(this.EMPTY_CART_SELECTOR);
		const correctPage = window.location.href.startsWith(CART_URL);
		const totalValue =
			document.querySelector(this.TOTAL_SELECTOR) &&
			document.querySelector(this.TOTAL_SELECTOR).textContent.includes('$');
		const isLoader = document.querySelector(this.LOADER_SELECTOR);
		const isSidebarCart =
			document.querySelector(this.SIDEBAR_CART_SELECTOR) &&
			document.querySelector(this.SIDEBAR_CART_SELECTOR).parentNode.style.display !== 'none';
		const btnSidebarOpen = document.querySelector(this.SIDEBAR_BTN_SELECTOR);
		if (
			isGenieUsed === '1' &&
			!isSidebarCart &&
			btnSidebarOpen &&
			document.readyState === 'complete' &&
			!isLoader
		) {
			btnSidebarOpen.click();
			setCookie('is-genie-used', '0', 90);
		}
		return totalValue && (correctPage || isSidebarCart) && !isCartEmpty;
	}

	checkCodes(codes, originalPrice = this._parseTotal(), originalItemsCount = this._parseItemsCount()) {
		const workingDiscounts = [];

		log('originalPrice: %s', originalPrice);
		log('originalItemsCount: %s', originalItemsCount);

		return Promise.each(codes, code =>
			this._applyCode(code, this._getApplyRequestData(this.cartPageHTML))
				.then(() => this._getUpdatedCart())
				.then(responseString => {
					const finalPrice = this._parseTotal(responseString);
					const finalDiscount = originalPrice - finalPrice;
					const freeGifts = this._parseItemsCount(responseString) - originalItemsCount;
					const result = { code, finalPrice, finalDiscount, freeGifts };

					if (finalDiscount > 0 || (finalPrice === originalPrice && freeGifts > 0)) {
						workingDiscounts.push(result);
					}
					if (this.NEED_REMOVE_CODE) {
						return this._removeCode(code, this._getRemoveRequestData(responseString)).then(() =>
							bus.$emit('code-checked', result),
						);
					}
					return bus.$emit('code-checked', result);
				}),
		);
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(responseHTML => {
			const subtotalWithDiscountEl = responseHTML.match(this.PRETAX_REGEX);
			const subtotalWithDiscount = subtotalWithDiscountEl ? toFloat(subtotalWithDiscountEl[1]) : 0;
			return subtotalWithDiscount;
		});
	}

	completeExperience() {
		setCookie('is-genie-used', '1', 90);
		window.location.reload();
		return Promise.resolve();
	}
};

export default {
	name: 'ZAGG',
	domain: 'zagg.com',
	Driver,
	isSinglePageApp: true,
	maxCoupons: 7, // site works slow
	completionAction: completionActions.custom,
};
