import MagentoTemplate from './_base';

const CART_URL = 'https://quickcandles.com/checkout/cart/';

/* Behaviour description:
- we can apply only one code;
- we can remove code;
- invalid code removes valid */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.NEED_REMOVE_CODE = false;
	}
};

export default {
	name: 'Quick Candles',
	domain: 'quickcandles.com',
	Driver,
	isCartEmpty() {
		return Boolean(document.querySelector('.cart-empty'));
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_URL);
	},
};
