import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { log } from '../../../utils';

/** Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: no
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: Cart
+ Is driver SPA: yes
+ Gather codes on site pages: no
+ maxCoupons: 12, due to test time
 * */

const CART_URL = 'https://www.myfreedomsmokes.com/checkout/cart';

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;

		this.TOTAL_SELECTOR = '.totals.incl .price';
		this.FORM_KEY_SELECTOR = '[name="form_key"]';
		this.EMPTY_CART_SELECTOR = '.cart-empty';

		this.totalRegExp = {
			regexObj: /"grand_total":"?([0-9.]+)"?/,
			groupNumber: 1,
		};
		this.afterCodeCheck = () => {
			// Clear this cookie to remove 'applied code' messages
			document.cookie = 'mage-messages=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
		};
	}

	checkCondition() {
		const isCartEmpty = (document.querySelector(this.EMPTY_CART_SELECTOR) || {}).offsetParent;
		const isOnCartUrl = window.location.href.startsWith(CART_URL);
		const isTotalEl = (document.querySelector(this.TOTAL_SELECTOR) || {}).offsetParent;
		return !isCartEmpty && isOnCartUrl && isTotalEl;
	}

	beforeTestCodes() {
		this.FORM_KEY = document.querySelector(this.FORM_KEY_SELECTOR).value;
		this.REMOVE_REQUEST_EXTRA_VALUES = {
			remove: 1,
			form_key: this.FORM_KEY,
		};
		this.APPLY_REQUEST_EXTRA_VALUES = {
			remove: 0,
			form_key: this.FORM_KEY,
		};
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code =>
			this._applyCode(code, this._getApplyRequestData(this.cartPageHTML)).then(responseString => {
				const result = { code, finalPrice: originalPrice, finalDiscount: 0 };
				if (!responseString) {
					return bus.$emit('code-checked', result);
				}
				result.finalPrice = this._parseTotal(responseString);
				result.finalDiscount = originalPrice - result.finalPrice;
				return this._removeCode(code, this._getRemoveRequestData(responseString)).then(() =>
					bus.$emit('code-checked', result),
				);
			}),
		);
	}

	_manageCode(requestURL, bodyObj) {
		return fetch(requestURL, {
			method: 'POST',
			credentials: 'include',
			headers: this.MANAGE_CODE_HEADERS,
			body: bodyObj,
		}).then(response => (response.status === 521 ? false : response.text()));
	}
};

export default {
	name: 'My Freedom Smokes',
	domain: 'myfreedomsmokes.com',
	Driver,
	isSinglePageApp: true,
	maxCoupons: 12,
};
