import MagentoTemplate from './_base';
import bus from '../../bus';

const CART_URL = 'https://www.jerrysartarama.com/checkout/cart';

/**
 Driver behavior description:
 Existing code: yes
 Remove code: yes
 Stackable: no
 Free gift support: no
 isValid implemented: no
 Manually apply code: no
 code testing may be run on 'Cart' page;
 */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;

		this.APPLIED_CODES_SELECTOR = '#coupon_code';
		this.TOTAL_SELECTOR = '#shopping-cart-totals-table strong .price';
		this.SPINNER_SELECTOR = '.checkout-cart-index.disabled';
		this.TOTAL_SPINNER_SELECTOR = '#shopping-cart-totals-table.loading';
		this.TITLE_SELECTOR = '.page-title';
	}

	watchForCartUrl() {
		let lastState = false;
		setInterval(() => {
			const titleElem = document.querySelector(this.TITLE_SELECTOR);
			const isCartEmpty = titleElem ? titleElem.innerText.toLowerCase().includes('is empty') : false;
			const isSpinner = document.querySelector(this.SPINNER_SELECTOR);
			const isTotalSpinner = document.querySelector(this.TOTAL_SPINNER_SELECTOR);
			const currentState =
				!isCartEmpty && !isSpinner && !isTotalSpinner && window.location.href.startsWith(CART_URL);
			if (lastState !== currentState) {
				bus.$emit(currentState ? 'cart-active' : 'cart-inactive');
			}
			lastState = currentState;
		}, 1000);
	}
};

export default {
	name: "Jerry's Artarama",
	domain: 'jerrysartarama.com',
	isSinglePageApp: true,
	maxCoupons: 8,
	Driver,
	isOnCartUrl() {
		return false;
	},
};
