import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';

const CART_URL = 'https://www.alloyapparel.com/store/checkout/cart';

/** Behavior description:
 * - not stackable
 * - can remove codes
 * - valid code can be replaced with another valid code
 *
 * ======== Special case ========
 * Site is slow, so maximum 10 codes are tested.
 * Shortened checkCodes and eliminated removeCodes to speed up the check.
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;

		this.APPLIED_CODES_SELECTOR = '#coupon_code';
		this.TOTAL_SELECTOR = '#shopping-cart-totals-table strong .price';
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		return Promise.each(codes, code =>
			this._applyCode(code, this._getApplyRequestData(this.cartPageHTML)).then(responseString => {
				const finalPrice = this._parseTotal(responseString);
				const finalDiscount = originalPrice - finalPrice;
				const result = { code, finalPrice, finalDiscount };
				return bus.$emit('code-checked', result);
			}),
		);
	}
};

export default {
	name: 'Alloy Apparel',
	domain: 'alloyapparel.com',
	maxCoupons: 10,
	Driver,
	isCartEmpty() {
		return !!document.querySelector('.cart-empty');
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_URL);
	},
};
