import MagentoTemplate from './_base';
import { DOM, format } from '../../../utils';

const { toFloat } = format;
const { extractElement } = DOM;

/*
Driver behavior description:
	+ Existing code: yes
	+ Remove code: yes
	+ Stackable: no
	+ Free gift support: yes
	+ Free shipping: no
	+ isValid implemented: yes
	+ Replace code: yes
	+ Pages, where extension works:
		- Cart page
		- Checkout (payment step)
	+ Is driver SPA: yes
	+ Gather codes on site pages: no
	+ Special case about the driver works (if it needed).
		- maxCoupons: 5
		- No remove button on checkout page, but extension works as expected.
*/
const BASE_URL = 'https://www.eileenfisher.com/checkout';
const CART_URL = `${BASE_URL}/cart`;
const IS_ON_CART = window.location.href.startsWith(CART_URL);

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.APPLIED_CODES_SELECTOR = '.remove-coupon span, #coupon_code, #discount-code';
		this.TOTAL_SELECTOR = '.grand .price, .grand.totals .price';
		this.ITEM_CONTAINER_SELECTOR = IS_ON_CART ? '#shopping-cart-table tbody' : '.product-image-container';
		this.EXISTING_CODE_REGEX = /applied: (\w+)/i;
		this.SHIPPING_VALUE_REGEX = /Shipping & Handling \(.+\)","value":"(\d+\.\d+)/i;
		this.TAX_VALUE_REGEX = /"Tax","value":"([0-9.-]+)/i;
		this.totalRegExp = {
			regexObj: /"Grand Total","value":"([0-9.-]+)/,
			groupNumber: 1,
		};
		this.itemsCountRegExp = {
			regexObj: /items_count":"(\d+)/,
			groupNumber: 1,
		};
		this.REMOVE_CODE_PARAM_NAME = 'remove_coupon';
		this.afterCodeCheck = () => {
			document.cookie = `mage-messages= ;domain=www.eileenfisher.com;path=/`;
		};
	}

	checkCondition() {
		const isCartEmpty = document.querySelector(this.EMPTY_CART_SELECTOR);
		const isOnCartUrl =
			window.location.href.startsWith(CART_URL) || window.location.href.startsWith(`${BASE_URL}/#payment`);
		const isCodeInput = document.querySelector(this.APPLIED_CODES_SELECTOR);
		const isTotal = document.querySelector(this.TOTAL_SELECTOR);
		return isOnCartUrl && !isCartEmpty && isTotal && isCodeInput;
	}

	async getExistingCodes() {
		const resHtml = IS_ON_CART ? false : await this._getUpdatedCart();
		return {
			codes: [
				...new Set(
					[...extractElement(this.APPLIED_CODES_SELECTOR, resHtml, true)]
						.map(item => item.textContent.match(this.EXISTING_CODE_REGEX))
						.filter(Boolean)
						.map(code => code[1]),
				),
			],
		};
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(responseHTML => {
			const shippingElem = responseHTML.match(this.SHIPPING_VALUE_REGEX);
			const shipping = shippingElem ? toFloat(shippingElem[1]) : 0;
			const taxElem = responseHTML.match(this.TAX_VALUE_REGEX);
			const tax = taxElem ? toFloat(taxElem[1]) : 0;
			return this._parseTotal(responseHTML) - tax - shipping;
		});
	}
};

export default {
	name: 'EILEEN FISHER',
	domain: 'eileenfisher.com',
	Driver,
	isSinglePageApp: true,
	maxCoupons: 5, // site works slow
};
