import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { log, DOM, format } from '../../../utils';

const { extractElement, extractFormValues } = DOM;
const { elemToFloat } = format;

const CART_URL = 'https://www.jomashop.com/checkout/cart';
const CHECKOUT_REQUEST_URL = 'https://www.jomashop.com/firecheckout/index/saveCoupon/';
const IS_CHECKOUT = !window.location.href.includes('/cart');

/** Behavior description:
 * - can get existing codes
 * - can remove codes
 * - valid code can be replaced with another valid code
 * - can apply only one code
 *
 * ======== Special case ========
 * Sometimes, site can ask user for `robot checking`,
 * We can have `Validating JavaScript` in the response,
 * add recursive applying, after 2 try, code will be emitted as not valid
 * Special case, as we can have `Validating Javascript` in response, maxCoupons for the checkout is 2 codes.
 * Sometimes we will have `Validating Javascript` and will be nothing.
 * Code testing can be run on the cart and checkout
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.TOTAL_SELECTOR = '#shopping-cart-totals-table strong .price, #checkout-review-table  strong .price';

		this.APPLIED_CODES_SELECTOR = '#coupon_code, #coupon-code';

		this.ITEM_CONTAINER_SELECTOR = '.jqCartItem';
		this.APPLY_COUPON_FORM_SELECTOR = '#discount-coupon-form, #firecheckout-form';
		this.REMOVE_COUPON_FORM_SELECTOR = this.APPLY_COUPON_FORM_SELECTOR;

		this.CONTENT_SELECTOR = '.content';
		this.CART_EMPTY_SELECTOR = '.cart-empty';
		this.VALID_CHECKOUT_PAGE = true;

		this.APPLY_REQUEST_EXTRA_VALUES = {
			[IS_CHECKOUT ? 'coupon[remove]' : 'remove']: 0,
		};

		this.APPLY_CODE_PARAM_NAME = IS_CHECKOUT ? 'coupon[code]' : this.APPLY_CODE_PARAM_NAME;
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;
		this.REMOVE_REQUEST_EXTRA_VALUES = {
			[IS_CHECKOUT ? 'coupon[remove]' : 'remove']: 1,
		};

		this.MESSAGE_SELECTOR = '.error-msg';
		this.DISABLE_CODE_TESTING = false;
		this.CHECKOUT_COUPON_SELECTOR = '.checkout-coupon-container';
	}

	checkCondition() {
		const recaptchaPageEl = document.querySelector(this.CONTENT_SELECTOR);
		const isCartEmpty =
			document.querySelector(this.CART_EMPTY_SELECTOR) ||
			(recaptchaPageEl && !!recaptchaPageEl.textContent.match(/pardon our interruption/));
		const isAppliedCode = document.querySelector(this.CHECKOUT_COUPON_SELECTOR);
		const isCodeInput = document.querySelector(this.APPLIED_CODES_SELECTOR);
		const currentState =
			isCodeInput && isCodeInput.offsetParent && !isAppliedCode && !isCartEmpty && !this.DISABLE_CODE_TESTING;
		return currentState;
	}

	getExistingCodes() {
		const codesElement = document.querySelector(this.APPLIED_CODES_SELECTOR);

		const codes = [codesElement.getAttribute('value')].filter(el => Boolean(el));
		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes(codes) {
		return this._removeCode(codes[0], this._getRemoveRequestData(this.cartPageHTML)).then(responseString => ({
			originalPrice: this._parseTotal(responseString),
			originalItemsCount: this._parseItemsCount(responseString),
		}));
	}

	checkCodes(codes, originalPrice = this._parseTotal(), originalItemsCount = this._parseItemsCount()) {
		log('originalPrice: %s', originalPrice);
		log('originalItemsCount: %s', originalItemsCount);

		return Promise.each(codes, code =>
			this._applyCode(code, this._getApplyRequestData(this.cartPageHTML)).then(responseString => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					freeGifts: 0,
				};
				if (this._isFakeCode(responseString)) {
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}

				result.finalPrice = this._parseTotal(responseString);
				result.finalDiscount = originalPrice - result.finalPrice;
				result.freeGifts = this._parseItemsCount(responseString) - originalItemsCount;
				return bus.$emit('code-checked', result);
			}),
		).then(() => {
			if (IS_CHECKOUT) {
				this.DISABLE_CODE_TESTING = true;
			}
			return Promise.resolve();
		});
	}

	_applyCode(code, requestData, tryNumber = 1) {
		requestData.params.set(this.APPLY_CODE_PARAM_NAME, code);
		return this._manageCode(requestData.action, requestData.params).then(response => {
			if (!IS_CHECKOUT && !extractElement(this.TOTAL_SELECTOR, response) && tryNumber < 2) {
				return this._applyCode(code, requestData, tryNumber + 1);
			}
			return response;
		});
	}

	_manageCode(requestURL, bodyObj) {
		return fetch(requestURL, {
			method: 'POST',
			credentials: 'include',
			headers: this.MANAGE_CODE_HEADERS,
			body: bodyObj,
		})
			.then(response => response.text())
			.then(response => {
				try {
					return JSON.parse(response);
				} catch (e) {
					return response;
				}
			});
	}

	_getApplyRequestData(htmlString) {
		if (!this.applyRequestData) {
			// Assumed that this form always exists
			this.applyRequestData = extractFormValues(this.APPLY_COUPON_FORM_SELECTOR, htmlString);
			Object.entries(this.APPLY_REQUEST_EXTRA_VALUES).forEach(([key, value]) => {
				this.applyRequestData.params.set(key, value);
			});

			if (IS_CHECKOUT) {
				this.applyRequestData.action = CHECKOUT_REQUEST_URL;
			}
		}
		return this.applyRequestData;
	}

	_getRemoveRequestData(htmlString) {
		if (!this.removeRequestData) {
			// Form may not exists if there is no code applied
			this.removeRequestData = extractFormValues(this.REMOVE_COUPON_FORM_SELECTOR, htmlString);
			//  If we succeeded, add extra values
			if (this.removeRequestData) {
				Object.entries(this.REMOVE_REQUEST_EXTRA_VALUES).forEach(([key, value]) => {
					this.removeRequestData.params.set(key, value);
				});
			}

			if (IS_CHECKOUT) {
				this.removeRequestData.action = CHECKOUT_REQUEST_URL;
			}
		}
		return this.removeRequestData;
	}

	_isFakeCode(response) {
		if (typeof response === 'string' && response.match(/Validating JavaScript/)) {
			return true;
		}
		if (IS_CHECKOUT) {
			return extractElement(this.MESSAGE_SELECTOR, response.update_section['coupon-discount']);
		}
		if (!response || response.match(/is not valid/i) || !extractElement(this.TOTAL_SELECTOR, response)) {
			return true;
		}
		return false;
	}

	_parseItemsCount(htmlString) {
		return !IS_CHECKOUT ? extractElement(this.ITEM_CONTAINER_SELECTOR, htmlString, true).length : 0;
	}

	_parseTotal(htmlString) {
		return elemToFloat(
			extractElement(
				this.TOTAL_SELECTOR,
				typeof htmlString === 'object' ? htmlString.update_section.review : htmlString,
			),
		);
	}
};

export default {
	name: 'Jomashop',
	domain: 'jomashop.com',
	isSinglePageApp: true,
	// due to recaptcha
	maxCoupons: 1,
	cartContinueUrl: window.location.href.split('#')[0],
	Driver,
	isOnCartUrl: () => false,
};
