import MagentoTemplate from './_base';
import { sessionTimer } from '../../lib';

const BASE_URL = 'https://www.dtlr.com/checkout';
const CART_URL = `${BASE_URL}/cart`;
const CHECKOUT_URL = `${BASE_URL}/#payment`;

/** Behavior description:
 * - existing code: yes
 * - remove code: yes
 * - stackable: no
 * - free gift support: no
 * - free shipping: no
 * - 'isValid' implemented: no
 * - manually apply code: no
 * - replace code: no
 * - extension works on Cart and Checkout(Payment step)
 * - is driver SPA: yes
 *   - is on Cart or Checkout(payment) url
 *   - Cart isn't empty
 *   - total price element present
 * - gather codes on site pages: no
 * - credentials (joseph@gmail.com, 12345qwertyQ)
 * - sessionTimer: 15 min
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.IS_ON_CART = window.location.href.toLowerCase().startsWith(CART_URL);

		this.APPLIED_CODES_SELECTOR = '.input-box strong, .coupon strong, .totals.discount span.discount.coupon';
		this.TOTAL_SELECTOR = 'strong .price';
		this.EMPTY_CART_SELECTOR = '.cart-empty';

		this.totalRegExp = {
			regexObj: /grand_total":"(\d+\.\d+)/,
			groupNumber: 1,
		};
		this.afterCodeCheck = () => {
			// Clear this cookie to remove 'applied code' messages
			document.cookie = 'mage-messages=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
		};
	}

	checkCondition() {
		const emptyCartElement = document.querySelector(this.EMPTY_CART_SELECTOR);
		const isRightUrl = this.IS_ON_CART || window.location.href.toLowerCase().startsWith(CHECKOUT_URL);
		const totalPriceElement = document.querySelector(this.TOTAL_SELECTOR);
		return isRightUrl && totalPriceElement && !emptyCartElement;
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)]
			.map(codeElement => codeElement.innerText)
			.filter(Boolean);
		return {
			codes,
			codeCount: codes.length,
		};
	}
};

export default {
	name: 'DTLR-VILLA',
	domain: 'dtlr.com',
	Driver,
	isSinglePageApp: true,
};
