import MagentoTemplate from './_base';
import { browserUtils, DOM, format } from '../../../utils';

const { isFirefox } = browserUtils;
const { elemToFloat } = format;
const { extractElement } = DOM;

const BASE_URL = 'https://www.smarthome.com';
const CART_URL = `${BASE_URL}/checkout/cart`;
const CHECKOUT_URL = `${BASE_URL}/onestepcheckout`;

/* Driver behavior description:
    Existing code: yes
    Remove code: yes
    Stackable:  no
    Free gift support: no
    isValid implemented: no
    Manually apply code: no
 */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '#shopping-cart-totals-table strong .price, .onestepcheckout-totals strong .price';
		this.CART_URL = CART_URL;
		this.PRICE_BLOCK_SELECTOR = '#shopping-cart-totals-table tbody tr';
		this.SHIPPING_VALUE_SELECTOR = '.price';
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(response => {
			const shipping = [...extractElement(this.PRICE_BLOCK_SELECTOR, response, true)].find(el =>
				el.textContent.match(/shipping/i),
			);
			const calculatedShipping = shipping ? elemToFloat(shipping.querySelector(this.SHIPPING_VALUE_SELECTOR)) : 0;
			// no tax on the site
			return this._parseTotal(response) - calculatedShipping;
		});
	}
};

export default {
	name: 'Smarthome',
	domain: 'smarthome.com',
	Driver,
	maxCoupons: 10,
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) || href.startsWith(CHECKOUT_URL);
	},
	isCartEmpty() {
		return (
			document.querySelector('.cart-empty') ||
			(isFirefox() &&
				window.navigator.userAgent.includes('Macintosh') &&
				window.location.href.startsWith(CHECKOUT_URL))
		);
	},
};
