import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { format, DOM, log } from '../../../utils';

const { toFloat, elemToFloat } = format;
const { extractElement } = DOM;

const CART_URL = 'https://fractureme.com/cart';

/** Behavior description:
 * - can get existing codes
 * - can remove codes
 * - valid code can`t be replaced with another valid code
 * - can`t apply multiple codes (stackable)
 *
 * - but we can have two types of codes first we can remove, another - can't. So I take all codes from page
 * */
/*    eslint class-methods-use-this: ["error", { "exceptMethods": ["_getSummary", "_manageCode"] }] */
const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;

		this.APPLIED_CODES_SELECTOR = '#js-cart-items [class*=js-item-coupon-]';
		this.APPLIED_CODE_REGEXP = /-coupon-([A-Za-z0-9_]*)\s/;
		this.TOTAL_SELECTOR = '.cart-subtotal-price';

		this.REMOVE_CODE_REGEXP = /-([A-Za-z0-9]*)/g;

		this.bodyData = new URLSearchParams();
		this.APPLY_COUPON_FORM_SELECTOR = '#discount-coupon-form';
		this.APPLY_CODE_PARAM_NAME = 'coupon_code';

		this.CART_REGEX = /https:\/\/fractureme.com\/(|\/)cart/;
		this.IS_ON_CART = this.CART_REGEX.test(window.location.href);
		this.CURRENT_STATE = '#js-apply-coupon-input';
	}

	watchForCartUrl() {
		let lastState = false;
		setInterval(() => {
			if (this.IS_ON_CART) {
				const currentState = Boolean(document.querySelector(this.CURRENT_STATE).offsetParent);
				if (lastState !== currentState) {
					bus.$emit(!lastState ? 'cart-active' : 'cart-inactive');
				}
				lastState = currentState;
			}
		}, 1000);
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)]
			.map(element => element.className.match(this.APPLIED_CODE_REGEXP))
			.filter(Boolean)
			.map(el => el[1]);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes() {
		const removeId = this._getRemoveCode();
		return Promise.each(removeId, id => this._removeCode(id));
	}

	checkCodes(codes) {
		let originalPrice = null;
		return this._getSummary()
			.then(responseText => {
				originalPrice = this._parseTotal(responseText, true);
				log(`Original price: ${originalPrice}`);
			})
			.then(() =>
				Promise.each(codes, code =>
					this._applyCode(code).then(responseJSON => {
						const result = {
							code,
							finalPrice: originalPrice,
							finalDiscount: 0,
						};

						if (!responseJSON.error) {
							result.finalPrice = this._parseTotal(responseJSON);
							result.finalDiscount = originalPrice - result.finalPrice;

							return this._getSummary()
								.then(responseHTML => this._removeCode(this._getRemoveCode(responseHTML)))
								.then(() => bus.$emit('code-checked', result));
						}
						return bus.$emit('code-checked', result);
					}),
				),
			);
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	_getSummary() {
		return fetch(CART_URL, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	_getRemoveCode(data = document.documentElement.innerHTML) {
		const codes = [...extractElement(this.APPLIED_CODES_SELECTOR, data, true)].map(el =>
			el.getAttribute('data-id'),
		);
		return codes;
	}

	_manageCode(url, bodyData, header) {
		return fetch(url, {
			method: 'POST',
			headers: header,
			credentials: 'include',
			body: bodyData,
		}).then(response => response.json());
	}

	_removeCode(data) {
		const headers = {
			Accept: 'application/json, text/javascript, */*; q=0.01',
			'X-Requested-With': 'XMLHttpRequest',
		};
		return this._manageCode(`${CART_URL}/update_item/remove/${data}`, null, headers);
	}

	_applyCode(code) {
		this.bodyData.set('coupon_name', code);
		return this._manageCode(`${CART_URL}/add_coupon`, this.bodyData);
	}

	_parseTotal(data, text = false) {
		return text ? elemToFloat(extractElement(this.TOTAL_SELECTOR, data)) : toFloat(data.cart_total);
	}
};

export default {
	name: 'Fracture',
	domain: 'fractureme.com',
	Driver,
	isSinglePageApp: true,
	isOnCartUrl() {
		return false;
	},
	// no need to write isCartEmpty() because we have isSinglePageApp
};
