import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { log, format, completionActions } from '../../../utils';

const { toFloat } = format;

const BASE_URL = 'https://www.magazines.com/checkout';
const BILLING_URL = `${BASE_URL}/checkout/billing`;

/** Behavior description:
+ Existing code: yes, but not always, get codes from response
+ Remove code: no
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: no
+ Codes can be replaced by another code: valid code can be replaced with another valid code,
  but message about appling on the page
+ Manually apply code: no
+ Pages, where extension works: Payment step of Checkout, sometimes redirect us to Cart,
  but after Cart user can go to Payment step directly
+ Is driver SPA: no
+ Gather codes on site pages: no
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = `${BASE_URL}/cart`;

		this.TOTAL_SELECTOR = '.order-total .value';
	}

	beforeTestCodes() {
		this.requestParams = new URLSearchParams({
			retfmt: 'json',
			apply: 'true',
		});
	}

	getExistingCodes() {
		return this._getSummary().then(responseJSON => {
			const codes = responseJSON.coupons ? [responseJSON.coupons.applied.code] : [];

			return {
				codes,
				codeCount: codes.length,
			};
		});
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code =>
			this._applyCode(code).then(response => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
				};
				if (response && !response.redemptionErrorMsg) {
					return this._getSummary().then(responseJSON => {
						result.finalPrice = this._parseTotal(responseJSON);
						result.finalDiscount = originalPrice - result.finalPrice;
						return bus.$emit('code-checked', result);
					});
				}
				return bus.$emit('code-checked', result);
			}),
		);
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	_applyCode(code) {
		const requestUrl = `${this.CART_URL}/couponPost/?`;
		this.requestParams.set('giftCertificateID', code);
		this.requestParams.set('couponCode', code);

		return this._manageCode(`${requestUrl}${this.requestParams.toString()}`);
	}

	_manageCode(requestURL) {
		return fetch(requestURL, {
			method: 'GET',
			credentials: 'include',
			headers: this.MANAGE_CODE_HEADERS,
		}).then(response => response.json());
	}

	_getSummary() {
		return this._manageCode(`${BASE_URL}/cart/getPrices/`);
	}

	_parseTotal(data) {
		if (data) {
			return data.grandTotal;
		}
		return toFloat(document.querySelector(this.TOTAL_SELECTOR).textContent);
	}
};

export default {
	name: 'magazines',
	domain: 'magazines.com',
	Driver,
	cartContinueUrl: BILLING_URL,
	completionAction: completionActions.revisit,
	isCartEmpty() {
		return Boolean(document.querySelector('.no-cart-items'));
	},
	isOnCartUrl(href) {
		return href.startsWith(BILLING_URL);
	},
};
