import MagentoTemplate from './_base';
import { sessionTimer } from '../../lib';

const CART_URL = 'https://www.peets.com/checkout/cart';

/** Driver behavior description:
 -    Existing code: yes
 -    Remove code: yes
 -    Stackable: no
 -    Free gift support: no
 -    isValid implemented: no
 Optional:
 -    Show message about free shipping on the cart page (without discount info): no
 -    Is driver SPA and add the condition to show popup: yes (run code testing on the cart page, "Checkout / Shipping Method", "Checkout / Review & Place Order")
 - 	  sessionTimer 15 min
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;

		this.APPLIED_CODES_SELECTOR = '[name=coupon_code]';
		this.TOTAL_SELECTOR = '#shopping-cart-totals-table strong .price';

		this.REQUEST_URL = 'https://www.peets.com/ajaxcontroller/cart/updateCoupon/';
		this.SHIPPING_SELECTOR = '#opc-shipping';
		this.REVIEW_SELECTOR = '#opc-review';
		this.EMPTY_CART_SELECTOR = '.cart-empty';
	}

	checkCondition() {
		const { href } = window.location;
		const shippingStepIsActive =
			document.querySelector(this.SHIPPING_SELECTOR) &&
			document.querySelector(this.SHIPPING_SELECTOR).classList.contains('active');
		const reviewStepIsActive =
			document.querySelector(this.REVIEW_SELECTOR) &&
			document.querySelector(this.REVIEW_SELECTOR).classList.contains('active');
		const emptyCart =
			document.querySelector(this.EMPTY_CART_SELECTOR) &&
			document.querySelector(this.EMPTY_CART_SELECTOR).offsetParent;

		const checkoutURL =
			href.startsWith('https://www.peets.com/checkout/onepage/') && (shippingStepIsActive || reviewStepIsActive);
		return (checkoutURL || href.startsWith(CART_URL)) && !emptyCart;
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();
		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	_manageCode(requestURL, bodyObj) {
		return fetch(this.REQUEST_URL, {
			method: 'POST',
			credentials: 'include',
			body: bodyObj,
		})
			.then(response => response.json())
			.then(response => response.update_sections['shopping-cart-totals']);
	}
};

export default {
	name: 'Peets Coffee',
	domain: 'peets.com',
	Driver,
	isSinglePageApp: true,
	maxCoupons: 10,
};
