import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { format, log } from '../../../utils';

const { toFloat, elemToFloat } = format;

const BASE_URL = 'https://www.shopnational.com';
const CHECKOUT_URL = `${BASE_URL}/checkout`;
const CART_URL = `${CHECKOUT_URL}/cart`;
const IS_CHECKOUT_PAGE = window.location.href.startsWith(CHECKOUT_URL) && !window.location.href.includes('cart');
const BASE_REQUEST_URL = `${BASE_URL}/rest/default/V1`;

/** Behavior description:
 * - can get existing codes
 * - can remove codes
 * - can't apply without removing
 * - can apply only one code (not stackable)
 * - maxCoupons = 7, 1 valid code ~ 8s
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.APPLY_BUTTON_SELECTOR = '#discount-form .action-apply';
		this.CANCEL_BUTTON_SELECTOR = '#discount-form .action-cancel';
		this.TOTAL_SELECTOR = '.grand .price';
		this.CART_URL = `${CART_URL}/`;
		this.CODE_INPUT_SELECTOR = '[name="coupon_code"], [name="discount_code"]';
		this.ENTITY_ID_SELECTOR = new RegExp(/"entity_id":"(.*?)"/);

		this.MANAGE_CODE_HEADERS = {
			'Content-Type': 'application/json',
			'x-requested-with': 'XMLHttpRequest',
		};

		this.requestData = null;
		this.updatedUrl = null;
	}

	checkCondition() {
		const totalElement = document.querySelector(this.TOTAL_SELECTOR);
		const codeInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		return totalElement && codeInput;
	}

	beforeTestCodes() {
		const entityId = [...document.querySelectorAll('script')]
			.find(element => element.text.match(this.ENTITY_ID_SELECTOR))
			.text.match(this.ENTITY_ID_SELECTOR)[1];
		if (!JSON.parse(localStorage.getItem('mage-cache-storage')).customer.fullname) {
			this.requestData = {
				action: `${BASE_REQUEST_URL}/guest-carts/${entityId}/coupons/`,
			};
			this.updatedUrl = `${BASE_REQUEST_URL}/guest-carts/${entityId}/payment-information`;
		} else {
			this.requestData = {
				action: `${BASE_REQUEST_URL}/carts/mine/coupons/`,
			};
			this.updatedUrl = `${BASE_REQUEST_URL}/carts/mine/payment-information`;
		}
		return Promise.resolve();
	}

	getExistingCodes() {
		return this._getUpdatedCart().then(responseJSON => {
			const codes = [responseJSON.totals.coupon_code].filter(Boolean);
			return {
				codes,
				codeCount: codes.length,
			};
		});
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
			};
			return this._applyCode(code).then(responseJSON => {
				result.isValid = !responseJSON.message;
				if (!result.isValid) {
					return bus.$emit('code-checked', result);
				}
				return this._getUpdatedCart().then(responseCartJSON => {
					result.finalPrice = this._parseTotal(responseCartJSON);
					result.finalDiscount = originalPrice - result.finalPrice;
					if (IS_CHECKOUT_PAGE) {
						result.freeShipping = this._checkFreeShipping(responseCartJSON);
					}
					return this._removeCode().then(() => bus.$emit('code-checked', result));
				});
			});
		});
	}

	beforeApplyCodes() {
		// Clear this cookie to remove 'applied code' messages
		document.cookie = 'mage-messages=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
		return Promise.resolve();
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	_applyCode(code) {
		return this._manageCode(`${this.requestData.action}${code}`, 'PUT').then(responseJSON => responseJSON.json());
	}

	_removeCode() {
		return this._manageCode(this.requestData.action, 'DELETE');
	}

	_manageCode(requestURL, method) {
		return fetch(requestURL, {
			method,
			credentials: 'include',
			headers: this.MANAGE_CODE_HEADERS,
		});
	}

	_checkFreeShipping(data) {
		return toFloat(data.totals.total_segments.find(item => item.code === 'shipping').value) === 0;
	}

	_parseTotal(data) {
		if (data) {
			return toFloat(data.totals.base_grand_total);
		}
		return elemToFloat(document.querySelector(this.TOTAL_SELECTOR));
	}

	_getUpdatedCart() {
		return fetch(this.updatedUrl, {
			method: 'GET',
			credentials: 'include',
			headers: this.MANAGE_CODE_HEADERS,
		}).then(responseJSON => responseJSON.json());
	}
};

export default {
	name: 'National',
	domain: 'shopnational.com',
	isSinglePageApp: true,
	maxCoupons: 7,
	Driver,
	isOnCartUrl() {
		return false;
	},
};
