import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { format, log, browserUtils, completionActions } from '../../../utils';
import { wait } from '../../../utils/browser';
import { setValue, runEmbeddedScript } from '../../../utils/DOM';

const { elemToFloat, toFloat } = format;
const { getCookie } = browserUtils;

const isOnShiping = () => window.location.href.includes('shipping-method');
const isOnPayment = () => window.location.href.includes('payment-information');
const loggedInUserOnShipping = () => {
	return (
		sessionStorage.state &&
		!JSON.parse(sessionStorage.getItem('state')).information.guest &&
		(isOnShiping() || isOnPayment())
	);
};

/*
Driver behavior description:
- Existing code: yes
- Remove code: yes
- Stackable:  no
- Free gift support:  no
- Free shipping:  no
- IsSinglePageApp: yes
- isValid implemented: yes
- Codes can be replaced by another code:  no
- Manually apply code: yes (on shipping for logged in user)
- Manually remove code: yes (n shipping for logged in user)
- GetMerchantCodes: yes, from header on Cart and Checkout
Special cases:
	- removing and applying code may change shipping method but we restore it after code testing
	- Pages, where extension works: cart (only if there is available promo code input), checkout shipping step
	- Don't support Payment, because after our extension works shipping method is default
	- Special case about the driver works: sometimes clear shipping method on shipping step
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.DOMAIN = 'prettylittlething.us';
		this.checkoutUrl = `https://checkout.${this.DOMAIN}/checkout-api`;
		this.APPLIED_CODES_SELECTOR = '.code, .c-coupon__selected';
		this.TOTAL_SELECTOR = '.grandTotalContainer .amount, .bag__totals-item:last-child :last-child';
		this.BAG_ITEM_SELECTOR = 'li.bag__totals-item span';
		this.DISCOUNT_REGEX = /\((\w+)\)/;
		this.CODE_INPUT_SELECTOR = '#coupon';
		this.CONTINUE_BUTTON_SELECTOR = '.box__content-container [type="submit"]';
		this.REMOVE_CODE_SELECTOR = '.block-current-coupon__coupon button';
		this.APPLY_BUTTON_SELECTOR = '.c-coupon__combined-input button';
		this.IS_ON_CART = window.location.href.includes('checkout/cart');
		this.MERCHANT_CODES_SELECTOR = '.banner_schedule_json';
		this.MERCHANT_CODES_REGEX = /enter: (\w+)/i;
		this.WARNING_CODE_SELECTOR = '.coupon-content_message';
		this.WARNING_CODE_REGEX = /except (\w+)/;
		this.FORM_KEY_SELECTOR = '#form_key';
		this.TITLE_SELECTOR = '.page-title';
		this.bodyData = new URLSearchParams();
		this.LOADER_SELECTOR = '.app-loading-spinner';
		this.SUBMIT_BTN_SELECTOR = '.c-button';
		this.SHIPPING_METHOD_LIST = '.c-shipping-method__list input[name="shipping-method"]';
		this.SHIPPING_METHOD_SELECTOR = '[name="shipping-method"]';
		this.LOADING_BUTTON_SELECTOR = '.btn--loading';
		this.SHIPPING_ON_PAYMENT_SELECTOR = '#shippingMethodHeaderText';

		this.REQUEST_DELAY = 3000;
	}

	checkCondition() {
		const titleEl = document.querySelector(this.TITLE_SELECTOR);
		const isCartEmpty = titleEl && titleEl.textContent.toLowerCase().includes('cart is empty');
		const isOnRightURL = this.IS_ON_CART || isOnShiping() || loggedInUserOnShipping();
		const loader = document.querySelector(this.LOADER_SELECTOR);
		const isCodeInputAvailable = this.IS_ON_CART ? document.querySelector(this.TOTAL_SELECTOR) : true;
		const loadingButton = document.querySelector(this.LOADING_BUTTON_SELECTOR);
		const state = JSON.parse(sessionStorage.getItem('state'));
		const ajaxCallsInProgress = state && state.ajaxCallsInProgress > 0;
		return (
			!isCartEmpty && isOnRightURL && !loader && isCodeInputAvailable && !loadingButton && !ajaxCallsInProgress
		);
	}

	checkRestoreDataCondition() {
		const submitBtn = document.querySelector(this.SUBMIT_BTN_SELECTOR);
		const shippingMethodList = document.querySelector(this.SHIPPING_METHOD_LIST);
		return shippingMethodList && !submitBtn.disabled && !loggedInUserOnShipping();
	}

	getUserData() {
		if (document.querySelector(this.SHIPPING_METHOD_LIST)) {
			const shippingMethodElement = [...document.querySelectorAll(this.SHIPPING_METHOD_LIST)].find(
				el => el.checked,
			);

			return { shippingMethodValue: shippingMethodElement.value };
		}

		return null;
	}

	setUserData(userData) {
		document.querySelector(`${this.SHIPPING_METHOD_LIST}[value="${userData.shippingMethodValue}"]`).click();
	}

	beforeTestCodes() {
		if (this.IS_ON_CART) {
			this.FORM_KEY = document.querySelector(this.FORM_KEY_SELECTOR).value;
			this.MANAGE_CODE_HEADERS = {
				'X-Requested-With': 'XMLHttpRequest',
				'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
			};
			return Promise.resolve();
		}
		this.MANAGE_CODE_HEADERS = {
			accept: 'application/json',
			'X-CSRF-TOKEN': getCookie('_csrf'),
			'Content-Type': 'application/x-www-form-urlencoded',
			'x-dtpc': getCookie('dtPC'),
		};

		if (isOnPayment()) {
			this.POSTAL_CODE = document.querySelector('.postcode').textContent;
			if (document.querySelector(this.SHIPPING_ON_PAYMENT_SELECTOR).textContent.match(/usa standard/i)) {
				this.shippingMethodValue = 'flatrate12_flatrate12';
			}
			this.shippingMethodValue = 'flatrate11_flatrate11';
		} else {
			this.shippingMethodValue = [...document.querySelectorAll(this.SHIPPING_METHOD_SELECTOR)]
				.find(el => el.checked)
				.getAttribute('value');
		}
		this.STATE = JSON.parse(sessionStorage.getItem('state'));

		let shippingAddress;
		const selectedAddress =
			this.STATE.form.shippingInformation && this.STATE.form.shippingInformation.values['select-address'];
		if (selectedAddress === 'new-address') {
			shippingAddress = this.STATE.form.shippingInformation.values;
		} else if (loggedInUserOnShipping()) {
			shippingAddress = selectedAddress
				? this.STATE.information.customer.addresses.find(address => address.uid === selectedAddress)
				: this.STATE.information.customer.addresses[0];
			shippingAddress.address1 =
				shippingAddress.addressLine1 && shippingAddress.addressLine1.length
					? shippingAddress.addressLine1
					: null;
			shippingAddress.address2 =
				shippingAddress.addressLine2 && shippingAddress.addressLine2.length
					? shippingAddress.addressLine2
					: null;
		} else {
			shippingAddress = this.STATE.form.billingInformation.values;
		}
		shippingAddress.billingCountryPhoneId = this.STATE.telephone.selectedBillingCountryPhoneId;

		const shippingPrice = toFloat(this.STATE.bag.shipping.price)
			.toFixed(2)
			.toString();
		this.updateBodyData = new URLSearchParams({
			option: this.shippingMethodValue,
			shippingMethodDetails: JSON.stringify({
				price: shippingPrice,
				name: this.STATE.bag.shipping.name,
				originalPrice: shippingPrice,
			}),
			syncQuote: false,
			storeId: this.STATE.information.store_id,
			sessionId: this.STATE.init.sessionId,

			shippingAddress: JSON.stringify(shippingAddress),
		});
		return Promise.resolve();
	}

	getMerchantCodes() {
		const codesFromPromo = [document.querySelector(this.MERCHANT_CODES_SELECTOR)]
			.filter(Boolean)
			.map(el => el.textContent.match(this.MERCHANT_CODES_REGEX))
			.filter(Boolean)
			.map(el => el[1]);
		const codesFromWarning = [...document.querySelectorAll(this.WARNING_CODE_SELECTOR)]
			.map(el => el.textContent.match(this.WARNING_CODE_REGEX))
			.filter(Boolean)
			.map(el => el[1]);

		return [...new Set([...codesFromPromo, ...codesFromWarning])];
	}

	getExistingCodes() {
		// at first try to take code from bag summary, because in coupon section may display invalid code
		const codesFromBagSummary = [...document.querySelectorAll(this.BAG_ITEM_SELECTOR)]
			.map(el => el.textContent.match(this.DISCOUNT_REGEX))
			.filter(Boolean)
			.map(el => el[1]);
		const codes = codesFromBagSummary.length
			? codesFromBagSummary
			: [document.querySelector(this.APPLIED_CODES_SELECTOR)]
					.filter(Boolean)
					.map(el => el.textContent.trim())
					.filter(Boolean);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes(codes) {
		return this._removeCode(codes[0], true)
			.then(responseJson => (this.IS_ON_CART ? responseJson : this._updateCartData()))
			.then(responseJson => {
				if (loggedInUserOnShipping()) {
					return this._removeCodeManually().then(() => this._parseTotal());
				}

				// take total from response, as removing code may change shiping
				return this._parseTotal(responseJson);
			});
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code => {
			if (code === 'SHIPNOW') {
				return bus.$emit('code-checked', {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					isValid: false,
				});
			}
			return this._applyCode(code).then(responseJSON => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					isValid: true,
				};
				if (responseJSON.error || responseJSON.success === false) {
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}

				return (this.IS_ON_CART ? responseJSON : this._updateCartData()).then(updatedResponse => {
					result.finalPrice = this._parseTotal(updatedResponse);

					result.finalDiscount = originalPrice - result.finalPrice;

					return this._removeCode(code, true).then(() => bus.$emit('code-checked', result));
				});
			});
		});
	}

	applyCodes(codes) {
		if (loggedInUserOnShipping()) {
			bus.$emit('change-completion-action', completionActions.none);
			return this._applyCodeManually(codes[0]);
		}
		return this._applyCode(codes[0]);
	}

	_applyCodeManually(code) {
		const inputEl = document.querySelector(this.CODE_INPUT_SELECTOR);
		setValue(inputEl, code, 'input');
		document.querySelector(this.APPLY_BUTTON_SELECTOR).click();
	}

	_removeCodeManually() {
		const removeButton = document.querySelector(this.REMOVE_CODE_SELECTOR);
		if (removeButton) {
			removeButton.click();
		}
		const continueButton = document.querySelector(this.CONTINUE_BUTTON_SELECTOR);
		return wait(
			() => continueButton && !continueButton.disabled && document.querySelector(this.CODE_INPUT_SELECTOR),
		);
	}

	_applyCode(code) {
		return this._manageCode(code);
	}

	_removeCode(code) {
		return this._manageCode(code, true);
	}

	_manageCode(code, isRemove) {
		let url = '';
		if (this.IS_ON_CART) {
			this._setCartData(code, isRemove);
			url = `https://www.${this.DOMAIN}/pltmobile/coupon/couponPost/`;
		} else if (isOnPayment()) {
			this._setPaymentData(code, isRemove);
			url = `${this.checkoutUrl}/coupon${!isRemove ? '/set' : ''}`;
		} else {
			this._setCheckoutData(code, isRemove);
			url = `${this.checkoutUrl}/coupon${!isRemove ? '/set' : ''}`;
		}

		return this._sendRequest(url, this.bodyData);
	}

	async _updateCartData() {
		return this._sendRequest(`${this.checkoutUrl}/shipping_method`, this.updateBodyData);
	}

	async _sendRequest(url, body) {
		localStorage.removeItem('genieResponse'); // for stability
		await Promise.delay(this.REQUEST_DELAY);
		await runEmbeddedScript(
			`fetch('${url}', {
				method: 'POST',
				credentials: 'include',
				headers: ${JSON.stringify(this.MANAGE_CODE_HEADERS)},
				body: '${body}'
			})
			.then(response => response.json())
			.then(json => localStorage.setItem('genieResponse', JSON.stringify(json)));`,
		);
		await wait(() => localStorage.getItem('genieResponse'), 100000, 50);
		return JSON.parse(localStorage.getItem('genieResponse'));
	}

	_setCartData(code, isRemove) {
		this.bodyData.set('code', code);
		this.bodyData.set('form_key', this.FORM_KEY);
		this.bodyData.delete('remove');
		if (isRemove) {
			this.bodyData.set('remove', 1);
		}
	}

	_setCheckoutData(code, isRemove) {
		if (!isRemove) {
			this.bodyData.set('coupon_code', code);
		}
		this.bodyData.set('form_key', '');
		const shippingData = loggedInUserOnShipping()
			? this.STATE.information.customer.addresses[0]
			: this.STATE.form.billingInformation.values;
		this.bodyData.set('countryId', shippingData.countryId);
		this.bodyData.set('postcode', shippingData.postcode);
		this.bodyData.set(
			'loadShippingMethods',
			`function(e) {return { type: r.GET_COUPON_SHIPPING_METHODS_SUCCESS, payload: e };}`,
		);
	}

	_setPaymentData(code, isRemove) {
		if (!isRemove) {
			this.bodyData.set('coupon_code', code);
			this.bodyData.set(
				'loadShippingMethods',
				'function(e){return{type:r.GET_COUPON_SHIPPING_METHODS_SUCCESS,payload:e}}',
			);
		}
		this.bodyData.set('postcode', this.POSTAL_CODE);
		this.bodyData.set('countryId', 'US');
		this.bodyData.set('form_key', '');
	}

	_parseTotal(data) {
		if (data) {
			return toFloat(data && data.quote ? data.quote.quote.grand_total : data.grand_total);
		}
		return elemToFloat(document.querySelector(this.TOTAL_SELECTOR));
	}
};

export default {
	name: 'PrettyLittleThing US',
	domain: 'prettylittlething.us',
	showDifferentDiscountMessage: true,
	isSinglePageApp: true,
	restoreUserData: true,
	Driver,
};
