import MagentoTemplate from './_base';

const BASE_URL = 'https://www.americanstationery.com/checkout';
const CART_URL = `${BASE_URL}/cart`;
const CHECKOUT_URL = `${BASE_URL}/onepage`;

/* Driver behavior description:
    Existing code: yes
    Remove code: yes
    Stackable: no
    Free gift support: no
    isValid implemented: no
    Manually apply code: no
Optional:
    Show message about free shipping on the cart page (without discount info): no
    Is driver SPA and add the condition to show popup: yes
    Gather codes on site pages: no */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;

		this.ITEM_CONTAINER_SELECTOR = null;
		this.EMPTY_CART_SELECTOR = '.cart-empty';
	}

	checkCondition() {
		const { href } = window.location;
		const isRightUrl = href.startsWith(CART_URL) || href.startsWith(CHECKOUT_URL);
		const emptyCartElement = document.querySelector(this.EMPTY_CART_SELECTOR);
		const inputField = document.querySelector(this.APPLIED_CODES_SELECTOR);
		const currentState = isRightUrl && (inputField && inputField.offsetParent) && !emptyCartElement;
		return currentState;
	}

	beforeCheckCodes(originalPrice) {
		if (!originalPrice) {
			// on the checkout page we can't get original price so we need to get it from the cart page
			return this._parseTotal(this.cartPageHTML);
		}
		return originalPrice;
	}

	applyCodes(codes) {
		return this._applyCode(codes[0], this._getApplyRequestData(this.cartPageHTML));
	}
};

export default {
	name: 'American Stationery Company',
	domain: 'americanstationery.com',
	Driver,
	isSinglePageApp: true,
	isOnCartUrl() {
		return false;
	},
};
