import MagentoTemplate from './_base';
import { DOM } from '../../../utils';

const { extractElement } = DOM;

const CART_URL = 'https://www.showmeyourmumu.com/checkout/cart';

/* Driver behavior description:
    Existing code: yes
    Remove code: yes
    Stackable: no
    Free gift support: no
    isValid implemented: no
    Manually apply code: no
    Active page: cart
Optional:
    Show message about free shipping on the cart page (without discount info): no
    Is driver SPA and add the condition to show popup: no
    Gather codes on site pages: no */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
	}

	async _manageCode(requestURL, bodyObj) {
		const responseHTML = await super._manageCode(requestURL, bodyObj);
		// retry for total element
		if (!extractElement(this.TOTAL_SELECTOR, responseHTML)) {
			return super._manageCode(requestURL, bodyObj);
		}
		return responseHTML;
	}
};

export default {
	name: 'ShowMeYourMuMu',
	domain: 'showmeyourmumu.com',
	isCartEmpty() {
		return document.querySelector('.empty-tote');
	},
	Driver,
	maxCoupons: 12, // may be slow
	isOnCartUrl(href) {
		return href.startsWith(CART_URL);
	},
};
