import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { format } from '../../../utils';

const { elemToFloat, toFloat } = format;
const BASE_URL = 'https://www.inkcartridges.com';
const CART_URL = `${BASE_URL}/checkout/cart`;

/* Behavior:
 * We can apply only one code
 * Can remove, can't replace with invalid code
 * This merchant has not template behaviour
 * in getUpdatedCart I am getting JSON type,
 * not HTML as template
 * I can't send request for removing code,
 * If code didn't give discount
 */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.APPLIED_CODES_SELECTOR = '[ng-bind="coupon.code"], .discount-coupon-code';
		this.TOTAL_SELECTOR = '.grand-total .price';
		this.CART_URL = CART_URL;
		this.REMOVE_BTN_SELECTOR = '.remove-coupon';
		this.CART_EMPTY_SELECTOR = '.empty-cart-row';
	}

	watchForCartUrl() {
		let lastState = false;
		setInterval(() => {
			const cartEmpty = document.querySelector(this.CART_EMPTY_SELECTOR);
			const currentState = cartEmpty && !cartEmpty.offsetParent && window.location.href.startsWith(CART_URL);
			if (lastState !== currentState) {
				bus.$emit(currentState ? 'cart-active' : 'cart-inactive');
			}
			lastState = currentState;
		}, 1000);
	}

	getExistingCodes() {
		const codes = [document.querySelector(this.APPLIED_CODES_SELECTOR)]
			.filter(Boolean)
			.map(code => code.textContent.trim());

		return {
			codes,
			codeCount: codes.length,
		};
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		return Promise.each(codes, code =>
			this._applyCode(code).then(applyCodeResponse => {
				let finalPrice = originalPrice;
				let finalDiscount = 0;
				if (Object.prototype.hasOwnProperty.call(applyCodeResponse, 'success')) {
					return this._getUpdatedCart()
						.then(updatedCart => {
							finalPrice = this._parseTotal(updatedCart);
							finalDiscount = originalPrice - finalPrice;
						})
						.then(() =>
							bus.$emit('code-checked', {
								code,
								finalPrice,
								finalDiscount,
								isValid: true,
							}),
						);
				}
				return bus.$emit('code-checked', {
					code,
					finalPrice,
					finalDiscount,
					isValid: false,
				});
			}),
		);
	}

	applyCodes(codes) {
		return super.applyCodes(codes);
	}

	_applyCode(code) {
		const body = { coupon_code: code, async: true };
		return this._manageCode(`${CART_URL}/couponPost`, body);
	}

	_removeCode(code) {
		const body = { amcoupon_code_cancel: code, async: true };
		return this._manageCode(`${BASE_URL}/amcoupons/checkout/cancelCoupon`, body);
	}

	_manageCode(url, body) {
		return fetch(url, {
			method: 'POST',
			credentials: 'include',
			body: JSON.stringify(body),
		}).then(responseJSON => responseJSON.json());
	}

	_getUpdatedCart() {
		return fetch(`${CART_URL}/retrieveCartTotals`, {
			method: 'GET',
			credentials: 'include',
		}).then(responseJSON => responseJSON.json());
	}

	_parseTotal(data) {
		return data ? toFloat(data.grandtotal) : elemToFloat(document.querySelector(this.TOTAL_SELECTOR));
	}
};

export default {
	name: 'InkCartridges.com',
	domain: 'inkcartridges.com',
	// because when we removed removed all products, page doesn't reload
	isSinglePageApp: true,
	Driver,
	isOnCartUrl(href) {
		return href.includes(CART_URL);
	},
};
