import MagentoTemplate from './_base';

const CART_URL = 'https://www.karenkane.com/store/checkout/cart';

/** Driver behavior description:
-    Existing code: yes
-    Remove code: yes
-    Stackable: no
-    Free gift support:no
-    isValid implemented: no
Optional:
-    Show message about free shipping on the cart page (without discount info): no
-    Is driver SPA and add the condition to show popup: no
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
	}
};

export default {
	name: 'Karen Kane',
	domain: 'karenkane.com',
	Driver,
	maxCoupons: 10, // due to ` Total test time`
	isOnCartUrl(href) {
		return href.startsWith(CART_URL);
	},
	isCartEmpty() {
		return !(document.querySelector('#coupon_code') && document.querySelector('#coupon_code').offsetParent);
	},
};
