import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { log } from '../../../utils';

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: no
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: Cart, Payment
+ Is driver SPA: no
+ Gather codes on site pages: no
*/

const BASE_URL = 'https://www.oneida.com';
const CART_URL = `${BASE_URL}/checkout/cart`;
const CHECKOUT_URL = `${BASE_URL}/checkout/onepage`;
const IS_ON_CART = window.location.href.startsWith(CART_URL);

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.TOTAL_SELECTOR = '#shopping-cart-totals-table strong .price';
		this.CODE_CONTAINER_SELECTOR = '#checkout-step-payment';
	}

	checkCondition() {
		const totalValue = document.querySelector(this.TOTAL_SELECTOR);
		const onCheckout = window.location.href.startsWith(CHECKOUT_URL);
		const applyCodeElement = (document.querySelector(this.CODE_CONTAINER_SELECTOR) || {}).offsetParent;
		return (IS_ON_CART && totalValue) || (onCheckout && applyCodeElement);
	}

	checkCodes(codes, originalPrice = this._parseTotal(this.cartPageHTML)) {
		log(`Original price: ${originalPrice}`);
		// send get request on checkout page to parse finalPrice
		// remove _removeCode method in order code can replace another code
		return Promise.each(codes, code =>
			this._applyCode(code, this._getApplyRequestData(this.cartPageHTML))
				.then(responseString => (!IS_ON_CART ? this._getUpdatedCart() : responseString))
				.then(responseString => {
					const finalPrice = this._parseTotal(responseString);
					const finalDiscount = originalPrice - finalPrice;
					return bus.$emit('code-checked', { code, finalPrice, finalDiscount });
				}),
		);
	}

	_manageCode(requestURL, bodyObj) {
		// don't allow automatic redirection on checkout page as we have to send another request
		return fetch(requestURL, {
			method: 'POST',
			credentials: 'include',
			headers: this.MANAGE_CODE_HEADERS,
			redirect: IS_ON_CART ? 'follow' : 'manual',
			body: bodyObj,
		}).then(response => response.text());
	}
};

export default {
	name: 'Oneida',
	domain: 'oneida.com',
	Driver,
	isSinglePageApp: true,
};
