import MagentoTemplate from './_base';

const CART_URL = 'https://www.g2play.net/checkout/cart';

/** Behavior description:
 * We can apply only one code
 * We can remove existing code
 * We can get existing code
 * We can apply codes only on cart page
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.TOTAL_SELECTOR = '#shopping-cart-totals-table .grand_total .price';
		this.NEED_REMOVE_CODE = true;
	}
};

export default {
	name: 'G2 Play',
	domain: 'g2play.net',
	Driver,
	maxCoupons: 8,
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) && document.querySelector('#coupon_code');
	},
};
