import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';

import { log } from '../../../utils';
import { elemToFloat, toFloat } from '../../../utils/format';

const BASE_URL = 'https://www.vincecamuto.com/checkout';
const CART_URL = `${BASE_URL}/cart`;
const CHECKOUT = `${BASE_URL}/#payment`;

/* Driver behavior description:
 Existing code: yes
 Remove code: yes
 Stackable: no
 Free gift support:  no
 isValid implemented: yes
on the cart we have calculated shipping but it doesn't show to the user. So I get it from the response
can't avoid redirect from the payment to shipping page
*/

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;

		this.APPLIED_CODES_SELECTOR = '.code';
		this.TOTAL_SELECTOR = 'strong .price';
		this.totalRegExp = /"base_grand_total":"?([0-9.]+)"?/;
		this.TAX_REGEX = /"Tax","value":"?([0-9.]+)"?/;
		this.SHIPPING_REGEX = /price_excl_tax":?([0-9.]+)/;
		this.SPINER = '.loading-mask';
		this.INPUT_SELECTOR = '#discount-code-fake';
		this.EMPTY_CART = '.cart-empty';
		this.ITEM_CONTAINER_SELECTOR = null;
	}

	checkCondition() {
		const isTotal = document.querySelector(this.TOTAL_SELECTOR);
		const emptyCart = document.querySelector(this.EMPTY_CART);
		const spiner =
			document.querySelector(this.SPINER) && document.querySelector(this.SPINER).style.display === 'none';
		const codeInput = document.querySelector(this.INPUT_SELECTOR);
		return (
			!emptyCart &&
			isTotal &&
			(window.location.href.startsWith(CART_URL) ||
				(window.location.href.startsWith(CHECKOUT) && codeInput && spiner))
		);
	}

	beforeTestCodes() {
		this.apply = new URLSearchParams();
		this.remove = new URLSearchParams();
		return Promise.resolve();
	}

	getExistingCodes() {
		const codes = [
			document.querySelector(this.APPLIED_CODES_SELECTOR) &&
				document.querySelector(this.APPLIED_CODES_SELECTOR).textContent,
		].filter(Boolean);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes(codes) {
		return this._removeCode(codes[0]).then(responseJSON => this._parseTotal(responseJSON));
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log('originalPrice: %s', originalPrice);
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				isValid: true,
			};
			return this._applyCode(code).then(response => {
				if (response.errors || parseFloat(response.totals.discount_amount !== 0)) {
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}
				result.finalPrice = this._parseTotal(response);
				result.finalDiscount = originalPrice - result.finalPrice;
				return this._removeCode(code).then(() => bus.$emit('code-checked', result));
			});
		});
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(responseHTML => {
			const total = this._parseTotal(responseHTML);
			const shippingElem = responseHTML.match(this.SHIPPING_REGEX);
			const shipping = shippingElem ? shippingElem[1] : 0;
			const taxElem = responseHTML.match(this.TAX_REGEX);
			const tax = taxElem ? taxElem[1] : 0;
			return total - toFloat(shipping) - toFloat(tax);
		});
	}

	_applyCode(code) {
		this.apply.set('last_code', code);
		this.apply.set('coupon_code', code);
		this.apply.set('isAjax', 1);
		return this._manageCode(`${CART_URL}/couponPost`, 1);
	}

	_removeCode(code) {
		this.remove.set('last_code', code);
		this.remove.set('remove_coupon', code);
		this.remove.set('coupon_code', '');
		this.remove.set('isAjax', 1);

		return this._manageCode(`${CART_URL}/couponPost`, 0);
	}

	_manageCode(requestURL, action) {
		return fetch(requestURL, {
			method: 'POST',
			body: action ? this.apply : this.remove,
			credentials: 'include',
		}).then(response => response.json());
	}

	_parseTotal(data) {
		if (data) {
			const totalElem = data && data.totals ? data.totals.base_grand_total : data.match(this.totalRegExp)[1];
			return toFloat(totalElem);
		}
		return elemToFloat(document.querySelector(this.TOTAL_SELECTOR));
	}
};

export default {
	name: 'Vince Camuto',
	domain: 'vincecamuto.com',
	isSinglePageApp: true,
	Driver,
};
