import MagentoTemplate from './_base';
import { format, DOM } from '../../../utils';

const { toFloat } = format;
const { extractData } = DOM;

const CART_URL = 'https://www.villeroy-boch.ca/shop/en/checkout/cart';

/*
Behavior description:
- can get existing codes;
- can remove codes;
- valid code cann't be replaced with another valid code;
- can't apply multiple codes (not stackable);
- code testing may be run on 'Cart' page;
*/

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.APPLIED_CODES_SELECTOR = '#discount-coupon-form button';
		this.TOTAL_SELECTOR = '#shopping-cart-totals-table tr:last-child .price';

		this.NEED_REMOVE_CODE = true;
		this.STACKABLE = false;
		this.VALID_CHECKOUT_PAGE = true;

		this.APPLIED_CODE_REG = /coupon code\s+"(\w+)/i;
		this.REG_TOTAL = /(&#36;)|[^0-9.-]+/g;
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)]
			.filter(codeElement => codeElement.innerText.match(this.APPLIED_CODE_REG))
			.map(codeElement => codeElement.innerText.match(this.APPLIED_CODE_REG)[1]);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	_parseTotal(data = false) {
		return toFloat(
			extractData(this.TOTAL_SELECTOR, data)
				.trim()
				.replace(this.REG_TOTAL, ''),
		);
	}
};

export default {
	name: 'Villeroy & Boch Canada',
	domain: 'villeroy-boch.ca',
	Driver,
	maxCoupons: 6, // site is very slow
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) && document.querySelector('#cart-discount-toggle');
	},
};
