import MagentoTemplate from './_base';
import { format, DOM } from '../../../utils';
import { wait } from '../../../utils/browser';

const { toFloat, toInt, elemToFloat } = format;
const { extractData, extractElement } = DOM;

const BASE_URL = 'https://www.lecreuset.com/';
const CART_URL = `${BASE_URL}checkout/cart`;

/* Behavior description:
+ Merchant codes: yes (from the cart page hidden block)
+ Existing codes: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: yes
+ Free shipping: no
+ isValid implemented: no
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: Cart
+ Is driver SPA: no
 */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.TOTAL_SELECTOR = '#shopping-cart-totals-table tfoot span.price';
		this.COUNT_ITEM_SELECTOR = '.header-minicart .count';
		this.MERCHANT_CODES_SELECTOR = '.skinny-banner .banner-line-break';
		this.MERCHANT_CODES_REGEX = /code: (\w+)/i;
		this.CHARGES_SELECTOR = '#shopping-cart-totals-table tbody td:last-child';
		this.CHARGES_REGEX = /(shipping|handling|tax)/i;
		this.TOTAL_REG = /(&#36;)|[^0-9.-]+/g;
		this.FREE_GIFT_SELECTOR = '#ampromo-items';
		this.ADD_GIFT_BUTTON_SELECTOR = '#ampromo-carousel-content .ampromo-slide:first-child button';
	}

	getMerchantCodes() {
		return this._getPage().then(responseText => {
			return [
				...new Set(
					[...extractElement(this.MERCHANT_CODES_SELECTOR, responseText, true)]
						.map(elem => elem.innerText.match(this.MERCHANT_CODES_REGEX))
						.filter(Boolean)
						.map(elem => elem[1]),
				),
			];
		});
	}

	async getPreTaxShippingTotal() {
		const cartStr = await fetch(window.location.href, { credentials: 'include' }).then(response => response.text());
		const total = this._parseTotal(cartStr);
		const taxes = [...extractElement(this.CHARGES_SELECTOR, cartStr, true)]
			.filter(el => el.parentElement.textContent.match(this.CHARGES_REGEX))
			.map(elemToFloat)
			.filter(Boolean)
			.reduce((charges, charge) => charges + charge, 0);
		return total - taxes;
	}

	getUserData() {
		return true;
	}

	checkRestoreDataCondition() {
		return document.querySelector(this.FREE_GIFT_SELECTOR);
	}

	setUserData() {
		const addToCartButtonSelector = document.querySelector(this.ADD_GIFT_BUTTON_SELECTOR);
		const getAddToCartButtonSelector = () => addToCartButtonSelector.offsetParent;
		return wait(getAddToCartButtonSelector, 10000, 400).then(() => addToCartButtonSelector.click());
	}

	_parseItemsCount(data) {
		if (extractElement(this.FREE_GIFT_SELECTOR, data)) {
			return toInt(extractData(this.COUNT_ITEM_SELECTOR, data) || 0) + 1;
		}
		return toInt(extractData(this.COUNT_ITEM_SELECTOR, data) || 0);
	}

	_parseTotal(data = false) {
		return toFloat(extractData(this.TOTAL_SELECTOR, data).replace(this.TOTAL_REG, ''));
	}

	_getPage(url = BASE_URL) {
		return fetch(url, {
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	name: 'Le Creuset',
	domain: 'lecreuset.com',
	Driver,
	restoreUserData: true,
	isCartEmpty() {
		const productsCount = document.querySelector('.header-minicart .count');
		return !!(productsCount && productsCount.innerText === '0');
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) && document.querySelector('.discount-form');
	},
};
