import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { format, DOM, log } from '../../../utils';

const { elemToFloat } = format;
const { extractElement } = DOM;

const CART_URL = 'https://www.draperjames.com/checkout/cart';

/** Behavior description:
 * - can get existing codes
 * - can remove codes
 * - valid code can be replaced with another valid code
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;

		this.APPLIED_CODES_SELECTOR = '#coupon_code';
		this.TOTAL_SELECTOR = '.table-total tr:last-child td:last-child';

		this.MANAGE_CODE_HEADERS = {
			'X-Requested-With': 'XMLHttpRequest',
		};
	}

	removeCodes(codes) {
		return this._removeCode(codes[0], this._getRemoveRequestData(this.cartPageHTML)).then(responseJson =>
			this._parseTotal(responseJson),
		);
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log('originalPrice: %s', originalPrice);

		return Promise.each(codes, code =>
			this._applyCode(code, this._getApplyRequestData(this.cartPageHTML)).then(responseJson => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					isValid: false,
				};
				if (responseJson.html_totals) {
					result.finalPrice = this._parseTotal(responseJson);
					result.finalDiscount = originalPrice - result.finalPrice;
					result.isValid = true;
				}
				return bus.$emit('code-checked', result);
			}),
		);
	}

	_manageCode(requestURL, bodyObj) {
		return fetch(requestURL, {
			method: 'POST',
			credentials: 'include',
			headers: this.MANAGE_CODE_HEADERS,
			body: bodyObj,
		}).then(response => response.json());
	}

	_parseTotal(data) {
		return elemToFloat(extractElement(this.TOTAL_SELECTOR, data && data.html_totals));
	}
};

export default {
	name: 'DraperJames',
	domain: 'draperjames.com',
	Driver,
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_URL);
	},
};
