import MagentoTemplate from './_base';

const CART_URL = 'https://www.luggagepros.com/checkout/cart';
const CHECKOUT_URL = 'https://www.luggagepros.com/onepagecheckout';

/** Behavior description:
 * - can get existing codes
 * - can remove codes
 * - valid code can be replaced with another valid code
 * - can't apply multiple codes (not stackable)
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;

		this.APPLIED_CODES_SELECTOR = '#coupon_code';
		this.TOTAL_SELECTOR = '#shopping-cart-totals-table strong .price';
	}
};

export default {
	name: 'Luggage Pros',
	domain: 'luggagepros.com',
	maxCoupons: 10, // site is slow
	Driver,
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) || href.startsWith(CHECKOUT_URL);
	},
};
