import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';

import { format, log, DOM, completionActions } from '../../../utils';
import { wait } from '../../../utils/browser';
import { extractElement } from '../../../utils/DOM';

const { toFloat, elemToFloat, toInt } = format;
const { setValue } = DOM;

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: yes
+ Free shipping: no
+ isValid implemented: yes on checkout
+ Codes can be replaced by another code: no
+ Max codes: 4 (for cart), 10 for checkout, else codes test too slow
+ Manually apply code: no
+ Pages, where extension works: cart and checkout
+ Is driver SPA: no
+ Gather codes on site pages: yes
======== Special case ========
On billing step we don't apply gift because Gift adds tax and original price is rising
*/

const BASE_URL = 'https://www.511tactical.com';
const CART_URL = `${BASE_URL}/checkout/cart`;
const IS_ON_CART = () => window.location.href.includes(CART_URL);

const MERCHANT_CODES_SELECTOR = '.bx-click-to-copy';

const _getMerchantCodes = () => [
	...new Set([...document.querySelectorAll(MERCHANT_CODES_SELECTOR)].map(el => el.textContent)),
];

const DriverCart = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.TOTAL_SELECTOR = '.grand .price';
		this.ITEM_CONTAINER_SELECTOR = 'tbody.cart.item';
		this.INPUT_FIELD_SELECTOR = '.cart-summary .discount';
		this.FORM_KEY_SELECTOR = 'input[name="form_key"]';
		this.GRAND_AMOUNT_REGEX = /"base_grand_total":"?([0-9.]+)"?/;
		this.SHIPPING_AMOUNT_REGEX = /"base_shipping_amount":"?([0-9.]+)"?/;
		this.TAX_AMOUNT_REGEX = /"Tax","value":"?([0-9.]+)"?/;

		this.afterCodeCheck = () => {
			// Clear this cookie to remove 'applied code' messages
			document.cookie = 'mage-messages=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
		};
		this.totalRegExp = {
			regexObj: /"grand_total":"?([0-9.]+)"?/,
			groupNumber: 1,
		};
	}

	checkCondition() {
		const correctUrl = window.location.href.includes(this.CART_URL);
		const codeInput = document.querySelector(this.INPUT_FIELD_SELECTOR);
		const isTotal = document.querySelector(this.TOTAL_SELECTOR);

		return correctUrl && codeInput && isTotal;
	}

	beforeTestCodes() {
		this.FORM_KEY = document.querySelector(this.FORM_KEY_SELECTOR).getAttribute('value');
		this.REMOVE_REQUEST_EXTRA_VALUES = {
			remove: 1,
			form_key: this.FORM_KEY,
		};
		this.APPLY_REQUEST_EXTRA_VALUES = {
			remove: 0,
			form_key: this.FORM_KEY,
		};
	}

	getMerchantCodes() {
		return _getMerchantCodes();
	}

	getPreTaxShippingTotal() {
		return this._getCart().then(responseHTML => {
			const total = toFloat(responseHTML.match(this.GRAND_AMOUNT_REGEX)[1]);
			const shipping = toFloat(responseHTML.match(this.SHIPPING_AMOUNT_REGEX)[1]) || 0;
			const taxes = toFloat(responseHTML.match(this.TAX_AMOUNT_REGEX)[1]) || 0;
			return total - shipping - taxes;
		});
	}

	_getCart() {
		return fetch(`${this.CART_URL}`, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}
};

const DriverCheckout = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.API_URL = `${BASE_URL}/rest/default/V1`;
		this.TOTAL_SELECTOR = '.grand .price';
		this.XPID_REGEX = /xpid:"([A-Za-z0-9_=]*)"/;
		this.ENTITY_ID_REGEX = /"entity_id":"([A-Za-z0-9]*)"/;
		this.ITEM_SELECTOR = '.item-options dd[data-bind="text: $parent.qty"]';
		this.CREATE_ACCOUNT_BUTTON_SELECTOR = `a[href="${BASE_URL}/customer/account/create/"]`;
		this.APPLY_CODE_SELECTOR = '.discount-code';
		this.SPINNER_SELECTOR = '.loader';
		this.APPLY_BUTTON_SELECTOR = '.action-apply[value="Apply Discount"]';
		this.CODE_INPUT_SELECTOR = '.input-text[name="discount_code"]';
		this.OPEN_CODE_INPUT_SELECTOR = '.payment-option-title[aria-expanded="false"]';
		this.CANCEL_BUTTON_SELECTOR = '.action-cancel[value="Cancel"]';
	}

	checkCondition() {
		const isSpinner = (document.querySelector(this.SPINNER_SELECTOR) || {}).offsetParent;
		const isCodeInput = (document.querySelector(this.APPLY_CODE_SELECTOR) || {}).offsetParent;
		const isTotalPrice = (document.querySelector(this.TOTAL_SELECTOR) || {}).offsetParent;
		return !isSpinner && isCodeInput && isTotalPrice;
	}

	async beforeTestCodes() {
		[this.token] = this._getRegExValue(this.XPID_REGEX);
		this.ENTITY_ID = this._getRegExValue(this.ENTITY_ID_REGEX);

		this.headers = {
			'Content-Type': 'application/json',
			'x-newrelic-id': `${this.token}`,
			'x-requested-with': 'XMLHttpRequest',
		};

		await this._checkUserAuthorization().then(response => {
			this.isLoggedUser = response;
		});
	}

	getMerchantCodes() {
		return _getMerchantCodes();
	}

	getExistingCodes() {
		return this._getPaymentInformation().then(json => {
			const codes = [json.totals.coupon_code].filter(Boolean);

			return {
				codes,
				codeCount: codes.length,
			};
		});
	}

	removeCodes() {
		return this._removeCodeManually()
			.then(() => this._getPaymentInformation())
			.then(responseJSON => ({
				originalPrice: this._parseTotal(responseJSON),
				originalItemsCount: this._parseItemsCount(responseJSON),
			}));
	}

	checkCodes(codes, originalPrice = this._parseTotal(), originalItemsCount = this._parseItemsCount()) {
		log(`Original price: ${originalPrice}`);
		log(`Original items count: ${originalItemsCount}`);
		return Promise.each(codes, code =>
			this._applyCode(code).then(responseJSON => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					freeGifts: 0,
					isValid: true,
				};
				if (!responseJSON) {
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}

				result.finalPrice = this._parseTotal(responseJSON);
				result.finalDiscount = originalPrice - result.finalPrice;
				result.freeGifts = this._parseItemsCount(responseJSON) - originalItemsCount;

				return this._removeCode(code).then(() => bus.$emit('code-checked', result));
			}),
		);
	}

	applyCodes(codes) {
		return this._manuallyApplyCode(codes[0]);
	}

	getPreTaxShippingTotal() {
		return Promise.delay(2000)
			.then(() => this._getPaymentInformation())
			.then(responseJSON => {
				const {
					base_grand_total: total,
					base_shipping_amount: shipping,
					base_tax_amount: tax,
				} = responseJSON.totals;
				return toFloat(total) - toFloat(shipping) - toFloat(tax);
			});
	}

	_removeCode() {
		return this._manageCode('DELETE');
	}

	_manuallyApplyCode(code) {
		this._openSideWithCodesInput();
		const codesInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		const applyButton = document.querySelector(this.APPLY_BUTTON_SELECTOR);
		setValue(codesInput, code, 'change');
		applyButton.click();
		return Promise.resolve();
	}

	_removeCodeManually() {
		this._openSideWithCodesInput();
		const isApplyButton = () => document.querySelector(this.APPLY_BUTTON_SELECTOR);
		document.querySelector(this.CANCEL_BUTTON_SELECTOR).click();
		return wait(isApplyButton, 6000, 300);
	}

	_applyCode(code) {
		return this._manageCode('PUT', code).then(response => (!response ? null : this._getPaymentInformation()));
	}

	_manageCode(method, code = '') {
		const url = this.isLoggedUser
			? `${this.API_URL}/carts/mine/coupons/${code}`
			: `${this.API_URL}/guest-carts/${this.ENTITY_ID}/coupons/${code}`;

		return fetch(url, {
			method,
			headers: this.headers,
		}).then(response => (response.status === 404 ? null : response.json()));
	}

	_openSideWithCodesInput() {
		const openSideWithCodesInput = document.querySelector(this.OPEN_CODE_INPUT_SELECTOR);
		if (openSideWithCodesInput) {
			return openSideWithCodesInput.click();
		}
		return null;
	}

	_parseItemsCount(json) {
		if (json) {
			return toFloat(json.totals.items_qty);
		}
		return [...document.querySelectorAll(this.ITEM_SELECTOR)]
			.map(el => toInt(el.textContent))
			.reduce((a, b) => a + b, 0);
	}

	_parseTotal(json) {
		if (json) {
			return toFloat(json.totals.base_grand_total);
		}
		return elemToFloat(document.querySelector(this.TOTAL_SELECTOR));
	}

	_getPaymentInformation() {
		const url = this.isLoggedUser
			? `${this.API_URL}/carts/mine/payment-information?_=${Date.now()}`
			: `${this.API_URL}/guest-carts/${this.ENTITY_ID}/payment-information?_=${Date.now()}`;

		return fetch(url, {
			method: 'GET',
			headers: this.headers,
		}).then(response => response.json());
	}

	_getRegExValue(regEx) {
		return [...document.querySelectorAll('script')]
			.map(el => el.textContent.match(regEx))
			.filter(Boolean)
			.map(el => el[1]);
	}

	_checkUserAuthorization() {
		return fetch(`${BASE_URL}/customer/account/index`, {
			method: 'GET',
		})
			.then(response => response.text())
			.then(data => !extractElement(this.CREATE_ACCOUNT_BUTTON_SELECTOR, data));
	}
};

export default {
	name: '5.11 Tactical',
	domain: '511tactical.com',
	showDifferentDiscountMessage: true,
	enableFreeGiftsWarning: true,
	completionAction: IS_ON_CART() ? completionActions.default : completionActions.none,
	Driver: IS_ON_CART() ? DriverCart : DriverCheckout,
	maxCoupons: IS_ON_CART() ? 4 : 14,
	isSinglePageApp: true,
};
