import Promise from 'bluebird';
import bus from '../../bus';
import MagentoTemplate from './_base';
import { DOM, log, format, sendXhr } from '../../../utils';

const { toFloat, elemToFloat } = format;
const { extractElement } = DOM;
const BASE_URL = 'https://www.scrubsandbeyond.com';
const CART_URL = `${BASE_URL}/checkout/cart`;
const CHECKOUT_URL = `${BASE_URL}/checkout/#payment`;

/* Driver behavior description:
    Existing code: yes
    Remove code: no
    Stackable: no
    Free gift support: no
    isValid implemented: no
    Manually apply code: no
    maxCoupons: 2 - for logged, for not - 5
    Active pages: cart, checkout(payment step) - user will be redirected to the 1 step of the checkout but his data will be saved
Optional:
    Show message about free shipping on the cart page: no
    Is driver SPA and add the condition to show popup: yes
    Gather codes on site pages: no */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.APPLIED_CODES_SELECTOR = '#coupon_code, #discount-code';
		this.APPLY_COUPON_FORM_SELECTOR = '#discount-coupon-form, #discount-form';

		this.TOTAL_SELECTOR = '.grand .price';
		this.EMPTY_CART_SELECTOR = '.cart-empty';
		this.TAX_SELECTOR = '.totals-tax span.price';

		this.afterCodeCheck = () => {
			// Clear this cookie to remove 'applied code' messages
			document.cookie = 'mage-messages=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
		};

		this.QUOTE_ID_REGEXP = /entity_id":"([A-Za-z0-9]+)(?=")/;

		this.LOAD_SELECTOR = '#checkout-loader';
	}

	checkCondition() {
		const { href } = window.location;
		const emptyCartElem = document.querySelector(this.EMPTY_CART_SELECTOR);
		const emptyCart = emptyCartElem && emptyCartElem.offsetParent;
		const totalElem = document.querySelector(this.TOTAL_SELECTOR);
		const loadElem = document.querySelector(this.LOAD_SELECTOR);
		const isOnCartUrl = href.startsWith(CART_URL) && !emptyCart;
		return ((href.startsWith(CHECKOUT_URL) && !loadElem) || isOnCartUrl) && totalElem && totalElem.offsetParent;
	}

	beforeTestCodes() {
		this.HEADERS = {
			'Content-Type': 'application/json',
			'X-Requested-With': 'XMLHttpRequest',
		};
		this.isTotalwithoutTaxes =
			window.location.href.startsWith(CART_URL) && !document.querySelector(this.TAX_SELECTOR);

		this.userIsLogged = this._getWindowObjectData('window.checkoutConfig.isCustomerLoggedIn');

		if (this.userIsLogged) {
			this.quoteId = 'mine';
		} else {
			const quoteScript = [...document.scripts].find(el => el.text.match(this.QUOTE_ID_REGEXP));
			[, this.quoteId] = quoteScript.text.match(this.QUOTE_ID_REGEXP);
		}
		if (window.location.href.startsWith(CART_URL)) {
			const bodyData = this._getWindowObjectData('window.checkoutConfig.selectedShippingMethod');
			const bodyDataAddress = { countryId: 'US', postcode: '*', region: 'R', regionId: '0' };

			this.bodyData = {
				addressInformation: {
					address: bodyDataAddress,
					shipping_carrier_code: bodyData ? bodyData.carrier_code : 'tablerate',
					shipping_method_code: bodyData ? bodyData.method_code : 'bestway',
				},
			};
		}
		// need to add time to render existing code
		return Promise.delay(1500);
	}

	getExistingCodes() {
		const codes = [document.querySelector(this.APPLIED_CODES_SELECTOR).value].filter(Boolean);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes() {
		return this._removeCode();
	}

	beforeCheckCodes() {
		return this._getTotalInfo().then(responseJson => ({
			originalPrice: this._parseTotal(responseJson),
		}));
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log('originalPrice: %s', originalPrice);

		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				isValid: true,
			};
			return this._applyCode(code).then(responseJSON => {
				if (responseJSON.message) {
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}
				return this._getTotalInfo().then(response => {
					result.finalPrice = this._parseTotal(response);
					result.finalDiscount = originalPrice - result.finalPrice;
					return this._removeCode().then(() => bus.$emit('code-checked', result));
				});
			});
		});
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	getPreTaxShippingTotal() {
		return this._getTotalInfo().then(responseJSON => {
			const totalsData = this.isTotalwithoutTaxes ? responseJSON : responseJSON.totals;
			const total = this._parseTotal(responseJSON);
			const shipping = toFloat(totalsData.base_shipping_amount);
			const taxes = toFloat(totalsData.base_tax_amount);
			return total - shipping - taxes;
		});
	}

	_getTotalInfo() {
		if (this.isTotalwithoutTaxes) {
			return sendXhr(
				`${BASE_URL}/rest/default/V1/${this.userIsLogged ? 'carts' : 'guest-carts'}/${
					this.quoteId
				}/totals-information`,
				{
					method: 'POST',
					credentials: 'include',
					body: JSON.stringify(this.bodyData),
					headers: this.HEADERS,
				},
			).then(response => response.json());
		}
		return sendXhr(
			`${BASE_URL}/rest/default/V1/${this.userIsLogged ? 'carts' : 'guest-carts'}/${
				this.quoteId
			}/payment-information?_=${Date.now()}`,
			{
				method: 'GET',
				credentials: 'include',
				headers: this.HEADERS,
			},
		).then(response => response.json());
	}

	_manageCode(method, code = '') {
		return sendXhr(
			`${BASE_URL}/rest/default/V1/${this.userIsLogged ? 'carts' : 'guest-carts'}/${
				this.quoteId
			}/coupons/${code}`,
			{
				method,
				credentials: 'include',
				body: JSON.stringify(this.bodyData),
				headers: this.HEADERS,
			},
		).then(response => response.json());
	}

	_applyCode(code) {
		return this._manageCode('PUT', code);
	}

	_removeCode() {
		return this._manageCode('DELETE');
	}

	_parseTotal(data) {
		if (data) {
			return data.totals ? toFloat(data.totals.base_grand_total) : toFloat(data.base_grand_total);
		}
		return elemToFloat(extractElement(this.TOTAL_SELECTOR));
	}

	_getWindowObjectData(variableString) {
		// We will put generated request payload to this element
		const requestDataElement = document.createElement('div');
		requestDataElement.id = 'get-data-genie';
		Object.assign(requestDataElement.style, { display: 'none' });

		document.documentElement.appendChild(requestDataElement);

		// Script which will generate request payload
		const requestDataScript = document.createElement('script');
		requestDataScript.textContent = `document.getElementById("${
			requestDataElement.id
		}").innerText = JSON.stringify(${variableString});`;
		document.documentElement.appendChild(requestDataScript);
		const data = JSON.parse(requestDataElement.innerText);
		// Remove created blocks
		requestDataElement.parentNode.removeChild(requestDataElement);
		requestDataScript.parentNode.removeChild(requestDataScript);
		return data;
	}
};

export default {
	name: 'Scrubs and Beyond',
	domain: 'scrubsandbeyond.com',
	Driver,
	maxCoupons: document.querySelector('.logged') ? 2 : 5, // code testing for logged in user is very slowly
	isSinglePageApp: true,
};
