import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { DOM } from '../../../utils';

const { extractElement } = DOM;

/* BEHAVIOUR
 * One to one as template
 * One more page where code can be applied
 * On checkout page another selector.
 * Added for case when code was not applied, and
 * total is getting from page
 */

const BASE_URL = 'https://shop.drcolbert.com';
const CART_URL = `${BASE_URL}/checkout/cart`;
const CHECKOUT_URL = `${BASE_URL}/onepage`;

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.TOTAL_SELECTOR = '#shopping-cart-totals-table strong .price, .grand_total .price';
		this.INVALID_CODE_SELECTOR = '.error-msg';
	}

	removeCodes(codes) {
		return this._removeCode(codes[0], this._getRemoveRequestData(this.cartPageHTML)).then(responseString => ({
			originalPrice: this._parseTotal(responseString),
			originalItemsCount: this._parseItemsCount(responseString),
		}));
	}

	// check if the valid code was found
	checkCodes(codes, originalPrice = this._parseTotal()) {
		return Promise.each(codes, code =>
			this._applyCode(code, this._getApplyRequestData(this.cartPageHTML)).then(responseString => {
				const finalPrice = this._parseTotal(responseString);
				const finalDiscount = originalPrice - finalPrice;
				const result = { code, finalPrice, finalDiscount };
				if (extractElement(this.INVALID_CODE_SELECTOR, responseString)) {
					result.isValid = false;
				}
				return bus.$emit('code-checked', result);
			}),
		);
	}
};

export default {
	name: 'Dr. Colbert',
	domain: 'shop.drcolbert.com',
	maxCoupons: 3,
	Driver,
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) || href.startsWith(CHECKOUT_URL);
	},
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
};
