import MagentoTemplate from './_base';
import { DOM, format } from '../../../utils';
import { sessionTimer } from '../../lib';

const { extractElement } = DOM;
const { elemToFloat } = format;

const CART_URL = 'https://invictastores.com/checkout/cart';

/** Behavior description:
 * - can get existing codes
 * - can remove codes
 * - can't apply code without removing previous
 * - can apply one code (not stackable)
 * - session timer 20 min
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.isTaxPresent = null;
		this.TABLE_ROW_SELECTOR = '#shopping-cart-totals-table tbody td';
		sessionTimer.initialize({
			actionInterval: null,
			timeout: 20,
		});
	}

	beforeTestCodes() {
		this.isTaxPresent = Boolean(
			[...document.querySelectorAll(this.TABLE_ROW_SELECTOR)].find(el => el.textContent.includes('Tax')),
		);
		return Promise.resolve();
	}

	_parseTotal(htmlString) {
		const totalElement = [...extractElement(this.TOTAL_SELECTOR, htmlString, true)];
		return elemToFloat(this.isTaxPresent ? totalElement[1] : totalElement[0]);
	}
};

export default {
	name: 'Invicta Stores',
	domain: 'invictastores.com',
	maxCoupons: 8,
	Driver,
	isCartEmpty() {
		return Boolean(document.querySelector('.cart-empty'));
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_URL);
	},
};
