import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { log } from '../../../utils';

/* Behavior description:
+ Existing code: yes
+ Remove code: yes, replace one code by another one
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: no
+ Codes can be replaced by another code: yes
+ Manually apply code: no
+ Pages, where extension works: Cart, Checkout
+ Is driver SPA: no
+ Gather codes on site pages: no
+ maxCoupons: on Cart - 17, on Checkout - 11, due to test time
+ always get price from Cart response, because on Cart page response doesn't always include tax (so if tax present original price in console can be not the same that is on page)
  for Checkout page get all data from Cart(so total will be not the same that is shown on Checkout page), because in Checkout response we get total without tax and shipping
  after visiting Payment step(total includes tax and shiping)
*/

const CART_URL = 'https://www.reeds.com/checkout/cart';
const CHECKOUT_URL = 'https://www.reeds.com/checkout';

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;

		this.APPLIED_CODES_SELECTOR = '#coupon_code';
		this.TOTAL_SELECTOR = '.grand .price';

		this.afterCodeCheck = () => {
			document.cookie = 'mage-messages=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
		};

		this.totalRegExp = {
			regexObj: /"grand_total"."([0-9,.]+)(?=")/,
			groupNumber: 1,
		};
	}

	beforeCheckCodes(originalPrice) {
		if (originalPrice) {
			return originalPrice;
		}
		if (this._isOnCart()) {
			// on Cart page total includes tax, but response total without tax
			return this._getUpdatedCart().then(response => this._parseTotal(response));
		}
		return Promise.resolve();
	}

	checkCodes(codes, originalPrice = this._parseTotal(this.cartPageHTML)) {
		log(`Original price: ${originalPrice}`);

		return Promise.each(codes, code =>
			this._applyCode(code, this._getApplyRequestData(this.cartPageHTML))
				// on Checkout page total doesn't include shipping, need to get total from Cart
				.then(response => (this._isOnCart() ? response : this._getUpdatedCart()))
				.then(responseString => {
					const finalPrice = this._parseTotal(responseString);
					const finalDiscount = originalPrice - finalPrice;

					return bus.$emit('code-checked', { code, finalPrice, finalDiscount });
				}),
		);
	}
};

export default {
	name: 'Reeds Jewelers',
	domain: 'reeds.com',
	Driver,
	maxCoupons: window.location.href.startsWith(CART_URL) ? 17 : 11, // due to test time
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) || href.startsWith(CHECKOUT_URL);
	},
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
};
