import Promise from 'bluebird';
import bus from '../../bus';

import { format, DOM, log } from '../../../utils';
import MagentoTemplate from './_base';

const { toFloat, elemToFloat } = format;
const { extractElement } = DOM;

/*
Driver behaviour description:
    Existing code: yes
    Remove code: yes
    Stackable: no
    Free gift support: no
    'isValid' implemented: yes(we marked all codes as false if we have fake apply, in other case we marked all codes as true)
    Codes can be replaced by another code: no
    Pages, where extension works: Cart, Checkout(Payment & Review)
    sessionTimer : no
*/

const BASE_URL = 'https://www.riteaid.com';
const IS_ON_CART = window.location.href.includes(`${BASE_URL}/shop/checkout/cart`);

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.INPUT_CODE_SELECTOR = '#coupon_code, #discount-code';
		this.TOTAL_SELECTOR = '.grand.totals .price';
		this.FORM_KEY_SELECTOR = 'input[name=form_key]';

		this.QUOTE_ID_REGEXP = /quoteId["]?[:]?["]?(.\w+)/;

		this.MERCHANT_CODE_SELECTOR = '.promotional-header-message-container';
		this.MERCHANT_CODE_REGEX = /use promo code[:]?\s+(.\w+)/i;
		this.APPLIED_COUPON_SALE_SELECTOR = '.discount.coupon';
		this.CART_EMPTY = '.cart-empty';
		this.LOADER = '.loader';

		this.afterCodeCheck = () => {
			// Clear this cookie to remove 'applied code' messages
			document.cookie = 'mage-messages=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
		};
	}

	checkCondition() {
		const isCorrectUrl = IS_ON_CART || window.location.href.includes('checkout/#payment');
		const isCartEmpty = document.querySelector(this.CART_EMPTY);
		const isInputPresent = document.querySelector(this.INPUT_CODE_SELECTOR);
		const isLoading = document.querySelector(this.LOADER) && document.querySelector(this.LOADER).offsetParent;
		const isTotalPresent = document.querySelector(this.TOTAL_SELECTOR);

		return isCorrectUrl && !isCartEmpty && isInputPresent && !isLoading && isTotalPresent;
	}

	beforeTestCodes() {
		this.userIsLogged = this._getWindowObjectData('window.checkoutConfig.isCustomerLoggedIn');

		const postcode = this._getWindowObjectData(
			'window.checkoutConfig.payment.sd_applepay.paymentRequest.shippingContact.postalCode',
		);

		const region = this._getWindowObjectData(
			'window.checkoutConfig.payment.sd_applepay.paymentRequest.shippingContact.administrativeArea',
		);

		const countryId = this._getWindowObjectData(
			'window.checkoutConfig.payment.sd_applepay.paymentRequest.shippingContact.countryCode',
		);

		if (this.userIsLogged) {
			this.quoteId = 'mine';
		} else {
			const quoteIdActions = JSON.parse(localStorage.getItem('mage-cache-storage')).cart.extra_actions;
			[, this.quoteId] = quoteIdActions.match(this.QUOTE_ID_REGEXP);
		}

		const bodyData = this._getWindowObjectData('window.checkoutConfig.selectedShippingMethod');
		const bodyDataAddress = { countryId: countryId.toUpperCase() };
		bodyDataAddress.postcode = postcode || null;
		bodyDataAddress.region = region || '';

		this.bodyData = {
			addressInformation: {
				address: bodyDataAddress,
				shipping_method_code: bodyData ? bodyData.method_code : 'freeshipping',
				shipping_carrier_code: bodyData ? bodyData.carrier_code : 'freeshipping',
			},
		};
		// eslint-disable-next-line camelcase
		const form_key = document.querySelector(this.FORM_KEY_SELECTOR).value;
		this.body = new URLSearchParams({
			form_key,
		});
		return Promise.resolve();
	}

	getMerchantCodes() {
		return [...document.querySelectorAll(this.MERCHANT_CODE_SELECTOR)]
			.map(el => el.textContent.match(this.MERCHANT_CODE_REGEX))
			.filter(Boolean)
			.map(res => res[1]);
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.INPUT_CODE_SELECTOR)]
			.map(codeElement => codeElement.value)
			.filter(Boolean);

		if (codes.length === 0 && document.querySelector(this.APPLIED_COUPON_SALE_SELECTOR)) {
			this.FAKE_CODE_APPLY = true;
		}

		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes() {
		return this._removeCode().then(() =>
			this._getUpdatedCart().then(responseHTML => this._parseTotal(responseHTML)),
		);
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);

		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				isValid: true,
			};

			if (this.FAKE_CODE_APPLY) {
				result.isValid = false;
				return Promise.delay(1000).then(() => bus.$emit('code-checked', result));
			}

			return this._applyCode(code).then(() =>
				this._getUpdatedCart().then(responseHTML => {
					result.finalPrice = this._parseTotal(responseHTML);
					result.finalDiscount = originalPrice - result.finalPrice;
					return this._removeCode(code).then(() => bus.$emit('code-checked', result));
				}),
			);
		});
	}

	applyCodes(codes) {
		if (this.afterCodeCheck) {
			this.afterCodeCheck();
		}
		return this._applyCode(codes[0]);
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(res => {
			if (IS_ON_CART) {
				return res.base_subtotal_with_discount;
			}
			return res.totals.base_subtotal_with_discount;
		});
	}

	_removeCode() {
		if (IS_ON_CART) {
			this.body.set('remove', 1);
			this.body.set('coupon_code', '');
			return this._manageCode();
		}
		return this._manageCode('DELETE');
	}

	_applyCode(code) {
		if (IS_ON_CART) {
			this.body.set('remove', 0);
			this.body.set('coupon_code', code);
			return this._manageCode();
		}
		return this._manageCode('PUT', code);
	}

	_manageCode(method, code = '') {
		if (IS_ON_CART) {
			return fetch(`${BASE_URL}/shop/checkout/cart/couponPost`, {
				method: 'POST',
				credentials: 'include',
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded',
				},
				body: this.body,
			});
		}
		return fetch(
			`${BASE_URL}/shop/rest/default/V1/${this.userIsLogged ? 'carts' : 'guest-carts'}/${
				this.quoteId
			}/coupons/${code}`,
			{
				method,
				credentials: 'include',
				headers: {
					'Content-Type': 'application/json',
					'X-Requested-With': 'XMLHttpRequest',
				},
			},
		);
	}

	_getUpdatedCart() {
		if (IS_ON_CART) {
			return fetch(
				`${BASE_URL}/shop/rest/default/V1/${this.userIsLogged ? 'carts' : 'guest-carts'}/${
					this.quoteId
				}/totals-information`,
				{
					method: 'POST',
					credentials: 'include',
					body: JSON.stringify(this.bodyData),
					headers: {
						'Content-Type': 'application/json',
						'X-Requested-With': 'XMLHttpRequest',
					},
				},
			).then(response => response.json());
		}
		return fetch(
			`${BASE_URL}/shop/rest/default/V1/${this.userIsLogged ? 'carts' : 'guest-carts'}/${
				this.quoteId
			}/payment-information?_=${Date.now()}`,
			{
				method: 'GET',
				credentials: 'include',
				headers: {
					'Content-Type': 'application/json',
					'X-Requested-With': 'XMLHttpRequest',
				},
			},
		).then(response => response.json());
	}

	_getWindowObjectData(variableString) {
		// We will put generated request payload to this element
		const requestDataElement = document.createElement('div');
		requestDataElement.id = 'get-data-genie';
		Object.assign(requestDataElement.style, { display: 'none' });

		document.documentElement.appendChild(requestDataElement);

		// Script which will generate request payload
		const requestDataScript = document.createElement('script');
		requestDataScript.textContent = `document.getElementById("${
			requestDataElement.id
		}").innerText = JSON.stringify(${variableString});`;
		document.documentElement.appendChild(requestDataScript);
		const data = JSON.parse(requestDataElement.innerText);
		// Remove created blocks
		requestDataElement.parentNode.removeChild(requestDataElement);
		requestDataScript.parentNode.removeChild(requestDataScript);
		return data;
	}

	_parseTotal(data) {
		if (data) {
			if (IS_ON_CART) {
				return toFloat(data.base_grand_total);
			}
			return toFloat(data.totals.base_grand_total);
		}
		return elemToFloat(extractElement(this.TOTAL_SELECTOR));
	}
};

export default {
	name: 'Rite Aid',
	domain: 'riteaid.com',
	isSinglePageApp: true,
	maxCoupons: 8,
	Driver,
};
