import MagentoTemplate from './_base';

const CART_URL = 'https://www.techrabbit.com/checkout/cart';

/** Behavior description:
 * - can get existing codes
 * - can remove codes
 * - codes can be applied on cart page
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.NEED_REMOVE_CODE = false;
	}
};

export default {
	name: 'TechRabbit',
	domain: 'techrabbit.com',
	Driver,
	maxCoupons: 10,
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_URL);
	},
};
