import MagentoTemplate from './_base';

const CART_URL = 'https://peekkids.com/checkout/cart/';

/** Behavior description:
 * - can get existing codes
 * - can remove codes
 * - valid code can be replaced with another valid code
 * - can't apply multiple codes (not stackable)
 * - show pop-up only on cart page, because other checkout steps have the problem to apply code manually
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.ITEM_CONTAINER_SELECTOR = true;
	}
};

export default {
	name: 'Peek Kids',
	domain: 'peekkids.com',
	Driver,
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_URL);
	},
};
