import MagentoTemplate from './_base';
import { completionActions } from '../../../utils';

const CHECKOUT_PAGE = 'https://lapolicegear.com/checkout';
const CART_URL = `${CHECKOUT_PAGE}/cart`;

const onCart = () => window.location.href.startsWith(CART_URL);

/* Driver behavior description:
    Existing code: yes
    Remove code: yes
    Stackable: no
    Free gift support: no
    isValid implemented: no
    Manually apply code: no
Optional:
    Show message about free shipping on the cart page (without discount info): no
    Is driver SPA and add the condition to show popup: yes
    Gather codes on site pages: no */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.NEED_REMOVE_CODE = false; // avoid codes removal to speed up the driver work

		this.TOTAL_SELECTOR = '.grand.totals strong .price';

		this.totalRegExp = {
			regexObj: /grand_total":(\d+.\d+|\d+)/,
			groupNumber: 1,
		};

		this.afterCodeCheck = () => {
			// Clear this cookie to remove 'applied code' messages
			document.cookie = 'mage-messages=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
		};
	}

	checkCondition() {
		const totalValue = document.querySelector(this.TOTAL_SELECTOR);
		const correctPage = window.location.href.startsWith(CHECKOUT_PAGE) || onCart();
		const currentState = totalValue && correctPage;
		return currentState;
	}

	applyCodes(codes) {
		this.afterCodeCheck();
		return this._applyCode(codes[0], this._getApplyRequestData(this.cartPageHTML));
	}
};

export default {
	name: 'LAPoliceGear.com',
	domain: 'lapolicegear.com',
	Driver,
	completionAction: completionActions.revisit,
	maxCoupons: 7, // website is slow
	isSinglePageApp: true,
	isOnCartUrl() {
		return false;
	},
};
