import MagentoTemplate from './_base';

/** Behavior description:
 * - can get existing codes only if user applies code manually and runs the extension
 * - can remove codes
 * - valid code can be replaced with another valid or invalid code
 * - can't apply multiple codes (not stackable)
 * - may run extension on 'Cart' and 'Checkout' pages
 * ======== Special case ========
 we don't show popup on the 'Checkout' page because applying codes will redirect us to the 'Cart' page
 and will clean all user's data
 * */

const BASE_URL = 'https://www.jasonwustudio.com';
const CART_URL = `${BASE_URL}/checkout/cart`;

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.APPLIED_CODES_SELECTOR = '.cart-wrapper .success-msg';
		this.TOTAL_SELECTOR = '.grand-total .price';
		this.ITEM_CONTAINER_SELECTOR = '#shopping-cart-table .item, #cart-sidebar .item';
		this.CODE_REGEX = /CODE\s"(\w+)/;
		this.NEED_REMOVE_CODE = false;
		this.afterCodeCheck = () => {
			document.cookie = 'mage-messages=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
		};
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)]
			.filter(codeElem => codeElem.innerText.match(this.CODE_REGEX))
			.map(code => code.innerText.match(this.CODE_REGEX)[1]);

		return {
			codes,
			codeCount: codes.length,
		};
	}
};

export default {
	name: 'Jason WU',
	domain: 'jasonwustudio.com',
	Driver,
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) && document.querySelector('#shopping-cart-table');
	},
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
};
