import MagentoTemplate from './_base';
import { completionActions } from '../../../utils';

const BASE_URL = 'https://www.jamesperse.com/checkout';
const CART_URL = `${BASE_URL}/cart`;

/* Driver behavior description:
    Existing code: yes
    Remove code: yes
    Stackable: no
    Free gift support: no
    isValid implemented: no
    active pages: cart, checkout
Optional:
    Show message about free shipping on the cart page (without discount info): yes
    Is driver SPA and add the condition to show popup: no
    Gather codes on site pages: no */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.APPLIED_CODES_SELECTOR = '.summary-collapse.discount-rule';
		this.TOTAL_SELECTOR = this._isOnCart()
			? '.totals tr:last-child .price'
			: '.checkout-costs-table tr:last-child .price';
		this.APPLY_COUPON_FORM_SELECTOR = '#discount-coupon-form, #fake-discount-coupon-form';
		this.REMOVE_COUPON_FORM_SELECTOR = this.APPLY_COUPON_FORM_SELECTOR; // must to be redefined

		this.FREE_SHIP_ELEM_SELECTOR = '.summary-collapse';

		this.VALID_CHECKOUT_PAGE = true;
	}

	getExistingCodes() {
		const inputCouponElement = document.querySelector(this.APPLIED_CODES_SELECTOR);
		const codes = inputCouponElement ? [inputCouponElement.innerText.trim().match(/(.*?)\s+/)[1]] : [];

		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes(codes) {
		return this._removeCode(codes[0], this._getRemoveRequestData()).then(responseTxt =>
			this._parseTotal(responseTxt),
		);
	}

	getPreTaxShippingTotal() {
		// on the cart page we have total without tax and shipping
		this.TOTAL_SELECTOR = '.totals tr:last-child .price';
		return this._getUpdatedCart().then(responseTxt => this._parseTotal(responseTxt));
	}
};

export default {
	name: 'James Perse',
	domain: 'jamesperse.com',
	maxCoupons: window.location.href.includes('/review') ? 3 : 4, // works slowly
	completionAction: completionActions.revisit,
	Driver,
	isCartEmpty() {
		return Boolean(document.querySelector('.shopping-bag-empty'));
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) || href.startsWith(`${BASE_URL}/review`);
	},
};
