import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { format, log } from '../../../utils';

const { toFloat } = format;

const BASE_URL = 'https://www.targetphoto.com';
const CHECKOUT_URL = `${BASE_URL}/checkout`;
const CART_URL = `${CHECKOUT_URL}/cart`;
const BASE_REQUEST_URL = `${BASE_URL}/rest/target/V1`;

/**
Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: yes
+ Codes can be replaced by another code: no
+ Pages, where extension works: cart, checkout
+ Is driver SPA: yes
+ Gather codes on site pages: yes
+ Special case about the driver works (if it needed).
  - maxCoupons (2, speed)
  - calculate the price online + store
 */

const isCart = window.location.href.startsWith(CART_URL);

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.APPLIED_CODE_SELECTOR = '.amCouponsCode, .discount.coupon';
		this.EMPTY_CART_SELECTOR = '.cart-empty';
		this.TOTAL_SECTION = isCart ? '.price[data-th*="Online"]' : '[data-th*="Online"] .price';
		this.TOTAL_SECTION_STORE = '[data-bind*="getPrintToStoreValue"]';
		this.totalRegExp = {
			regexObj: isCart ? /subtotal_with_discount":"?([0-9.]+)"?/ : /base_grand_total":"?([0-9.]+)"?/,
			groupNumber: 1,
		};
		this.totalRegExpStore = {
			regexObj: /window.p2sSubtotal ='?([0-9.]+)'?/,
			groupNumber: 1,
		};
		this.MERCHANT_CODE_SELECTOR = '.header-coupon';
		this.MERCHANT_CODE_REGEX = /code\s(\w+)/gi;

		this.ENTITY_ID_SELECTOR = /"entity_id":"(.*?)"/;

		this.afterCodeCheck = () => {
			// Clear this cookie to remove 'applied code' messages
			document.cookie = 'mage-messages=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
		};

		this.MANAGE_CODE_HEADERS = {
			'Content-Type': 'application/json',
			'x-requested-with': 'XMLHttpRequest',
		};

		this.SPINNER_SELECTOR = '[data-role="loader"]';
		this.CHECKOUT_SPINER = '#checkout-loader';
		this.NOT_LOGGED_SELECTOR = '.not-logged-in';
		this.FORM_KEY_SELECTOR = '[name="form_key"]';
	}

	checkCondition() {
		const spiner = [...document.querySelectorAll(this.SPINNER_SELECTOR)]
			.map(el => el.style.position)
			.filter(Boolean).length;
		const checkoutSpiner = document.querySelector(this.CHECKOUT_SPINER);
		const isCartEmpty = document.querySelector(this.EMPTY_CART_SELECTOR);
		const isTotalSection = document.querySelector(this.TOTAL_SECTION);
		const isOnCheckout = window.location.href.startsWith(CHECKOUT_URL);
		return (isCart || isOnCheckout) && !isCartEmpty && isTotalSection && !spiner && !checkoutSpiner;
	}

	beforeTestCodes() {
		this.IS_LOGGED_IN = document.querySelector(this.NOT_LOGGED_SELECTOR);
		if (this.IS_LOGGED_IN) {
			const entityId = [...document.querySelectorAll('script')]
				.find(element => element.textContent.match(this.ENTITY_ID_SELECTOR))
				.innerHTML.match(this.ENTITY_ID_SELECTOR)[1];
			this.requestData = { action: `${BASE_REQUEST_URL}/guest-carts/${entityId}/coupons/` };
			this.MANAGE_CODE_HEADERS = {
				'Content-Type': 'application/json',
				'x-requested-with': 'XMLHttpRequest',
			};
		} else {
			this.requestData = { action: `${CHECKOUT_URL}/cart/couponPost/` };
			this.MANAGE_CODE_HEADERS = {
				'Content-Type': 'application/x-www-form-urlencoded',
			};
			this.FORM_KEY = document.querySelector(this.FORM_KEY_SELECTOR).value;
			this.bodyData = new URLSearchParams({
				form_key: this.FORM_KEY,
				coupon_code_fake: '',
			});
		}

		return Promise.resolve();
	}

	getMerchantCodes() {
		const codeElem = document.querySelector(this.MERCHANT_CODE_SELECTOR);
		return codeElem
			? [...codeElem.textContent.matchAll(this.MERCHANT_CODE_REGEX)].filter(Boolean).map(code => code[1])
			: [];
	}

	getExistingCodes() {
		const codes = [
			...new Set(
				[...document.querySelectorAll(this.APPLIED_CODE_SELECTOR)]
					.map(el => (isCart ? el.value : el.textContent))
					.filter(Boolean),
			),
		];
		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes(codes) {
		return this._removeCode(codes[0])
			.then(() => this._getUpdatedCart())
			.then(responseString => this._parseTotal(responseString));
	}

	beforeCheckCodes(originalPrice) {
		return originalPrice || this._getUpdatedCart().then(resHtml => this._parseTotal(resHtml));
	}

	checkCodes(codes, originalPrice) {
		log('originalPrice: %s', originalPrice);
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				isValid: true,
			};
			return this._applyCode(code).then(response => {
				if (!response || response.message) {
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}
				return (this.IS_LOGGED_IN ? this._getUpdatedCart() : Promise.resolve(response)).then(responseCart => {
					result.finalPrice = this._parseTotal(responseCart);
					result.finalDiscount = originalPrice - result.finalPrice;
					return this._removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			});
		});
	}

	beforeApplyCodes() {
		// Clear this cookie to remove 'applied code' messages
		document.cookie = 'mage-messages=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
		return Promise.resolve();
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	_applyCode(code) {
		if (this.IS_LOGGED_IN) {
			return this._manageCode(`${this.requestData.action}${code}`, 'PUT').then(responseJSON =>
				responseJSON.json(),
			);
		}
		this.bodyData.set('remove_coupon', '');
		this.bodyData.set('coupon_code', code);
		this.bodyData.set('last_code', code);
		this.bodyData.set('coupon_code_fake', code);
		return this._manageCode(this.requestData.action, 'POST', this.bodyData).then(response => response.text());
	}

	_removeCode(code) {
		if (this.IS_LOGGED_IN) {
			return this._manageCode(this.requestData.action, 'DELETE');
		}
		this.bodyData.set('coupon_code_fake', '');
		this.bodyData.set('coupon_code', '');
		this.bodyData.set('last_code', code);
		this.bodyData.set('remove_coupon', code);
		return this._manageCode(this.requestData.action, 'POST', this.bodyData);
	}

	_manageCode(requestURL, method, body) {
		return fetch(requestURL, {
			method,
			credentials: 'include',
			headers: this.MANAGE_CODE_HEADERS,
			body,
		});
	}

	_parseTotal(htmlString) {
		const totalElement = htmlString.match(this.totalRegExp.regexObj)[this.totalRegExp.groupNumber];
		const totalStore = htmlString.match(this.totalRegExpStore.regexObj)[this.totalRegExpStore.groupNumber];
		return toFloat(totalElement) + toFloat(totalStore);
	}

	_getUpdatedCart() {
		return fetch(window.location.href, {
			method: 'GET',
			credentials: 'include',
		}).then(responseJSON => responseJSON.text());
	}
};

export default {
	name: 'Target Photo',
	domain: 'targetphoto.com',
	maxCoupons: 2, // site is slow
	isSinglePageApp: true,
	Driver,
};
