import MagentoTemplate from './_base';
import { completionActions } from '../../../utils';

const BASE_URL = 'https://www.h2oplus.com/checkout';
const CART_URL = `${BASE_URL}/cart`;
const IS_ON_CART = window.location.href.startsWith(CART_URL);

/** Behavior description:
+ Existing code: yes 
+ Remove code: yes 
+ Stackable:  no
+ Free gift support: yes
+ Free shipping: no
+ isValid implemented: no
+ Pages, where extension works: cart, checkout
+ Is driver SPA: yes
+ Gather codes on site pages: no
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.APPLIED_CODES_SELECTOR = '#coupon_code, #discount-code';
		this.TOTAL_SELECTOR = '.grand .price, .grand.totals .price';

		this.ITEM_CONTAINER_SELECTOR = IS_ON_CART ? '#shopping-cart-table tbody' : '.product-image-container';

		this.NEED_REMOVE_CODE = false;

		this.totalRegExp = {
			regexObj: /grand_total":"(\d+\.\d+)/,
			groupNumber: 1,
		};
		this.itemsCountRegExp = {
			regexObj: /items_count":"(\d+)/,
			groupNumber: 1,
		};

		this.afterCodeCheck = () => {
			// Clear this cookie to remove 'applied code' messages
			document.cookie = 'mage-messages=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
		};
	}

	checkCondition() {
		const totalValue = document.querySelector(this.TOTAL_SELECTOR);
		const correctPage = window.location.href.startsWith(BASE_URL);
		const currentState = totalValue && correctPage;
		return currentState;
	}

	beforeApplyCodes() {
		// to remove error messages on cart page
		const iframeElem = document.createElement('iframe');
		iframeElem.src = CART_URL;
		Object.assign(iframeElem.style, { display: 'none' });

		document.documentElement.appendChild(iframeElem);
	}
};

export default {
	name: 'H2O+',
	domain: 'h2oplus.com',
	completionAction: completionActions.revisit,
	Driver,
	isSinglePageApp: true,
};
