import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { format, log, completionActions } from '../../../utils';
import { sessionTimer } from '../../lib';

const { toFloat, elemToFloat } = format;

const BASE_URL = 'https://www.ldproducts.com';
const CART_URL = `${BASE_URL}/checkout/cart`;
const CHECKOUT_URL = `${BASE_URL}/checkout/onepage`;

/**
 * Driver behavior description:
 -    Existing code: yes
 -    Remove code: yes
 -    Stackable: no
 -    Free gift support: no
 -    isValid implemented: yes
 Optional:
 -	  maxCoupons: 7, // site can be is very slow
 -	  sessionTimer 15 min
 -    Is driver SPA and add the condition to show popup: no
 -	Can run testing on Cart and Checkout(all steps except shipping address) pages
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.grand-total .price';

		this.APPLIED_CODES_SELECTOR = '.code.ng-binding, .discount-coupon-code';

		this.INPUT_SELECTOR = '#coupon_code';
		this.APPLY_BUTTON_SELECTOR = '#btn-apply-coupon';
		this.SHIPPING_SECTION = '#opc-shipping';
		this.ITEMS_IN_CART = '#item_in_cart';
	}

	checkCondition() {
		const { href } = window.location;
		const cartURL = href.startsWith(CART_URL);
		const checkoutURL =
			href.startsWith(CHECKOUT_URL) &&
			!(
				document.querySelector(this.SHIPPING_SECTION) &&
				document.querySelector(this.SHIPPING_SECTION).classList.contains('active')
			);
		const emptyCart = Boolean(
			document.querySelector(this.ITEMS_IN_CART) &&
				document.querySelector(this.ITEMS_IN_CART).innerText === '0 items',
		);

		return (checkoutURL || cartURL) && !emptyCart;
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();
		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	getExistingCodes() {
		const codes = [document.querySelector(this.APPLIED_CODES_SELECTOR)]
			.filter(Boolean)
			.map(element => element.innerText.trim());
		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes(codes) {
		return this._removeCode(codes[0])
			.then(() => this._getOrderSummary())
			.then(responseJSON => this._parseTotal(responseJSON));
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code =>
			this._applyCode(code).then(response => {
				const result = { code, finalPrice: originalPrice, finalDiscount: 0, isValid: true };
				if (response.error) {
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}
				return this._getOrderSummary().then(responseJSON => {
					result.finalPrice = this._parseTotal(responseJSON);
					result.finalDiscount = originalPrice - result.finalPrice;

					return this._removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			}),
		);
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	_applyCode(code) {
		const bodyParams = {
			coupon_code: code,
			async: true,
		};
		return this._manageCode(`${CART_URL}/couponPost`, bodyParams);
	}

	_removeCode(code) {
		const bodyParams = {
			amcoupon_code_cancel: code,
			async: true,
		};
		return this._manageCode(`${BASE_URL}/amcoupons/checkout/cancelCoupon`, bodyParams);
	}

	_getOrderSummary() {
		return fetch(`${CART_URL}/retrieveCartTotals`, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.json());
	}

	_manageCode(requestURL, bodyParams) {
		return fetch(requestURL, {
			method: 'POST',
			body: JSON.stringify(bodyParams),
			credentials: 'include',
			headers: {
				accept: 'application/json, text/plain, */*',
				'content-type': 'application/json;charset=UTF-8',
			},
		}).then(response => response.json());
	}

	_parseTotal(data) {
		if (data) {
			return toFloat(data.grandtotal);
		}
		return elemToFloat(document.querySelector(this.TOTAL_SELECTOR));
	}
};

export default {
	name: 'LDProducts',
	domain: 'ldproducts.com',
	maxCoupons: 7, // site can be is very slow
	Driver,
	completionAction: completionActions.revisit,
	isSinglePageApp: true,
};
