import Promise from 'bluebird';
import bus from '../../bus';
import MagentoTemplate from './_base';
import { request, requestTypes, DOM, format } from '../../../utils';
import { sessionTimer, handleDriverError } from '../../lib';

const { extractData } = DOM;
const { toFloat } = format;

const BASE_URL = 'https://shop.harborfreight.com';
const CART_URL = `${BASE_URL}/checkout/cart`;

/** Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: no
+ Codes can be replaced by another code: no
+ Pages, where extension works: cart
+ Is driver SPA no
+ Gather codes on site pages: yes, from https://www.harborfreight.com/savings_coupons.html by the item name
+ sessionTimer: 15min
+ maxCoupons: 10
Special caase:
+ If there are more than 1 item in the Cart codes can be stacked.
  if there is only one item in the Cart codes replace each other (worse code cannot apply better code).
  It is so much strange behavior, so during stacking if two codes give the same discount driver shows as the best code first code,
  which gives discount (extension behavior).
  But on the page will be applied the last code, because it replaces previous code (merchant behavior).
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;

		this.APPLIED_CODES_SELECTOR = '.cart_item_unit_discount_text span';
		this.TOTAL_SELECTOR = '.GrandTotalDollars .price';

		this.APPLIED_CODES_REGEX = /Coupon Applied (\d*)/;

		this.STACKABLE = true;

		request.setType(requestTypes.xhr);

		sessionTimer.initialize();
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)]
			.map(element => element.innerText)
			.map(element => element.match(this.APPLIED_CODES_REGEX)[1]);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		this.ORIGINAL_PRICE = originalPrice;
		return super.checkCodes(codes, originalPrice);
	}

	stackCodes(validationInfo) {
		const { discounts, originalPrice, originalItemsCount } = validationInfo;

		discounts.sort((a, b) => {
			if (b.finalDiscount - a.finalDiscount === 0) {
				return b.freeGifts - a.freeGifts;
			}
			return b.finalDiscount - a.finalDiscount;
		});

		let currentPrice = originalPrice;
		let currentDiscount = 0;
		let currentFreeGiftsCount = 0;
		const successfulCodes = [];

		return Promise.each(discounts, discount =>
			this._applyCode(discount.code, this._getApplyRequestData()).then(responseString => {
				const finalPrice = this._parseTotal(responseString);
				const finalDiscount = originalPrice - finalPrice;
				const freeGifts = this._parseItemsCount(responseString) - originalItemsCount;

				bus.$emit('code-stacked', {
					code: discount.code,
					finalPrice,
					finalDiscount,
					freeGifts,
				});

				if (finalPrice < currentPrice || (finalPrice === currentPrice && freeGifts > currentFreeGiftsCount)) {
					successfulCodes.push(discount.code);
					currentDiscount = finalDiscount;
					currentPrice = finalPrice;
					currentFreeGiftsCount = freeGifts;
				}
				return Promise.resolve();
			}),
		).then(() =>
			bus.$emit('stackable-complete', {
				codes: successfulCodes,
				finalPrice: currentPrice,
				finalDiscount: currentDiscount,
				freeGifts: currentFreeGiftsCount,
			}),
		);
	}

	_removeCode(code) {
		return request.get(`${BASE_URL}/hftcoupons/index/remove?coupon_code=${code}`).then(response => response.text());
	}

	_manageCode(requestURL, bodyObj) {
		return request
			.post(requestURL, bodyObj, {
				headers: this.MANAGE_CODE_HEADERS,
			})
			.then(response => response.text());
	}

	_getUpdatedCart() {
		return request.get(this.CART_URL).then(response => response.text());
	}

	_parseTotal(htmlString) {
		try {
			return toFloat(extractData(this.TOTAL_SELECTOR, htmlString));
		} catch (e) {
			handleDriverError(e, { htmlString });
			return this.ORIGINAL_PRICE;
		}
	}
};

export default {
	name: 'Harbor Freight Tools',
	domain: 'harborfreight.com',
	Driver,
	maxCoupons: 10,
	stackable: true,
	showDifferentDiscountMessage: true,
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) && document.querySelector('#cart_wrapper');
	},
};
