import MagentoTemplate from './_base';

import { sessionTimer } from '../../lib';

/** Behavior description:
Existing code: yes
Remove code: yes
Stackable: no
Free gift support: no
Free shipping: no
'isValid' implemented: no
Codes can be replaced by another code: no
Manually apply code: no (so on Checkout we lose information, without redirect to other page, can avoid Cart)
Is driver SPA and add the condition to show popup: yes
Pages, where extension works: `Cart` and `Checkout`(Payment step)
sessionTimer: 10min
Gather codes on site pages: no
Credentials (josephhhh@gmail.com, 12345qwertyQ)
*/

const SITE_URL = 'https://www.7forallmankind.com';
const URL = 'www.7forallmankind.com';
const CART_URL = `${SITE_URL}/checkout/cart`;

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;

		this.TOTAL_SELECTOR = '.grand .price';
		this.URL_REGEXP = new RegExp(`${window.location.protocol}//${URL}/checkout/cart`, 'gi');
		this.CODE_INPUT_SELECTOR = '#block-discount-heading';
		this.EMPTY_CART_SELECTOR = '.cart-empty';

		this.IS_ON_CART_PAGE = Boolean(window.location.href.match(this.URL_REGEXP));

		this.SHIPPING_REGEX = /"shipping_amount":"?([0-9.]+)"?/;
		this.TAX_REGEX = /"Tax","value":"?([0-9.]+)"?/;

		this.totalRegExp = {
			regexObj: /"grand_total":"?([0-9.]+)"?/,
			groupNumber: 1,
		};

		this.afterCodeCheck = () => {
			// Clear this cookie to remove 'applied code' messages
			document.cookie = 'mage-messages=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
		};

		this.MANAGE_CODE_HEADERS = {
			accept: 'text/html,application/xhtml+xml,application/xml;q=0.9,image/webp,image/apng,*/*;q=0.8',
			referer: 'https://www.7forallmankind.com/checkout/', // need for FF
		};
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 10,
		});
	}

	checkCondition() {
		this.IS_ON_CART_PAGE = Boolean(window.location.href.match(this.URL_REGEXP));
		this.ON_CHECKOUT_PAGE = window.location.href.startsWith(`${SITE_URL}/checkout`);
		const cartEmpty = document.querySelector(this.EMPTY_CART_SELECTOR);
		const isTotalEl = (document.querySelector(this.TOTAL_SELECTOR) || {}).offsetParent;
		const codeInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		return (this.IS_ON_CART_PAGE || this.ON_CHECKOUT_PAGE) && isTotalEl && codeInput && !cartEmpty;
	}

	beforeCheckCodes(originalPrice) {
		if (!originalPrice && this.IS_ON_CART_PAGE) {
			return this._manageCode().then(responseHTML => this._parseTotal(responseHTML));
		}
		return originalPrice;
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(response => {
			const tax = response.match(this.TAX_REGEX)[1];
			const shipping = response.match(this.SHIPPING_REGEX)[1];
			return this._parseTotal(response) - tax - shipping;
		});
	}

	_manageCode(requestURL, bodyObj) {
		return fetch(requestURL, {
			method: 'POST',
			credentials: 'include',
			headers: this.MANAGE_CODE_HEADERS,
			body: bodyObj,
			redirect: 'manual',
		}).then(() => this._getCartPage());
	}

	_getCartPage() {
		return fetch(window.location.href, {
			method: 'GET',
			credentials: 'include',
			headers: this.MANAGE_CODE_HEADERS,
		}).then(response => response.text());
	}
};

export default {
	name: '7 For all mankind',
	domain: '7forallmankind.com',
	Driver,
	isSinglePageApp: true,
	maxCoupons: 5, // site works slow
};
