import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { backgroundMessaging } from '../../lib';
import { format, DOM, log } from '../../../utils';

const { toFloat } = format;
const { extractElement } = DOM;

/* Behavior description:
	+ Existing code: yes
	+ Remove code: yes
	+ Stackable: no
	+ Free gift support: yes
	+ isValid implemented: yes
	+ Free shipping: no
	+ Is SPA: yes
	+ Gather code: yes (cart page)
====Special cases:====
	+ allow 2 times per 10 min (due to 429 error(to many requests))
	+ on checkout maxCoupons: 10 (site works slow), on Cart page:15
	+ 404 error if code is not valid
	+ Sometimes site shows shipping value in total on cart, sometimes no, so we can see incorrect original price
	on sites page
 */

const DOMAIN = 'cjponyparts.com';
const BASE_URL = `https://www.${DOMAIN}/checkout`;
const BASE_REQUEST_URL = `https://www.${DOMAIN}/rest/default/V1`;
const CART_URL = `${BASE_URL}/cart`;
const IS_ON_PAYMENT = window.location.href.startsWith(`${BASE_URL}/#payment`);

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.APPLIED_CODES_SELECTOR = '#coupon_code, #discount-code';
		this.TOTAL_SELECTOR = '.grand.totals .price';
		this.TAX_SELECTOR = '.totals-tax';
		this.SPINER_SELECTOR = '.loading-mask';
		this.EMPTY_CART_SELECTOR = '#shopping-cart-empty-message';
		this.MERCHANT_CODE_SELECTOR = '#header-banner-content';
		this.AUTH_BUTTON_SELECTOR = '.authorization-link > a, .authentication-wrapper';
		this.MERCHANT_CODE_REGEX = /code[\s:-]*(\w+)/i;
		this.ENTITY_ID_REG = /entity_id":"(\w+)/;
		this.ENTITY_ID = null;
		this.URL_STRING = null;
		this.IS_PAYMENT = null;
		this.DISABLE_TIME_MS = 10 * 60 * 1000; // 10 minutes
	}

	async checkCondition() {
		const { href } = window.location;
		let isNotMaxCodeTesting = true;
		const driverStorage = await backgroundMessaging.getDriverStorage(DOMAIN);
		if (driverStorage) {
			const dataExpired = Date.now() - driverStorage.lastTestTimestamp >= this.DISABLE_TIME_MS;
			if (dataExpired) {
				backgroundMessaging.deleteDriverStorage(DOMAIN);
			} else {
				isNotMaxCodeTesting = driverStorage.codeTestingCount < 2;
			}
		}
		const noSpiner = IS_ON_PAYMENT
			? document.querySelector(this.SPINER_SELECTOR) &&
			  document.querySelector(this.SPINER_SELECTOR).style.display === 'none'
			: true;
		const isCartHref = href.startsWith(`${BASE_URL}/#payment`) || href.startsWith(CART_URL);
		const isTotal = Boolean(document.querySelector(this.TOTAL_SELECTOR));
		const isInput = document.querySelector(this.APPLIED_CODES_SELECTOR);
		const isCartEmpty = document.querySelector(this.EMPTY_CART_SELECTOR);
		return isNotMaxCodeTesting && isCartHref && isTotal && noSpiner && isInput && !isCartEmpty;
	}

	getMerchantCodes() {
		return [...document.querySelectorAll(this.MERCHANT_CODE_SELECTOR)]
			.map(item => item.textContent.match(this.MERCHANT_CODE_REGEX))
			.filter(Boolean)
			.map(item => item[1]);
	}

	beforeTestCodes() {
		[, this.ENTITY_ID] = document.body.innerHTML.match(this.ENTITY_ID_REG);
		this.IS_NOT_LOGGED_IN =
			document.querySelector(this.AUTH_BUTTON_SELECTOR) &&
			document.querySelector(this.AUTH_BUTTON_SELECTOR).style.display !== 'none' &&
			document
				.querySelector(this.AUTH_BUTTON_SELECTOR)
				.textContent.trim()
				.toLowerCase()
				.includes('sign in');
		this.URL_STRING = this.IS_NOT_LOGGED_IN ? `guest-carts/${this.ENTITY_ID}` : 'carts/mine';
		this.IS_PAYMENT = window.location.href.startsWith(`${BASE_URL}/#payment`);
		return Promise.resolve();
	}

	getExistingCodes() {
		return Promise.resolve()
			.then(() => (this.IS_PAYMENT ? this._getCodeFromCart() : false))
			.then(html => {
				const codes = [extractElement(this.APPLIED_CODES_SELECTOR, html)]
					.map(element => element.defaultValue)
					.filter(Boolean);

				return {
					codes,
					codeCount: codes.length,
				};
			});
	}

	removeCodes() {
		return this._removeCode()
			.then(() => this._getPrice())
			.then(responseJSON => this._parseTotal(responseJSON));
	}

	beforeCheckCodes() {
		return this._parseTotal();
	}

	checkCodes(codes, originalPrice) {
		log('originalPrice: %s', originalPrice);

		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				isValid: true,
			};
			return this._applyCode(code).then(response => {
				if (response) {
					return this._getPrice().then(responseJSON => {
						result.finalPrice = this._parseTotal(responseJSON);
						result.finalDiscount = originalPrice - result.finalPrice;
						return bus.$emit('code-checked', result);
					});
				}
				result.isValid = false;
				return bus.$emit('code-checked', result);
			});
		});
	}

	async beforeApplyCodes() {
		const driverStorage = await backgroundMessaging.getDriverStorage(DOMAIN);
		const codeTestingCount = driverStorage ? driverStorage.codeTestingCount : 0;
		backgroundMessaging.setDriverStorage(DOMAIN, {
			codeTestingCount: codeTestingCount + 1,
			lastTestTimestamp: Date.now(),
		});
		document.cookie = 'mage-messages=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
		return Promise.resolve();
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	_removeCode() {
		return this._manageCode('DELETE');
	}

	_applyCode(code) {
		return this._manageCode('PUT', code);
	}

	_manageCode(method, code = '') {
		return fetch(`${BASE_REQUEST_URL}/${this.URL_STRING}/coupons/${code}`, {
			method,
			credentials: 'include',
			headers: {
				'content-type': 'application/json',
				'x-requested-with': 'XMLHttpRequest',
			},
		}).then(response => (response.status !== 404 ? response.json() : false));
	}

	_getPrice() {
		return fetch(`${BASE_REQUEST_URL}/${this.URL_STRING}/payment-information`, {
			method: 'GET',
			credentials: 'include',
			headers: {
				'content-type': 'application/json',
				'x-requested-with': 'XMLHttpRequest',
			},
		}).then(response => response.json());
	}

	_parseTotal(data) {
		if (data) {
			return toFloat(data.totals.base_grand_total);
		}
		return this._getPrice().then(res => toFloat(res.totals.base_grand_total));
	}

	_getCodeFromCart() {
		return fetch(CART_URL, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	name: 'CJ Pony Parts',
	domain: 'cjponyparts.com',
	Driver,
	isSinglePageApp: true,
	showDifferentDiscountMessage: true,
	maxCoupons: IS_ON_PAYMENT ? 10 : 15, // works slow on checkout
};
