import MagentoTemplate from './_base';
import { completionActions, format, DOM } from '../../../utils';

const { elemToFloat } = format;
const { extractElement } = DOM;

const CART_URL = 'https://www.alternativeapparel.com/checkout/cart';

/* Driver behavior description:
    Existing code: yes
    Remove code: yes
    Stackable: yes
    Free gift support: no
    isValid implemented: no
    Manually apply code: no
    Active page: Cart
Optional:
    Show message about free shipping on the cart page (without discount info): no
    Is driver SPA and add the condition to show popup: no
    Gather codes on site pages: no */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.TOTAL_SELECTOR = '.-borderless:last-child .price';
		this.TOTAL_EXCL_TAX_SELECTOR = '.-borderless:nth-last-of-type(2) .price';
		this.TOTAL_INCL_TAX_SELECTOR = '.-borderless:nth-last-of-type(1) .price';

		this.SUBTOTAL_SELECTOR = '.wsc__cart-price-subtotal .price';

		this.REMOVE_COUPON_FORM_SELECTOR = '[id^="discount-coupon-cancel-form"]';
		this.APPLIED_CODES_SELECTOR = '.discount-coupon__applied .discount-coupon__code';

		this.REMOVE_CODE_PARAM_NAME = 'amcoupon_code_cancel';
		this.REMOVE_REQUEST_EXTRA_VALUES = {};

		this.STACKABLE = true;
	}

	getExistingCodes() {
		const couponElementsArray = document.querySelectorAll(this.APPLIED_CODES_SELECTOR);
		const codes = [...couponElementsArray].map(el => el.innerText);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	beforeCheckCodes() {
		return this._getUpdatedCart().then(response => {
			const subtotal = elemToFloat(extractElement(this.SUBTOTAL_SELECTOR, response));
			const total = elemToFloat(extractElement(this.TOTAL_INCL_TAX_SELECTOR, response));
			// can't get it in another way
			this.TAX_TOTAL_VALUE = total - subtotal;
		});
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(
			response => elemToFloat(extractElement(this.TOTAL_INCL_TAX_SELECTOR, response)) - this.TAX_TOTAL_VALUE,
		);
	}
};

export default {
	name: 'Alternative Apparel',
	domain: 'alternativeapparel.com',
	completionAction: completionActions.revisit,
	maxCoupons: 5, // block
	stackable: true,
	Driver,
	isOnCartUrl(href) {
		return href.startsWith(CART_URL);
	},
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
};
