import MagentoTemplate from './_base';
import { extractElement } from '../../../utils/DOM';

/** Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: no
+ Codes can be replaced by another code: yes
+ Pages, where extension works:
 - cart
+ Is driver SPA: no
+ Gather codes on site pages: no

We don't support the checkout page, because all user data will be cleared and a user will be redirected on the first step of checkout.
We can't avoid the cart page. */

const CART_URL = 'https://www.vivaterra.com/checkout/cart';

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;

		this.APPLIED_CODES_SELECTOR = '[name="coupon_code"]';
		this.TOTAL_SELECTOR = '.price-total .price';
		this.SHIPPING_SELECTOR = '#shopping-cart-totals-table tbody';
		this.SHIPPING_REGEX = /(shipping=?).+\$([0-9.,]+)/is;
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(responseHTML => {
			const total = this._parseTotal(responseHTML);
			const shipping = extractElement(this.SHIPPING_SELECTOR, responseHTML);
			const calculatedShipping =
				shipping && shipping.textContent.match(this.SHIPPING_REGEX)
					? shipping.textContent.match(this.SHIPPING_REGEX)[2]
					: 0;
			return total - parseFloat(calculatedShipping);
		});
	}
};

export default {
	name: 'Viva Terra',
	domain: 'vivaterra.com',
	Driver,
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_URL);
	},
};
