import MagentoTemplate from './_base';
import { DOM } from '../../../utils';

const { extractElement } = DOM;

const BASE_URL = 'https://www.vitalitymedical.com';

/** Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: yes
+ Codes can be replaced by another code: yes
+ Manually apply code: no
+
+ Pages, where extension works:
    - cart
+ Is driver SPA: yes
+ Gather codes on site pages: yes ('https://www.vitalitymedical.com')
*/

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = `${BASE_URL}/checkout/cart/`;
		this.APPLIED_CODES_SELECTOR = '#coupon_code, #coupon_code_onestepcheckout';
		this.TOTAL_SELECTOR = '#shopping-cart-totals-table strong .price, #checkout-review-table strong .price';
		this.EMPTY_CART_SELECTOR = '.cart-empty';
		this.CODE_INPUT_SELECTOR = '#coupon_code';
		this.BANNER_SELECTOR = '.ambanners';
		this.MERCHANT_CODE_REGEX = /use code:? (\w+)/i;
		this.NEED_REMOVE_CODE = false;
	}

	checkCondition() {
		const isOnCartUrl = window.location.href.includes(this.CART_URL);
		const codeInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		const emptyCart = document.querySelector(this.EMPTY_CART_SELECTOR);
		return isOnCartUrl && codeInput && !emptyCart;
	}

	getMerchantCodes() {
		return fetch(BASE_URL)
			.then(response => response.text())
			.then(responseHTML => {
				return [...extractElement(this.BANNER_SELECTOR, responseHTML, true)]
					.map(element => element.textContent.match(this.MERCHANT_CODE_REGEX))
					.filter(Boolean)
					.map(matched => matched[1]);
			});
	}
};

export default {
	name: 'Vitality Medical',
	domain: 'vitalitymedical.com',
	Driver,
	isSinglePageApp: true,
};
