import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { DOM } from '../../../utils';

const { extractElement } = DOM;

const BASE_URL = 'https://www.papyrusonline.com';
const CART_URL = `${BASE_URL}/checkout/cart`;
const CHECKOUT_URL = `${BASE_URL}/onestepcheckout`;
const IS_CART = window.location.href.startsWith(CART_URL);
const TOTAL_SELECTOR = '#shopping-cart-totals-table strong .price, .onestepcheckout-totals strong .price';

/** Behavior description
+ Existing code: yes 
+ Remove code: yes, but not always
+ Stackable:  no
+ Free gift support:  no
+ Free shipping:  no
+ isValid implemented: yes 
+ Codes can be replaced by another code:  yes
+ Pages, where extension works:
   - cart
   - checkout
+ Is driver SPA no
+ Gather codes on site pages: no

can't remove code on cart page if shipping method is selected
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.TOTAL_SELECTOR = TOTAL_SELECTOR; // On some websites this may differ.
		this.INVALID_CODE_SELECTOR = '.error-msg';
	}

	removeCodes(codes) {
		return Promise.each(codes, code => this._removeCode(code, this._getRemoveRequestData(this.cartPageHTML)))
			.then(() => (IS_CART ? this._getUpdatedCart() : this._getUpdatedCheckout())) // Promise.each() return array of applied codes.
			.then(responseString => this._parseTotal(responseString));
	}

	// check if the valid code was found
	checkCodes(codes, originalPrice = this._parseTotal()) {
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				isValid: false,
			};
			return this._applyCode(code, this._getApplyRequestData(this.cartPageHTML)).then(responseString => {
				if (
					extractElement(this.INVALID_CODE_SELECTOR, responseString) ||
					(extractElement(this.REMOVE_BTN_SELECTOR, responseString) &&
						extractElement(this.REMOVE_BTN_SELECTOR, responseString).style.display === 'none')
				) {
					return bus.$emit('code-checked', result);
				}
				const promise = IS_CART ? Promise.resolve(responseString) : this._getUpdatedCheckout();
				return promise.then(response => {
					result.finalPrice = this._parseTotal(response);
					result.finalDiscount = originalPrice - result.finalPrice;
					result.isValid = true;
					return bus.$emit('code-checked', result);
				});
			});
		});
	}

	_getUpdatedCart() {
		return fetch(this.CART_URL, {
			credentials: 'include',
		}).then(response => response.text());
	}

	_getUpdatedCheckout() {
		return fetch(CHECKOUT_URL, {
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	name: 'Papyrus',
	domain: 'papyrusonline.com',
	Driver,
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
	isOnCartUrl(href) {
		return (href.startsWith(CART_URL) || href.startsWith(CHECKOUT_URL)) && document.querySelector(TOTAL_SELECTOR);
	},
};
