import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { format, log } from '../../../utils';

const { toFloat, elemToFloat } = format;

const BASE_URL = 'https://www.ninewest.com';
const CHECKOUT_URL = `${BASE_URL}/checkout`;
const CART_URL = `${CHECKOUT_URL}/cart`;
const IS_CHECKOUT_PAGE = window.location.href.startsWith(CHECKOUT_URL) && !window.location.href.includes('cart');
const IS_ON_CART = window.location.href.startsWith(CART_URL);
const BASE_REQUEST_URL = `${BASE_URL}/rest/ninewest/V1`;

/** Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: Cart, Checkout
+ Is driver SPA: yes
+ Gather codes on site pages: no
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;

		this.APPLIED_CODES_SELECTOR = '#discount_code';
		this.TOTAL_SELECTOR = IS_CHECKOUT_PAGE ? '.grand.incl .price' : '.grand.excl .price';
		this.CODE_INPUT_SELECTOR = '#coupon_code, #discount-code';
		this.SHIPPING_ELEM_SELECTOR = '.shipping .price';

		this.ENTITY_ID_SELECTOR = new RegExp(/"entity_id":"(.*?)"/);

		this.MANAGE_CODE_HEADERS = {
			'Content-Type': 'application/json',
			'x-requested-with': 'XMLHttpRequest',
		};
		this.requestData = null;
		this.updatedUrl = null;
	}

	checkCondition() {
		const totalElement = document.querySelector(this.TOTAL_SELECTOR);
		const codeInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		const currentState = totalElement && codeInput;
		return currentState;
	}

	beforeTestCodes() {
		const authorization = document.querySelector('.authorization-link [data-post]');

		// there is no shipping value in the response when you change it on the cart page
		const shippElem = IS_ON_CART ? document.querySelector(this.SHIPPING_ELEM_SELECTOR) : null;
		this.shippValue = shippElem ? toFloat(shippElem.innerText) : 0;

		const entityId = [...document.querySelectorAll('script')]
			.find(element => element.text.match(this.ENTITY_ID_SELECTOR))
			.text.match(this.ENTITY_ID_SELECTOR)[1];
		if (!authorization) {
			this.requestData = {
				action: `${BASE_REQUEST_URL}/guest-carts/${entityId}/coupons/`,
			};
			this.updatedUrl = `${BASE_REQUEST_URL}/guest-carts/${entityId}/payment-information`;
		} else {
			this.requestData = {
				action: `${BASE_REQUEST_URL}/carts/mine/coupons/`,
			};
			this.updatedUrl = `${BASE_REQUEST_URL}/carts/mine/payment-information`;
		}
		return Promise.resolve();
	}

	getExistingCodes() {
		return this._getUpdatedCart().then(responseJSON => {
			const codes = [responseJSON.totals.coupon_code].filter(Boolean);
			return {
				codes,
				codeCount: codes.length,
			};
		});
	}

	removeCodes() {
		return this._removeCode()
			.then(() => this._getUpdatedCart())
			.then(responseString => this._parseTotal(responseString));
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code =>
			this._applyCode(code)
				.then(responseJSON => (responseJSON && responseJSON.message ? null : this._getUpdatedCart()))
				.then(responseJSON => {
					if (!responseJSON || responseJSON.status === 503) {
						return bus.$emit('code-checked', {
							code,
							finalPrice: originalPrice,
							finalDiscount: 0,
							isValid: false,
						});
					}
					const finalPrice = this._parseTotal(responseJSON);
					const finalDiscount = originalPrice - finalPrice;
					return this._removeCode().then(() =>
						bus.$emit('code-checked', {
							code,
							finalPrice,
							finalDiscount,
							isValid: true,
						}),
					);
				}),
		);
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(responseJSON => {
			const taxValue = toFloat(responseJSON.totals.base_tax_amount);
			const shippingValue = toFloat(responseJSON.totals.base_shipping_amount);
			const totalValue = this._parseTotal(responseJSON);
			const theLastValue = totalValue - shippingValue - taxValue;
			return shippingValue === 0 ? theLastValue - this.shippValue : theLastValue;
		});
	}

	_applyCode(code) {
		return this._manageCode(`${this.requestData.action}${code}`, 'PUT').then(responseJSON => responseJSON.json());
	}

	_removeCode() {
		return this._manageCode(this.requestData.action, 'DELETE');
	}

	_manageCode(requestURL, method) {
		return fetch(requestURL, {
			method,
			credentials: 'include',
			headers: this.MANAGE_CODE_HEADERS,
		});
	}

	_parseTotal(data) {
		if (data) {
			// cart doesn't reload with changing shipping, but we receive this value after checkout
			const total = toFloat(data.totals.base_grand_total);
			return toFloat(data.totals.base_shipping_amount) === 0 ? total + this.shippValue : total;
		}
		return elemToFloat(document.querySelector(this.TOTAL_SELECTOR));
	}

	_getUpdatedCart() {
		return fetch(this.updatedUrl, {
			method: 'GET',
			credentials: 'include',
			headers: this.MANAGE_CODE_HEADERS,
		}).then(responseJSON => responseJSON.json());
	}
};

export default {
	name: 'Nine West',
	domain: 'ninewest.com',
	isSinglePageApp: true,
	Driver,
};
