import MagentoTemplate from './_base';

/*
Behavior description:
- can get existing codes;
- can apply code;
- can remove codes;
- can't apply multiple codes (not stackable);
- code testing may be run on 'Cart' page;
======== Special case ========
We create iframe with current page after codes testing
to avoid showing error messages after page reload;
*/

const CART_URL = 'https://www.eastwood.com/checkout/cart';

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;

		this.APPLIED_CODES_SELECTOR = '#coupon_code';
		this.TOTAL_SELECTOR = '#totals_grand_total .price';
	}

	applyCodes(codes) {
		return this._applyCode(codes[0], this._getApplyRequestData(this.cartPageHTML)).then(() => this._createIframe());
	}

	_createIframe() {
		const iframe = document.createElement('iframe');
		iframe.setAttribute('src', `${window.location.href}?demo=1`);
		iframe.setAttribute('style', 'display: none;');
		document.body.appendChild(iframe);
	}
};

export default {
	name: 'Eastwood',
	domain: 'eastwood.com',
	Driver,
	maxCoupons: 8, // site is little slow
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_URL);
	},
};
