import MagentoTemplate from './_base';
import { sessionTimer } from '../../lib';

/** Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: yes
+ Codes can be replaced by another code: yes
+ Manually apply code: no
+ Pages, where extension works: Cart, Checkout(for logged in user - all steps, for not logged - only first step, due to redirection and clear data)
+ Is driver SPA: no
+ Gather codes on site pages: no
+ credentials: joseph@gmail.com, 12345qwerty
+ 6ms - testing 1 valid code = maxCoupons = 10
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		const BASE_URL = 'https://www.worx.com';
		this.CART_URL = `${BASE_URL}/checkout/cart`;

		this.TOTAL_SELECTOR = '#shopping-cart-totals-table tfoot .price, #checkout-totals tr:last-child .price';
		this.NEED_REMOVE_CODE = false;
		this.VALID_CHECKOUT_PAGE = false;
		this.EMPTY_CART_SELECTOR = '.cart-empty';
		this.BILLING_SELECTOR = '.billing_section.active';
		this.NOT_LOGIN_SELECTOR = '#onestepcheckout_login_link';
		this.CODE_INPUT_SELECTOR = '#discount-coupon-form, .cart-forms .discount-form';
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();
		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	checkCondition() {
		const { href } = window.location;
		const emptyCart = document.querySelector(this.EMPTY_CART_SELECTOR);
		const isOnCartURL = href.startsWith(this.CART_URL);
		const isOnShipping = document.querySelector(this.BILLING_SELECTOR);
		const isNotLogged = document.querySelector(this.NOT_LOGIN_SELECTOR);
		const isCodeInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		const isOnCheckout = isNotLogged ? isOnShipping : true;
		return !emptyCart && (isOnCartURL || isOnCheckout) && isCodeInput;
	}
};

export default {
	name: 'Worx',
	domain: 'worx.com',
	maxCoupons: 10,
	isSinglePageApp: true,
	Driver,
};
