import MagentoTemplate from './_base';
import { DOM, format } from '../../../utils';

const { extractElement } = DOM;
const { toFloat } = format;

const CART_URL = 'https://www.partsgeek.com/cart';

/* Behavior description:
+ Existing code: yes
+ Remove code: yes (via applying an invalid one)
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: no
+ Manually apply code: no
+ Pages, where extension works: Cart
+ Is driver SPA: no
+ Gather codes on site pages: no
*/

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;

		this.APPLIED_CODES_SELECTOR = '.cart-coupon-text';
		this.APPLIED_CODE_REGEX = /Coupon\s*(\w+)\s*applied/;
		this.TOTAL_SELECTOR = '.cart-total-value';
		this.PRICES_SELECTOR = '.cart-totals-heading';

		this.APPLY_COUPON_FORM_SELECTOR = '.cart-coupon-form';
	}

	beforeTestCodes() {
		this.removeRequestData = this._getApplyRequestData();
		this.removeRequestData.params.set(this.REMOVE_CODE_PARAM_NAME, 'SAVE0');
		this.removeRequestData.params.delete('remove'); // with this key requests aren't accepted
	}

	getExistingCodes() {
		const appliedCodesMsg = document.querySelector(this.APPLIED_CODES_SELECTOR).textContent;
		const codeMatch = appliedCodesMsg.match(this.APPLIED_CODE_REGEX);
		const codes = codeMatch ? [codeMatch[1]] : [];

		return {
			codes,
			codeCount: codes.length,
		};
	}

	getPreTaxShippingTotal() {
		return this._getCartPage().then(responseHTML => {
			const shippingAndHandlingEl = this._findElement('Shipping and Handling', responseHTML);
			const shippingAndHandling = shippingAndHandlingEl ? toFloat(shippingAndHandlingEl) || 0 : 0;
			const shippingInsuranceEl = this._findElement('Shipping Insurance', responseHTML);
			const shippingInsurance = shippingInsuranceEl ? toFloat(shippingInsuranceEl) || 0 : 0;

			return this._parseTotal(responseHTML) - shippingAndHandling - shippingInsurance;
		});
	}

	_applyCode(code, requestData) {
		// Safari does not correctly extract form values
		requestData.params.set(this.APPLY_CODE_PARAM_NAME, code);
		requestData.params.delete('remove'); // with this key requests aren't accepted
		return this._manageCode(requestData.action || CART_URL, requestData.params);
	}

	_removeCode() {
		// Safari does not correctly extract form values
		return this._manageCode(this.removeRequestData.action || CART_URL, this.removeRequestData.params);
	}

	_findElement(targetString, response) {
		const targetEl = [...extractElement(this.PRICES_SELECTOR, response, true)].find(el =>
			el.textContent.includes(targetString),
		);
		return targetEl ? targetEl.nextElementSibling.textContent : null;
	}

	_getCartPage(url = window.location.href) {
		return fetch(url, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	name: 'Parts Geek',
	domain: 'partsgeek.com',
	Driver,
	isCartEmpty() {
		const cartContent = document.querySelector('#content > table:first-child');
		return cartContent && cartContent.offsetParent;
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_URL);
	},
};
