import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { DOM, log, completionActions } from '../../../utils';

const { setValue, extractElement } = DOM;

const BASE_URL = 'https://www.vionicshoes.com';
const CART_URL = `${BASE_URL}/checkout/cart`;
const CHECKOUT_URL = `${BASE_URL}/checkout/onepage`;

const isOnCheckout = () =>
	window.location.href.includes('onepage') && !document.querySelector('#opc-login.section.allow.active');

/** Behavior description:
 * - can get existing codes
 * - can remove codes
 * - valid code can be replaced with another valid code
 *
 * ======== Special case ========
 * we can't do requests on cart from checkout page (the error will be appear after that)
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;

		this.APPLIED_CODES_SELECTOR = '#coupon_code, #sidebarcoupon_code';
		this.TOTAL_SELECTOR = '#shopping-cart-totals-table strong .price, #checkout-sidebar-table strong .price';

		this.APPLY_BUTTON_SELECTOR = '#coupon-apply';

		this.INACTIVE_SIDE_BAR_SELECTOR = '.sidebar_coupon_wrapper.inactive';
		this.EMPTY_CART_SELECTOR = '.cart-empty';

		this.CHECKOUT_APPLY_URL = `${BASE_URL}/customcheckout/coupon/applyCoupon/`;
		this.CHECKOUT_REMOVE_URL = `${BASE_URL}/customcheckout/coupon/cancelCoupon/`;

		this.SUCCESS_MESSAGE_SELECTOR = '.success-msg';
	}

	watchForCartUrl() {
		const previousState = {
			cartActive: false,
			completionAction: false,
		};
		setInterval(() => {
			const inactiveSideBar = document.querySelector(this.INACTIVE_SIDE_BAR_SELECTOR);
			const emptyCartElement = document.querySelector(this.EMPTY_CART_SELECTOR);

			const cartActiveState =
				(window.location.href.startsWith(CART_URL) && !emptyCartElement) ||
				(window.location.href.startsWith(CHECKOUT_URL) && !inactiveSideBar);

			const completionActionState = isOnCheckout();

			if (cartActiveState !== previousState.cartActive) {
				bus.$emit(cartActiveState ? 'cart-active' : 'cart-inactive');
			}
			if (completionActionState !== previousState.completionAction) {
				bus.$emit('change-completion-action', completionActions[completionActionState ? 'none' : 'reload']);
			}
			previousState.cartActive = cartActiveState;
			previousState.completionAction = completionActionState;
		}, 1000);
	}

	beforeTestCodes() {
		if (isOnCheckout()) {
			// an error appears when we do request on cart page from checkout
			return Promise.resolve();
		}
		return super.beforeTestCodes();
	}

	getExistingCodes() {
		const codes = [document.querySelector(this.APPLIED_CODES_SELECTOR)]
			.map(element => (isOnCheckout() ? element.value : element.getAttribute('value')))
			.filter(Boolean);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log('originalPrice: %s', originalPrice);

		return Promise.each(codes, code =>
			this._applyCode(code, this._getApplyRequestData(this.cartPageHTML)).then(response => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					isValid: false,
				};
				if (isOnCheckout()) {
					if (typeof response === 'object' && response.coupon_applied) {
						result.isValid = true;
						return this._getUpdatedCart().then(responseString => {
							result.finalPrice = this._parseTotal(responseString);
							result.finalDiscount = originalPrice - result.finalPrice;
							return bus.$emit('code-checked', result);
						});
					}
					return bus.$emit('code-checked', result);
				}
				result.finalPrice = this._parseTotal(response);
				result.finalDiscount = originalPrice - result.finalPrice;
				if (this._codeAppliedCheck(response)) {
					result.isValid = true;
				}
				return bus.$emit('code-checked', result);
			}),
		);
	}

	applyCodes(codes) {
		return isOnCheckout() ? this._manuallyApplyCode(codes[0]) : super.applyCodes(codes);
	}

	_manageCode(requestURL, bodyObj) {
		return fetch(requestURL, {
			method: 'POST',
			credentials: 'include',
			headers: this.MANAGE_CODE_HEADERS,
			body: bodyObj,
		})
			.then(response => response.text())
			.then(response => {
				try {
					return JSON.parse(response);
				} catch (e) {
					return response;
				}
			});
	}

	_manuallyApplyCode(code) {
		setValue(document.querySelector(this.APPLIED_CODES_SELECTOR), code);
		document.querySelector(this.APPLY_BUTTON_SELECTOR).click();
		return Promise.resolve();
	}

	_getUpdatedCart() {
		return fetch(isOnCheckout() ? `${BASE_URL}/checkout/onepage/progress/?prevStep=sidebar` : this.CART_URL, {
			credentials: 'include',
		}).then(response => response.text());
	}

	_getApplyRequestData(htmlString) {
		if (isOnCheckout() && !this.applyRequestData) {
			this.applyRequestData = {
				action: this.CHECKOUT_APPLY_URL,
				params: new URLSearchParams(),
			};
			return this.applyRequestData;
		}
		return super._getApplyRequestData(htmlString);
	}

	_getRemoveRequestData(htmlString) {
		if (isOnCheckout() && !this.removeRequestData) {
			this.removeRequestData = {
				action: this.CHECKOUT_REMOVE_URL,
				params: new URLSearchParams({}),
			};
			return this.removeRequestData;
		}
		return super._getRemoveRequestData(htmlString);
	}

	_codeAppliedCheck(data) {
		return extractElement(this.SUCCESS_MESSAGE_SELECTOR, data);
	}
};

export default {
	name: 'Vionic Shoes',
	domain: 'vionicshoes.com',
	Driver,
	isSinglePageApp: true,
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) || href.startsWith(CHECKOUT_URL);
	},
};
