import MagentoTemplate from './_base';
import { DOM } from '../../../utils';

const { extractData } = DOM;

const CART_URL = 'https://www.dailysale.com/checkout/cart';
const STACKABLE = false;

/* Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: no
+ Codes can be replaced by another code: yes
+ Manually apply code: no
+ Pages, where extension works: Cart, Checkout
+ Is driver SPA: yes
+ Gather codes on site pages: no
 */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;

		this.APPLIED_CODES_SELECTOR = '#coupon_code, #discount-code';
		this.TOTAL_SELECTOR = '.grand.totals strong .price';
		this.FORM_KEY_SELECTOR = '[name="form_key"]';

		this.STACKABLE = STACKABLE;
		this.NEED_REMOVE_CODE = false;

		this.APPLY_REQUEST_EXTRA_VALUES = {
			remove: 0,
		};

		this.REMOVE_REQUEST_EXTRA_VALUES = {
			remove: 1,
		};
		this.totalRegExp = {
			regexObj: /"grand_total":"(\d+,*\d+.\d+)/,
			groupNumber: 1,
		};

		this.afterCodeCheck = () => {
			// Clear this cookie to remove 'applied code' messages
			document.cookie = 'mage-messages=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
		};

		this.VALID_CHECKOUT_PAGE = false;
	}

	checkCondition() {
		const totalValue = document.querySelector(this.TOTAL_SELECTOR);
		const isInput = document.querySelector(this.APPLIED_CODES_SELECTOR);
		return totalValue && isInput;
	}

	beforeTestCodes() {
		return super.beforeTestCodes().then(() => {
			const formKey = extractData(this.FORM_KEY_SELECTOR, this.cartPageHTML, false, 'value');
			this.APPLY_REQUEST_EXTRA_VALUES.form_key = formKey;
			this.REMOVE_REQUEST_EXTRA_VALUES.form_key = formKey;
			return Promise.resolve();
		});
	}
};

export default {
	name: 'dailysale',
	domain: 'dailysale.com',
	Driver,
	maxCoupons: 10, // due to code testing duration
	isSinglePageApp: true,
};
