import MagentoTemplate from './_base';

import { sessionTimer } from '../../lib';
import { format, DOM } from '../../../utils';

const { elemToFloat } = format;
const { extractElement } = DOM;

/*
Driver behavior:
   Existing code: yes
   Remove code: yes
   Code retesting: yes
   Stackable: no
   Free gift support: no
   isValid implemented: no
   Show message about free shipping: no
   Driver is SPA: no, works on cart
   Gather merchant codes: no
   Special case: max code = 6 (slow work), sessiontimer = 18min.
   Credentials: no
   Test Item(s): -
   Valid Code(s): -, last checked 03-25-2019
*/

const CART_URL = 'https://shop.highlights.com/checkout/cart';

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '#shopping-cart-totals-table tfoot .price';
		this.CART_URL = CART_URL;
		this.NEED_REMOVE_CODE = false;

		this.CHARGES_REGEX = /(shipping|handling|tax)/i;
		this.CHARGES_SELECTOR = '.cart-order-totals tr td:last-child';

		sessionTimer.initialize({
			actionInterval: null,
			timeout: 18,
		});
	}

	async getPreTaxShippingTotal() {
		const cartStr = await fetch(window.location.href, { credentials: 'include' }).then(response => response.text());
		const total = elemToFloat(extractElement(this.TOTAL_SELECTOR, cartStr));
		const taxes = [...extractElement(this.CHARGES_SELECTOR, cartStr, true)]
			.filter(el => el.parentElement.textContent.match(this.CHARGES_REGEX))
			.map(el => elemToFloat(el))
			.filter(Boolean)
			.reduce((charges, charge) => charges + charge, 0); // shipping & tax might not be counted on cart
		return total - taxes;
	}
};

export default {
	name: 'Highlights',
	domain: 'highlights.com',
	maxCoupons: 6, // site is very slow
	Driver,
	isOnCartUrl(href) {
		return href.startsWith(CART_URL);
	},
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
};
