import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { format, log, DOM } from '../../../utils';
import { sessionTimer } from '../../lib';

const { toFloat, elemToFloat } = format;
const { extractElement } = DOM;

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: Cart, Checkout(user can avoid Cart)
+ Is driver SPA: yes
+ Gather codes on site pages: yes (on current page)
+ Session timer: 15min
+ Restore user data: yes
*/

const BASE_URL = 'https://www.easyspirit.com';
const CART_URL = `${BASE_URL}/checkout/cart`;

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.TOTAL_SELECTOR = '.grand.totals.incl .price';
		this.APPLIED_CODES_SELECTOR = '#coupon_code, #discount-code';
		this.CODE_INPUT_SELECTOR = this.APPLIED_CODES_SELECTOR;

		this.SHIPPING_OPTIONS_SELECTOR = '.methods-shipping .radioparent .radio';

		this.isOnCart = window.location.href.startsWith(CART_URL);

		this.QUOTE_ID_SELECTOR = '#gift-options-cart script, #checkout script';
		this.QUOTE_ID_REGEXP = /(entity_id|quoteId)":"([A-Za-z0-9]+)(?=")/;

		this.BANNER_SELECTOR = '.ambanners a';
		this.MERCHANT_CODE_REGEX = /use code (\w+)/i;

		this.MANAGE_CODE_HEADERS = {
			'Content-Type': 'application/json',
			'x-requested-with': 'XMLHttpRequest',
		};

		this.bodyData = null;
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	checkCondition() {
		const isOnHref = window.location.href.startsWith(`${BASE_URL}/checkout`);
		const totalElement = document.querySelector(this.TOTAL_SELECTOR);
		const codeInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		return totalElement && codeInput && isOnHref;
	}

	beforeTestCodes() {
		this.userIsLogged = this._getWindowObjectData('window.checkoutConfig.isCustomerLoggedIn');
		if (this.userIsLogged) {
			this.quoteId = 'mine';
		} else {
			[, , this.quoteId] = extractElement(this.QUOTE_ID_SELECTOR).textContent.match(this.QUOTE_ID_REGEXP);
		}
		if (this.isOnCart) {
			const bodyData = this._getWindowObjectData('window.checkoutConfig.selectedShippingMethod');
			const bodyDataAddress = { countryId: 'US', postcode: '*', regionId: '0' };

			this.bodyData = {
				addressInformation: {
					address: bodyDataAddress,
					shipping_method_code: bodyData ? bodyData.method_code : '',
					shipping_carrier_code: bodyData ? bodyData.carrier_code : '',
				},
			};
		}
	}

	getMerchantCodes() {
		return [...document.querySelectorAll(this.BANNER_SELECTOR)]
			.map(el => el.textContent.match(this.MERCHANT_CODE_REGEX))
			.filter(Boolean)
			.map(el => el[1]);
	}

	getExistingCodes() {
		const codes = [document.querySelector(this.APPLIED_CODES_SELECTOR).value].filter(Boolean);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes() {
		return this._removeCode();
	}

	beforeCheckCodes() {
		return this._getTotalInfo().then(responseJson => ({
			originalPrice: this._parseTotal(responseJson),
			originalItemsCount: this._parseItemsCount(responseJson),
		}));
	}

	checkCodes(codes, originalPrice = this._parseTotal(), originalItemsCount = this._parseItemsCount()) {
		log('originalPrice: %s', originalPrice);
		log('originalItemsCount: %s', originalItemsCount);

		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				freeGifts: 0,
				isValid: false,
			};
			return this._applyCode(code).then(responseJSON => {
				if (!responseJSON.message) {
					return this._getTotalInfo().then(response => {
						result.finalPrice = this._parseTotal(response);
						result.finalDiscount = originalPrice - result.finalPrice;
						result.freeGifts = this._parseItemsCount(response) - originalItemsCount;
						result.isValid = true;

						return this._removeCode().then(() => bus.$emit('code-checked', result));
					});
				}
				return bus.$emit('code-checked', result);
			});
		});
	}

	getPreTaxShippingTotal() {
		return this._getTotalInfo().then(response => {
			const totals = response.totals || response;
			const shipping = toFloat(totals.shipping_amount);
			const tax = toFloat(totals.tax_amount);
			return this._parseTotal(response) - shipping - tax;
		});
	}

	checkRestoreDataCondition() {
		return this._isShippingOptionsElement();
	}

	getUserData() {
		if (!this._isShippingOptionsElement()) {
			return null;
		}

		const checkedShippingElement = [...document.querySelectorAll(this.SHIPPING_OPTIONS_SELECTOR)].find(
			element => element.checked,
		);
		return {
			shippingSelector: `#${checkedShippingElement.getAttribute('id')}`,
		};
	}

	setUserData(userData) {
		document.querySelector(userData.shippingSelector).click();
	}

	_isShippingOptionsElement() {
		return document.querySelector(`${this.SHIPPING_OPTIONS_SELECTOR}:checked`);
	}

	_getTotalInfo() {
		if (this.isOnCart) {
			return fetch(
				`${BASE_URL}/rest/default/V1/${this.userIsLogged ? 'carts' : 'guest-carts'}/${
					this.quoteId
				}/totals-information`,
				{
					method: 'POST',
					credentials: 'include',
					body: JSON.stringify(this.bodyData),
					headers: {
						'Content-Type': 'application/json',
						'X-Requested-With': 'XMLHttpRequest',
					},
				},
			).then(response => response.json());
		}
		return fetch(
			`${BASE_URL}/rest/default/V1/${this.userIsLogged ? 'carts' : 'guest-carts'}/${
				this.quoteId
			}/payment-information`,
			{
				method: 'GET',
				credentials: 'include',
				headers: {
					'Content-Type': 'application/json',
					'X-Requested-With': 'XMLHttpRequest',
				},
			},
		).then(response => response.json());
	}

	_manageCode(method, code = '') {
		return fetch(
			`${BASE_URL}/rest/default/V1/${this.userIsLogged ? 'carts' : 'guest-carts'}/${
				this.quoteId
			}/coupons/${code}`,
			{
				method,
				credentials: 'include',
				body: JSON.stringify(this.bodyData),
				headers: {
					'Content-Type': 'application/json',
					'X-Requested-With': 'XMLHttpRequest',
				},
			},
		).then(response => response.json());
	}

	_applyCode(code) {
		return this._manageCode('PUT', code);
	}

	_removeCode() {
		return this._manageCode('DELETE');
	}

	_parseTotal(data) {
		if (data) {
			return this.isOnCart ? toFloat(data.grand_total) : toFloat(data.totals.grand_total);
		}
		return elemToFloat(extractElement(this.TOTAL_SELECTOR));
	}

	_parseItemsCount(data) {
		if (data) {
			return this.isOnCart ? toFloat(data.items_qty) : toFloat(data.totals.items_qty);
		}
		return document.querySelectorAll(this.ITEM_COUNT_SELECTOR).length;
	}

	_getApplyRequestData() {
		return Promise.resolve();
	}

	_getWindowObjectData(variableString) {
		// We will put generated request payload to this element
		const requestDataElement = document.createElement('div');
		requestDataElement.id = 'get-data-genie';
		Object.assign(requestDataElement.style, { display: 'none' });

		document.documentElement.appendChild(requestDataElement);

		// Script which will generate request payload
		const requestDataScript = document.createElement('script');
		requestDataScript.textContent = `document.getElementById("${
			requestDataElement.id
		}").innerText = JSON.stringify(${variableString});`;
		document.documentElement.appendChild(requestDataScript);
		const data = JSON.parse(requestDataElement.innerText);
		// Remove created blocks
		requestDataElement.parentNode.removeChild(requestDataElement);
		requestDataScript.parentNode.removeChild(requestDataScript);
		return data;
	}
};

export default {
	name: 'Easy Spirit',
	domain: 'easyspirit.com',
	Driver,
	isSinglePageApp: true,
	restoreUserData: true,
};
