import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { DOM, format, log } from '../../../utils';

const { toFloat, elemToFloat } = format;
const { extractElement } = DOM;

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: no
+ Codes can be replaced by another code: yes
+ Manually apply code: no
+ Pages, where extension works: cart page and on payment page
+ Is driver SPA: no
+ Gather codes on site pages: no
+ Need additional request for getting price
*/

const BASE_URL = 'https://www.shiekh.com/checkout';
const BASE_REQUEST_URL = 'https://www.shiekh.com/rest/shiekhcom/V1';
const CART_URL = `${BASE_URL}/cart`;

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.APPLIED_CODES_SELECTOR = '#coupon_code, #discount-code';
		this.TOTAL_SELECTOR = '.grand.totals .price';
		this.TAX_SELECTOR = '.totals-tax';
		this.LOGIN_SELECTOR = '.welcome span';
		this.REMOVE_BUTTON_SELECTOR = '#discount-form .action-cancel';
		this.APPLY_BUTTON_SELECTOR = '#discount-form .action-apply';
		this.ENTITY_ID_REG = /entity_id":"(\w+)/;
		this.ENTITY_ID = null;
		this.URL_STRING = null;
		this.IS_PAYMENT = null;
		this.params = new URLSearchParams();
	}

	checkCondition() {
		const { href } = window.location;
		const isCartHref = href.startsWith(`${BASE_URL}/#payment`) || href.startsWith(CART_URL);
		const isTotal = Boolean(document.querySelector(this.TOTAL_SELECTOR));
		return isCartHref && isTotal;
	}

	beforeTestCodes() {
		[, this.ENTITY_ID] = [...document.querySelectorAll('script')]
			.find(element => element.text.match(this.ENTITY_ID_REG))
			.text.match(this.ENTITY_ID_REG);
		this.URL_STRING = document.querySelector(this.LOGIN_SELECTOR) ? 'carts/mine' : `guest-carts/${this.ENTITY_ID}`;
		this.IS_PAYMENT = window.location.href.startsWith(`${BASE_URL}/#payment`);
		return Promise.resolve();
	}

	getExistingCodes() {
		return Promise.resolve()
			.then(() => (this.IS_PAYMENT ? this._getCodeFromCart() : false))
			.then(html => {
				const codes = [extractElement(this.APPLIED_CODES_SELECTOR, html)]
					.map(element => element.defaultValue)
					.filter(Boolean);

				return {
					codes,
					codeCount: codes.length,
				};
			});
	}

	removeCodes() {
		return this._removeCode()
			.then(() => this._getPrice())
			.then(responseJSON => this._parseTotal(responseJSON));
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);

		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
			};
			return this._applyCode(code).then(response => {
				if (response) {
					return this._getPrice().then(responseJSON => {
						if (!responseJSON) {
							return bus.$emit('code-checked', result);
						}
						result.finalPrice = this._parseTotal(responseJSON);
						result.finalDiscount = originalPrice - result.finalPrice;
						return bus.$emit('code-checked', result);
					});
				}
				return bus.$emit('code-checked', result);
			});
		});
	}

	beforeApplyCodes() {
		document.cookie = 'mage-messages=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
		return Promise.resolve();
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	_removeCode(code) {
		return this._manageCode('1', code);
	}

	_applyCode(code) {
		return this._manageCode('0', code);
	}

	_manageCode(param, code) {
		this.params.set('coupon_code', code);
		this.params.set('remove', param);
		return fetch(`${BASE_URL}/cart/couponPost/`, {
			method: 'POST',
			credentials: 'include',
			body: this.params,
			headers: {
				'X-Requested-With': 'XMLHttpRequest',
			},
		});
	}

	_getPrice() {
		return fetch(`${BASE_REQUEST_URL}/${this.URL_STRING}/payment-information`, {
			method: 'GET',
			credentials: 'include',
			headers: {
				'content-type': 'application/json',
			},
		}).then(response => (response.status === 404 ? null : response.json()));
	}

	_parseTotal(data) {
		if (data) {
			const isTax = Boolean(document.querySelector(this.TAX_SELECTOR));
			return toFloat(data.totals[isTax ? 'base_grand_total' : 'grand_total']);
		}
		return elemToFloat(document.querySelector(this.TOTAL_SELECTOR));
	}

	_getCodeFromCart() {
		return fetch(CART_URL, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	name: 'Shiekh',
	domain: 'shiekh.com',
	Driver,
	maxCoupons: 10,
	isSinglePageApp: true,
};
