import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { format, DOM, log, completionActions } from '../../../utils';

const { elemToFloat, toFloat } = format;
const { extractElement, setValue } = DOM;

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no, will be implemented in EXT-1160
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: yes, on Payment
+ Pages, where extension works: Cart, Payment
+ Is driver SPA: yes
+ Gather codes on site pages: yes, from Cart page
*/

const isOnCartPage = window.location.href.includes('cart');

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		const BASE_URL = 'https://www.solesociety.com';
		this.ORDER_SUMMARY_URL = `${BASE_URL}/customer/section/load`;
		this.CART_URL = `${BASE_URL}/checkout/cart`;
		this.CART_URL_PAYMENT = `${BASE_URL}/checkout/#payment`;
		this.APPLIED_CODE_SELECTOR = '.totals.discount .code, .totals.discount.cart-summary .code';
		this.TOTAL_SELECTOR = '.grand .price';
		this.MERCHANT_CODES_SELECTOR = '.header-page-promo-banner';
		this.SPECIAL_MERCHANT_CODES_SELECTOR = '.bx-click-to-copy';
		this.MERCHANT_CODES_REGEX = /use code:?\s?(\w+)/i;
		this.PROMO_CODES_REGEX = /promoCode":"([\w,]+)/i;
		this.TOTAL_REGEX = /"grand_total":"?([0-9.]+)"?/i;
		this.CODE_INPUT_SELECTOR = '#discount-code-fake';
		this.APPLY_BUTTON_SELECTOR = '#discount-form .action-add';
	}

	checkCondition() {
		const totalIsRendered = document.querySelector(this.TOTAL_SELECTOR);
		const onRightURL =
			window.location.href.startsWith(this.CART_URL) || window.location.href.startsWith(this.CART_URL_PAYMENT);
		return totalIsRendered && onRightURL;
	}

	beforeTestCodes() {
		this.params = new URLSearchParams({ isAjax: 1 });
		this.queryStringParams = new URLSearchParams({
			sections: 'cart',
			update_section_id: false,
		});
	}

	getMerchantCodes() {
		const codesFromPage = [...document.querySelectorAll(this.SPECIAL_MERCHANT_CODES_SELECTOR)]
			.map(el => el.textContent)
			.filter(Boolean);
		return (window.location.href.startsWith(this.CART_URL) ? Promise.resolve(null) : this._getUpdatedCart()).then(
			response => [
				...new Set([
					...codesFromPage,
					...[...extractElement(this.MERCHANT_CODES_SELECTOR, response, true)]
						.map(el => el.textContent.match(this.MERCHANT_CODES_REGEX))
						.filter(Boolean)
						.map(el => el[1]),
				]),
			],
		);
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODE_SELECTOR)]
			.map(el => el.textContent)
			.filter(Boolean);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes(codes) {
		return this._removeCode(codes[0]).then(responseJSON => this._parseTotal(responseJSON));
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				isValid: true,
			};
			return this._applyCode(code).then(data => {
				if (data.errors) {
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}

				result.finalPrice = this._parseTotal(data);
				result.finalDiscount = originalPrice - result.finalPrice;
				return this._removeCode(code).then(() => bus.$emit('code-checked', result));
			});
		});
	}

	applyCodes(codes) {
		return isOnCartPage ? this._applyCode(codes[0]) : this._manuallyApplyCode(codes[0]);
	}

	_manuallyApplyCode(code) {
		setValue(document.querySelector(this.CODE_INPUT_SELECTOR), code);
		document.querySelector(this.APPLY_BUTTON_SELECTOR).click();
	}

	_applyCode(code) {
		return this._manageCode(code, false);
	}

	_removeCode(code) {
		return this._manageCode(code, true);
	}

	_manageCode(code, isRemoving) {
		const parsedCodes = decodeURI(code);
		this.params.set('remove_coupon', isRemoving ? parsedCodes : false);
		this.params.set('last_code', parsedCodes);
		this.params.set('coupon_code', isRemoving ? '' : parsedCodes);
		return fetch(`${this.CART_URL}/couponPost`, {
			method: 'POST',
			credentials: 'include',
			body: this.params,
		}).then(response => response.json());
	}

	_getUpdatedCart() {
		return fetch(this.CART_URL).then(res => res.text());
	}

	_parseTotal(data) {
		if (data) {
			const total = data.totals ? data.totals.grand_total : data.match(this.TOTAL_REGEX)[1];
			return toFloat(total);
		}
		return elemToFloat(document.querySelector(this.TOTAL_SELECTOR));
	}
};

export default {
	name: 'Sole Society',
	domain: 'solesociety.com',
	isSinglePageApp: true,
	completionAction: isOnCartPage ? completionActions.default : completionActions.none,
	Driver,
};
