import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { log, format, DOM } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat } = format;

/* Behavior description:
- we can apply code;
- we can remove code;
- we can apply only one code (not stackable);
- we can apply code only on cart page;
- sometimes we cannot get the correct response from the server, and we need additional recursive requests
but if the number of requests is 4, we mark the codes as invalid
*/

const CART_URL = 'https://www.dellrefurbished.com/checkout/cart';

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.ERROR_SELECTOR = '.error-msg';
		this.recursiveCount = 0;
		this.IS_INVALID = false;
		this.ORDER_SUMMARY_SELECTOR = '#shopping-cart-totals-table tr';
	}

	applyCodes(codes) {
		return this._applyCode(codes[0], this._getApplyRequestData(this.cartPageHTML)).then(() => {
			// for cleaning error messages after applying code (cant avoid applying invalid code on checkout)
			const iframeElem = document.createElement('iframe');
			iframeElem.src = CART_URL;
			Object.assign(iframeElem.style, { display: 'none' });
			document.documentElement.appendChild(iframeElem);
			return Promise.resolve();
		});
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log('originalPrice: %s', originalPrice);
		return Promise.each(codes, code =>
			this._applyCode(code, this._getApplyRequestData(this.cartPageHTML)).then(responseString => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					isValid: false,
				};
				if (!extractElement(this.ERROR_SELECTOR, responseString) && !this.IS_INVALID) {
					result.finalPrice = this._parseTotal(responseString);
					result.finalDiscount = originalPrice - result.finalPrice;
					result.isValid = true;
				}
				return bus.$emit('code-checked', result);
			}),
		);
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(responseHTML => {
			const getSummaryValue = regex => {
				const valueElement = [...extractElement(this.ORDER_SUMMARY_SELECTOR, responseHTML, true)].find(
					element => regex.test(element.textContent),
				);

				return valueElement ? elemToFloat(valueElement.querySelector('td span.price')) : 0;
			};
			const shipping = getSummaryValue(/shipping/i);
			const tax = getSummaryValue(/tax/i);

			return this._parseTotal(responseHTML) - shipping - tax;
		});
	}

	_getUpdatedCart() {
		return fetch(CART_URL, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	_applyCode(code, requestData) {
		requestData.params.set(this.APPLY_CODE_PARAM_NAME, code);
		return this._manageCode(requestData.action, requestData.params).then(responseHTML => {
			if (!extractElement(this.TOTAL_SELECTOR, responseHTML) && this.recursiveCount < 5) {
				this.recursiveCount = this.recursiveCount === 4 ? 'expired' : this.recursiveCount + 1;
				return this._applyCode(code, requestData);
			}
			if (!extractElement(this.TOTAL_SELECTOR, responseHTML) && this.recursiveCount === 'expired') {
				this.IS_INVALID = true;
				return responseHTML;
			}
			this.IS_INVALID = false;
			this.recursiveCount = 0;
			return responseHTML;
		});
	}
};

export default {
	name: 'Dell Refurbished',
	domain: 'dellrefurbished.com',
	Driver,
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) && document.querySelector('#coupon_code');
	},
};
