import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { log } from '../../../utils';
import { sessionTimer } from '../../lib';

/* Behavior description:
 * - can get existing codes
 * - can remove codes
 * - valid code can be replaced with another valid code
 * - can apply only one coupon code
 *
 * ======== Special case ========
 * remove support of 'Checkout' page due to merchants problems
 * Session timer 15 min
 */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = 'https://www.mypillow.com/checkout/cart';
		this.TOTAL_SELECTOR = '.grand .price';

		this.totalRegExp = {
			regexObj: /"grand_total":"(\d+\.\d+)/,
			groupNumber: 1,
		};

		this.INPUT_CODE_SELECTOR = '#coupon_code, #discount-code';
		this.EMPTY_CART_SELECTOR = '.cart-empty';
		this.SPINER_SELECTOR = '.table-wrapper._block-content-loading';

		// Clear this cookie to remove 'applied code' messages
		document.cookie = 'mage-messages=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	checkCondition() {
		const totalValue = document.querySelector(this.TOTAL_SELECTOR);
		const isInputElement = document.querySelector(this.INPUT_CODE_SELECTOR);
		const emptyCart = document.querySelector(this.EMPTY_CART_SELECTOR);
		const spiner = document.querySelector(this.SPINER_SELECTOR);
		const correctPage = window.location.href.startsWith(this.CART_URL);
		return totalValue && isInputElement && correctPage && !emptyCart && !spiner;
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log('originalPrice: %s', originalPrice);

		return Promise.each(codes, code =>
			this._applyCode(code, this._getApplyRequestData(this.cartPageHTML)).then(responseString => {
				const finalPrice = this._parseTotal(responseString);
				const finalDiscount = originalPrice - finalPrice;
				const result = { code, finalPrice, finalDiscount, isValid: false };

				if (finalDiscount > 0) {
					// there are no invalid code error messages
					result.isValid = true;
				}

				return bus.$emit('code-checked', result);
			}),
		);
	}

	applyCodes(codes) {
		return super.applyCodes(codes).then(() => {
			// for cleaning error messages after applying code
			const iframeElem = document.createElement('iframe');
			iframeElem.src = this.CART_URL;
			Object.assign(iframeElem.style, { display: 'none' });
			document.documentElement.appendChild(iframeElem);
			return Promise.resolve();
		});
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(responseHTML => {
			return this._parseTotal(responseHTML);
		});
	}
};

export default {
	name: 'MyPillow',
	domain: 'mypillow.com',
	Driver,
	maxCoupons: 8, // site can be slow
	isSinglePageApp: true,
	isOnCartUrl() {
		return false;
	},
};
