import MagentoTemplate from './_base';

const BASE_URL = 'https://www.straighterline.com/checkout';
const CART_URL = `${BASE_URL}/cart/`;
const CHECKOUT_URL = `${BASE_URL}/onepage/`;

/* Behaviour description:
- we can apply only one code;
- we can remove code;
- invalid code removes valid */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.NEED_REMOVE_CODE = false;

		this.TOTAL_SELECTOR = '#shopping-cart-totals-table strong .price, .mini-products-list-total-wrapper strong';
		this.EMPTY_CART_SELECTOR = '.cart-empty';
		this.BILLING_STEP_SELECTOR = '#opc-billing';
	}

	checkCondition() {
		const isCouponInput = document.querySelector(this.APPLIED_CODES_SELECTOR);
		const billingStep = document.querySelector(this.BILLING_STEP_SELECTOR);
		const isActiveBillingStep = billingStep && billingStep.getAttribute('class').includes('active');
		const currentState =
			(window.location.href.startsWith(CART_URL) ||
				(window.location.href.startsWith(CHECKOUT_URL) && isActiveBillingStep)) &&
			isCouponInput;
		return currentState;
	}
};

export default {
	name: 'Straighterline',
	domain: 'straighterline.com',
	maxCoupons: 7,
	Driver,
	isSinglePageApp: true,
};
