import MagentoTemplate from './_base';
import { DOM } from '../../../utils';

const { extractData } = DOM;

const CART_URL = 'https://www.triabeauty.co.uk/checkout/cart';

/** Behavior description:
 * - can get existing codes
 * - can remove codes
 * - valid code can't be replaced with another valid code
 * - can't apply multiple codes
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = `${CART_URL}/`;

		this.TOTAL_SELECTOR = '.grand .price';
		this.STACKABLE = false;

		this.afterCodeCheck = () => {
			// Clear this cookie to remove 'applied code' messages
			document.cookie = 'mage-messages=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
		};

		this.REG_TOTAL = /"Grand Total","value":"([0-9.-]+)/;
	}

	_parseTotal(data = false) {
		return [...extractData('script', data, true)]
			.find(element => element.match(this.REG_TOTAL))
			.match(this.REG_TOTAL)[1];
	}
};

export default {
	name: 'Tria Beauty UK',
	domain: 'triabeauty.co.uk',
	Driver,
	maxCoupons: 10, // site is very slow
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) && document.querySelector('#coupon_code');
	},
};
