import MagentoTemplate from './_base';
import bus from '../../bus';

const CART_URL = 'https://www.karmaloop.com/checkout/cart';
const CHECKOUT_URL = 'https://www.karmaloop.com/checkout/#payment';

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.TOTAL_SELECTOR = '.grand .price, .grand.totals .price';
		this.totalRegExp = {
			regexObj: /subtotal_with_discount":"(\d+\.\d+)/,
			groupNumber: 1,
		};
	}

	watchForCartUrl() {
		let previousState = false;
		setInterval(() => {
			// Activate extension popup only on when the total has been rendered.
			const totalValue = document.querySelector(this.TOTAL_SELECTOR);
			const onCart = window.location.href.startsWith(CART_URL);
			const onRightUrl = window.location.href.startsWith(CHECKOUT_URL) || onCart;

			const currentState = totalValue && (onCart || onRightUrl);

			if (currentState !== previousState) {
				if (currentState) {
					bus.$emit('cart-active');
				} else {
					bus.$emit('cart-inactive');
				}
			}
			previousState = currentState;
		}, 1000);
	}
};

export default {
	maxCoupons: 10,
	name: 'Karmaloop',
	domain: 'karmaloop.com',
	isSinglePageApp: true,
	Driver,
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) || href.startsWith(CHECKOUT_URL);
	},
};
