import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';

/** Behavior description:
 * can get existing code
 * can remove code
 * valid code can be replaced with another valid or invalid code
 * isn' stackable
 * code can be applied on cart page and on payment page
 * */

const BASE_URL = 'https://www.elaineturner.com/checkout';
const CART_URL = `${BASE_URL}/cart`;

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;
		this.TOTAL_SELECTOR = '.grand.totals .price';
		this.totalRegExp = {
			regexObj: /grand_total":"(\d+\.\d+)/,
			groupNumber: 1,
		};
		this.afterCodeCheck = () => {
			// Clear this cookie to remove 'applied code' messages
			document.cookie = 'mage-messages=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
		};
	}

	watchForCartUrl() {
		let lastState = false;
		setInterval(() => {
			const { href } = window.location;
			const isCartHref = href.startsWith(`${BASE_URL}/#payment`) || href.startsWith(CART_URL);
			const isTotal = Boolean(document.querySelector(this.TOTAL_SELECTOR));
			const currentState = isCartHref && isTotal;
			if (lastState !== currentState) {
				bus.$emit(!lastState ? 'cart-active' : 'cart-inactive');
			}
			lastState = currentState;
		}, 1000);
	}

	// remove _removeCodes() in order to reduce number of queries
	checkCodes(codes, originalPrice = this._parseTotal()) {
		return Promise.each(codes, code =>
			this._applyCode(code, this._getApplyRequestData(this.cartPageHTML)).then(responseString => {
				const finalPrice = this._parseTotal(responseString);
				const finalDiscount = originalPrice - finalPrice;
				return bus.$emit('code-checked', { code, finalPrice, finalDiscount });
			}),
		);
	}
};

export default {
	name: 'Elaine Turner',
	domain: 'elaineturner.com',
	Driver,
	isSinglePageApp: true,
	isOnCartUrl(href) {
		return href.startsWith(`${BASE_URL}/#payment`) || href.startsWith(CART_URL);
	},
};
