import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { format, log, DOM, sendXhr } from '../../../utils';

const { toFloat, elemToFloat } = format;
const { extractElement } = DOM;

const BASE_URL = 'https://www.spanx.com';
const CART_URL = `${BASE_URL}/checkout/cart`;

/* Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: Cart, Checkout(only first step for not loged in user, all steps for logged in)
+ Is driver SPA: yes
+ Gather codes on site pages: no
Optional:
Not logged in user can't test codes on shipping/payment pages, because his shipping options sets to default after reloading page
*/

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;

		this.IS_ON_CART = window.location.href.startsWith(CART_URL);
		this.APPLIED_CODES_SELECTOR = this.IS_ON_CART ? '.amCouponsCode' : '#coupon_code, #discount-code';
		this.TOTAL_SELECTOR = '.grand.totals .price';
		this.SHIPPING_FORM_SELECTOR = '#co-shipping-method-form';
		this.SHIPPING_METHOD_SELECTOR = '[name="shipping_method"]';
		this.NEWRELIC_ID_REGEX = /xpid:"([\w=]*)"/;

		this.NEED_REMOVE_CODE = false;

		this.afterCodeCheck = () => {
			// Clear this cookie to remove 'applied code' messages
			document.cookie = 'mage-messages=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
		};

		this.QUOTE_ID_REGEXP = /entity_id":"([A-Za-z0-9]+)(?=")/;

		this.MANAGE_CODE_HEADERS = {
			'Content-Type': 'application/json',
			'x-requested-with': 'XMLHttpRequest',
		};

		this.bodyData = null;
	}

	checkCondition() {
		const correctPage = window.location.href.startsWith(BASE_URL);
		const isShippingPage = this._isShippingOption();
		const totalValue = document.querySelector(this.TOTAL_SELECTOR);

		return (
			totalValue &&
			correctPage &&
			(this._getWindowObjectData('window.checkoutConfig.isCustomerLoggedIn') ? true : !isShippingPage)
		);
	}

	checkRestoreDataCondition() {
		return this._isShippingOption() && (document.querySelector('.loader') || {}).offsetParent;
	}

	getUserData() {
		if (!this._isShippingOption()) {
			return null;
		}
		const checkedShippingElement = [...document.querySelectorAll(this.SHIPPING_METHOD_SELECTOR)].find(
			element => element.checked,
		);
		return {
			shippingSelector: `#${checkedShippingElement.getAttribute('id')}`,
		};
	}

	setUserData(userData) {
		document.querySelector(userData.shippingSelector).click();
	}

	beforeTestCodes() {
		this.isTotalwithoutTaxes =
			window.location.href.startsWith(CART_URL) && !document.querySelector('.totals-tax span.price');
		this.userIsLogged = this._getWindowObjectData('window.checkoutConfig.isCustomerLoggedIn');
		[, this.newrelicId] = [...document.scripts]
			.find(el => el.text.match(this.NEWRELIC_ID_REGEX))
			.text.match(this.NEWRELIC_ID_REGEX);
		if (this.userIsLogged) {
			this.quoteId = 'mine';
		} else {
			const quoteScript = [...document.scripts].find(el => el.text.match(this.QUOTE_ID_REGEXP));
			[, this.quoteId] = quoteScript.text.match(this.QUOTE_ID_REGEXP);
		}
		if (window.location.href.startsWith(CART_URL)) {
			const bodyData = this._getWindowObjectData('window.checkoutConfig.selectedShippingMethod');
			const bodyDataAddress = { countryId: 'US', postcode: '*', regionId: '0' };

			this.bodyData = {
				addressInformation: {
					address: bodyDataAddress,
					shipping_method_code: bodyData ? bodyData.method_code : '',
					shipping_carrier_code: bodyData ? bodyData.carrier_code : '',
				},
			};
		}
	}

	getExistingCodes() {
		const codes = [document.querySelector(this.APPLIED_CODES_SELECTOR)]
			.filter(Boolean)
			.map(element => element.value)
			.filter(Boolean);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes() {
		return this._removeCode();
	}

	beforeCheckCodes() {
		return this._getTotalInfo().then(responseJson => ({
			originalPrice: this._parseTotal(responseJson),
			originalItemsCount: this._parseItemsCount(responseJson),
		}));
	}

	checkCodes(codes, originalPrice = this._parseTotal(), originalItemsCount = this._parseItemsCount()) {
		log('originalPrice: %s', originalPrice);
		log('originalItemsCount: %s', originalItemsCount);

		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				freeGifts: 0,
				isValid: false,
			};
			return this._applyCode(code).then(responseJSON => {
				if (!responseJSON.message) {
					return this._getTotalInfo().then(response => {
						result.finalPrice = this._parseTotal(response);
						result.finalDiscount = originalPrice - result.finalPrice;
						result.freeGifts = this._parseItemsCount(response) - originalItemsCount;
						result.isValid = true;

						return this._removeCode().then(() => bus.$emit('code-checked', result));
					});
				}
				return bus.$emit('code-checked', result);
			});
		});
	}

	getPreTaxShippingTotal() {
		return this._getTotalInfo().then(responseJSON => {
			const totalsData = this.isTotalwithoutTaxes ? responseJSON : responseJSON.totals;
			const total = this._parseTotal(responseJSON);
			const shipping = toFloat(totalsData.base_shipping_amount);
			const taxes = toFloat(totalsData.base_tax_amount);
			return total - shipping - taxes;
		});
	}

	_getTotalInfo() {
		if (this.isTotalwithoutTaxes) {
			return sendXhr(
				`${BASE_URL}/rest/default/V1/${this.userIsLogged ? 'carts' : 'guest-carts'}/${
					this.quoteId
				}/totals-information`,
				{
					method: 'POST',
					credentials: 'include',
					body: JSON.stringify(this.bodyData),
					headers: {
						'Content-Type': 'application/json',
						'listrak-listening': 1,
						'x-newrelic-id': this.newrelicId,
						'X-Requested-With': 'XMLHttpRequest',
					},
				},
			).then(response => response.json());
		}
		return sendXhr(
			`${BASE_URL}/rest/default/V1/${this.userIsLogged ? 'carts' : 'guest-carts'}/${
				this.quoteId
			}/payment-information`,
			{
				method: 'GET',
				credentials: 'include',
				headers: {
					'Content-Type': 'application/json',
					'X-Requested-With': 'XMLHttpRequest',
				},
			},
		).then(response => response.json());
	}

	_manageCode(method, code = '') {
		return sendXhr(
			`${BASE_URL}/rest/default/V1/${this.userIsLogged ? 'carts' : 'guest-carts'}/${
				this.quoteId
			}/coupons/${code}`,
			{
				method,
				credentials: 'include',
				body: JSON.stringify(this.bodyData),
				headers: {
					'Content-Type': 'application/json',
					'X-Requested-With': 'XMLHttpRequest',
				},
			},
		).then(response => response.json());
	}

	_applyCode(code) {
		return this._manageCode('PUT', code);
	}

	_removeCode() {
		return this._manageCode('DELETE');
	}

	_parseTotal(data) {
		if (data) {
			return this.isTotalwithoutTaxes ? toFloat(data.base_grand_total) : toFloat(data.totals.base_grand_total);
		}
		return elemToFloat(extractElement(this.TOTAL_SELECTOR));
	}

	_parseItemsCount(data) {
		if (data) {
			return this.isTotalwithoutTaxes ? toFloat(data.items_qty) : toFloat(data.totals.items_qty);
		}
		return document.querySelectorAll(this.ITEM_COUNT_SELECTOR).length;
	}

	_getApplyRequestData() {
		return Promise.resolve();
	}

	_isShippingOption() {
		return (document.querySelector(this.SHIPPING_FORM_SELECTOR) || {}).offsetParent;
	}

	_getWindowObjectData(variableString) {
		// We will put generated request payload to this element
		const requestDataElement = document.createElement('div');
		requestDataElement.id = 'get-data-genie';
		Object.assign(requestDataElement.style, { display: 'none' });

		document.documentElement.appendChild(requestDataElement);

		// Script which will generate request payload
		const requestDataScript = document.createElement('script');
		requestDataScript.textContent = `document.getElementById("${
			requestDataElement.id
		}").innerText = JSON.stringify(${variableString});`;
		document.documentElement.appendChild(requestDataScript);
		const data = JSON.parse(requestDataElement.innerText);
		// Remove created blocks
		requestDataElement.parentNode.removeChild(requestDataElement);
		requestDataScript.parentNode.removeChild(requestDataScript);
		return data;
	}
};

export default {
	name: 'Spanx',
	domain: 'spanx.com',
	isSinglePageApp: true,
	Driver,
	isOnCartUrl() {
		return false;
	},
	restoreUserData: true,
};
