import Promise from 'bluebird';
import bus from '../../bus';
import BaseDriver from '../base';
import { DOM, format, log } from '../../../utils';

const { toFloat } = format;
const { extractElementFromHTML, extractFormValues, extractElement } = DOM;

// This template driver is for merchants built on the Magento CMS

/* Similar behavior:
 * Code can be applied on cart (POST). Sometimes - on checkout ( PUT request).
 * Requests and selectors differ on cart and checkout!
 * Invalid code removes a valid one. This action doesn't always refresh the checkout/cart page, so we need to specify the appropriate switch.
 */

export default class Driver extends BaseDriver {
	constructor() {
		super();
		this.CART_URL = ''; // Cart url without slash at the end.

		this.APPLIED_CODES_SELECTOR = '#coupon_code'; // On some websites this may differ.
		this.TOTAL_SELECTOR = '#shopping-cart-totals-table strong .price'; // On some websites this may differ.

		this.ITEM_CONTAINER_SELECTOR = null;

		this.APPLY_COUPON_FORM_SELECTOR = '#discount-coupon-form'; // Ususally this doesn't change.
		// Not obligatory. Change only if remove code request is different.
		this.REMOVE_COUPON_FORM_SELECTOR = this.APPLY_COUPON_FORM_SELECTOR;

		this.APPLY_CODE_PARAM_NAME = 'coupon_code';
		this.APPLY_REQUEST_EXTRA_VALUES = {
			remove: 0,
		};

		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;
		this.REMOVE_REQUEST_EXTRA_VALUES = {
			remove: 1,
		};

		// Switches:

		this.VALID_CHECKOUT_PAGE = false;

		// If website is stackable, set this value to true.
		this.STACKABLE = false;

		// If valid codes don't replace each other, need to remove them after each applying
		this.NEED_REMOVE_CODE = true;

		// In case there's a need to add custom headers they can be added here.
		this.MANAGE_CODE_HEADERS = {
			'Content-Type': 'application/x-www-form-urlencoded',
		};

		// Function to be executed after codes check is completed.
		this.afterCodeCheck = null;

		// Regex for parsing total {regexObj: REGEX_OBJ, groupNumber: 0};
		this.totalRegExp = null;

		// REGEX for parsing items count
		this.itemsCountRegExp = null;
	}

	beforeTestCodes() {
		// We will save here cart page to use it if needed on checkout page
		this.cartPageHTML = null; // move to 'beforeTestCodes()' to avoid error in FF

		// Save cartPage in case checkout doesn't have all required data
		if (!this._isOnCart() && !this.VALID_CHECKOUT_PAGE) {
			return this._getUpdatedCart().then(response => {
				this.cartPageHTML = response;
			});
		}
		return Promise.resolve();
	}

	getExistingCodes() {
		// debugger;
		const codesElement = this.cartPageHTML
			? extractElementFromHTML(this.cartPageHTML, this.APPLIED_CODES_SELECTOR)
			: document.querySelector(this.APPLIED_CODES_SELECTOR);

		const codes = [codesElement.getAttribute('value')].filter(el => Boolean(el));
		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes(codes) {
		return Promise.each(codes, code => this._removeCode(code, this._getRemoveRequestData(this.cartPageHTML)))
			.then(() => this._getUpdatedCart()) // Promise.each() return array of applied codes.
			.then(responseString => ({
				originalPrice: this._parseTotal(responseString),
				originalItemsCount: this._parseItemsCount(responseString),
			}));
	}

	checkCodes(codes, originalPrice = this._parseTotal(), originalItemsCount = this._parseItemsCount()) {
		const workingDiscounts = [];

		log('originalPrice: %s', originalPrice);
		log('originalItemsCount: %s', originalItemsCount);

		return Promise.each(codes, code =>
			this._applyCode(code, this._getApplyRequestData(this.cartPageHTML)).then(responseString => {
				const finalPrice = this._parseTotal(responseString);
				const finalDiscount = originalPrice - finalPrice;
				const freeGifts = this._parseItemsCount(responseString) - originalItemsCount;
				const result = { code, finalPrice, finalDiscount, freeGifts };

				if (finalDiscount > 0 || (finalPrice === originalPrice && freeGifts > 0)) {
					workingDiscounts.push(result);
				}
				if (this.NEED_REMOVE_CODE) {
					return this._removeCode(code, this._getRemoveRequestData(responseString)).then(() =>
						bus.$emit('code-checked', result),
					);
				}
				return bus.$emit('code-checked', result);
			}),
		).then(() => {
			if (this.STACKABLE) {
				bus.$emit('stack-codes', {
					codes: workingDiscounts.map(discount => discount.code),
					discounts: workingDiscounts,
					originalPrice,
					originalItemsCount,
				});
			}
			return Promise.resolve();
		});
	}

	stackCodes(validationInfo) {
		const { discounts, originalPrice, originalItemsCount } = validationInfo;

		discounts.sort((a, b) => {
			if (b.finalDiscount - a.finalDiscount === 0) {
				return b.freeGifts - a.freeGifts;
			}
			return b.finalDiscount - a.finalDiscount;
		});

		let currentPrice = originalPrice;
		let currentDiscount = 0;
		let currentFreeGiftsCount = 0;
		const successfulCodes = [];

		return Promise.each(discounts, discount =>
			this._applyCode(discount.code, this._getApplyRequestData()).then(responseString => {
				const finalPrice = this._parseTotal(responseString);
				const finalDiscount = originalPrice - finalPrice;
				const freeGifts = this._parseItemsCount(responseString) - originalItemsCount;

				bus.$emit('code-stacked', {
					code: discount.code,
					finalPrice,
					finalDiscount,
					freeGifts,
				});

				if (finalPrice < currentPrice || (finalPrice === currentPrice && freeGifts > currentFreeGiftsCount)) {
					successfulCodes.push(discount.code);
					currentDiscount = finalDiscount;
					currentPrice = finalPrice;
					currentFreeGiftsCount = freeGifts;
					return Promise.resolve();
				}
				return this._removeCode(discount.code, this._getRemoveRequestData(responseString));
			}),
		).then(() =>
			bus.$emit('stackable-complete', {
				codes: successfulCodes,
				finalPrice: currentPrice,
				finalDiscount: currentDiscount,
				freeGifts: currentFreeGiftsCount,
			}),
		);
	}

	applyCodes(codes) {
		if (this.afterCodeCheck) {
			this.afterCodeCheck();
		}
		return this.STACKABLE
			? Promise.resolve()
			: this._applyCode(codes[0], this._getApplyRequestData(this.cartPageHTML));
	}

	_applyCode(code, requestData) {
		requestData.params.set(this.APPLY_CODE_PARAM_NAME, code);
		return this._manageCode(requestData.action, requestData.params);
	}

	_removeCode(code, requestData) {
		if (!requestData) {
			// No `requestData` was passed (=found), so code was not applied, nothing to remove
			return Promise.resolve();
		}
		requestData.params.set(this.REMOVE_CODE_PARAM_NAME, code);
		return this._manageCode(requestData.action, requestData.params);
	}

	_manageCode(requestURL, bodyObj) {
		return fetch(requestURL, {
			method: 'POST',
			credentials: 'include',
			headers: this.MANAGE_CODE_HEADERS,
			body: bodyObj,
		}).then(response => response.text());
	}

	_parseTotal(htmlString) {
		let totalElement;
		if (htmlString && this.totalRegExp) {
			totalElement = htmlString.match(this.totalRegExp.regexObj)[this.totalRegExp.groupNumber];
		} else if (htmlString) {
			totalElement = extractElementFromHTML(htmlString, this.TOTAL_SELECTOR).textContent;
		} else {
			totalElement = document.querySelector(this.TOTAL_SELECTOR).textContent;
		}
		return toFloat(totalElement);
	}

	_parseItemsCount(htmlString) {
		if (htmlString && this.itemsCountRegExp) {
			return htmlString.match(this.itemsCountRegExp.regexObj)[this.itemsCountRegExp.groupNumber];
		}
		return this.ITEM_CONTAINER_SELECTOR ? extractElement(this.ITEM_CONTAINER_SELECTOR, htmlString, true).length : 0;
	}

	_getUpdatedCart() {
		return fetch(this.CART_URL, {
			credentials: 'include',
		}).then(response => response.text());
	}

	_isOnCart() {
		return window.location.href.startsWith(this.CART_URL);
	}

	_getApplyRequestData(htmlString) {
		if (!this.applyRequestData) {
			// Assumed that this form always exists
			this.applyRequestData = extractFormValues(this.APPLY_COUPON_FORM_SELECTOR, htmlString);
			Object.entries(this.APPLY_REQUEST_EXTRA_VALUES).forEach(([key, value]) => {
				this.applyRequestData.params.set(key, value);
			});
		}
		return this.applyRequestData;
	}

	_getRemoveRequestData(htmlString) {
		if (!this.removeRequestData) {
			// Form may not exists if there is no code applied
			this.removeRequestData = extractFormValues(this.REMOVE_COUPON_FORM_SELECTOR, htmlString);
			//  If we succeeded, add extra values
			if (this.removeRequestData) {
				Object.entries(this.REMOVE_REQUEST_EXTRA_VALUES).forEach(([key, value]) => {
					this.removeRequestData.params.set(key, value);
				});
			}
		}
		return this.removeRequestData;
	}
}
