import Promise from 'bluebird';
import MagentoTemplate from './_base';
import bus from '../../bus';
import { completionActions, DOM, format, log } from '../../../utils';

const { toFloat } = format;
const { setValue } = DOM;

const CART_URL = 'https://membership.shrm.org/join/register/';

/** Behavior description:
 * - can get existing codes
 * - can remove codes
 * - valid code can be replaced with another valid code
 * - can`t apply multiple codes (stackable)
 * */

const Driver = class Driver extends MagentoTemplate {
	constructor() {
		super();
		this.CART_URL = CART_URL;

		this.TOTAL_SELECTOR = '.data-table .even:last-child .price .price .price';
		this.TOTAL_SELECTOR_REGEX = /\$[A-Za-z0-9]*/;
		this.CODE_APPLIED_REGEX = /error-msg/;
		this.REQUEST_URL = 'https://membership.shrm.org/index/couponPost';

		this.APPLY_REQUEST_EXTRA_VALUES = {
			remove: 0,
			product_discount_id: '',
		};

		this.REMOVE_REQUEST_EXTRA_VALUES = this.APPLY_REQUEST_EXTRA_VALUES;

		this.INPUT_CODE_SELECTOR = this.APPLIED_CODES_SELECTOR;
		this.APPLY_BTN_SELECTOR = '.btn-apply';
		this.INPUT_FIELD_SELECTOR = '#coupon_code';
	}

	watchForCartUrl() {
		let previousState = false;
		setInterval(() => {
			const couponInputElement = document.querySelector(this.INPUT_FIELD_SELECTOR);
			const cartPage = window.location.href.startsWith(CART_URL);
			const currentState = couponInputElement && cartPage;
			if (currentState !== previousState) {
				bus.$emit(currentState ? 'cart-active' : 'cart-inactive');
			}
			previousState = currentState;
		}, 500);
	}

	removeCodes(codes) {
		return this._removeCode(codes[0], this._getRemoveRequestData()).then(responseString =>
			this._parseTotal(responseString),
		);
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log('originalPrice: %s', originalPrice);

		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				isValid: false,
			};
			return this._applyCode(code, this._getApplyRequestData()).then(responseJson => {
				if (this._codeAppliedCheck(responseJson.html)) {
					return bus.$emit('code-checked', result);
				}

				result.finalPrice = this._parseTotal(responseJson);
				result.finalDiscount = originalPrice - result.finalPrice;
				result.isValid = true;

				return this._removeCode(code, this._getRemoveRequestData()).then(() =>
					bus.$emit('code-checked', result),
				);
			});
		});
	}

	applyCodes(codes) {
		const INPUT_CODE_ELEMENT = document.querySelector(this.INPUT_CODE_SELECTOR);
		const APPLY_BTN_ELEMENT = document.querySelector(this.APPLY_BTN_SELECTOR);

		setValue(INPUT_CODE_ELEMENT, codes[0]);
		APPLY_BTN_ELEMENT.click();
	}

	_codeAppliedCheck(data) {
		return Boolean(this.CODE_APPLIED_REGEX.test(data));
	}

	_getApplyRequestData() {
		if (!this.applyRequestData) {
			// Assumed that this form always exists
			this.applyRequestData = new URLSearchParams();
			this.applyRequestData.set('params', '');
			this.applyRequestData.action = this.REQUEST_URL;
			this.applyRequestData.params = new URLSearchParams();

			Object.entries(this.APPLY_REQUEST_EXTRA_VALUES).forEach(([key, value]) => {
				this.applyRequestData.params.set(key, value);
			});
		}
		return this.applyRequestData;
	}

	_getRemoveRequestData() {
		if (!this.removeRequestData) {
			// Form may not exists if there is no code applied
			this.removeRequestData = new URLSearchParams();
			this.removeRequestData.set('params', '');
			this.removeRequestData.action = `${this.REQUEST_URL}/?remove=1`;
			this.removeRequestData.params = new URLSearchParams();
			//  If we succeeded, add extra values
			if (this.removeRequestData) {
				Object.entries(this.REMOVE_REQUEST_EXTRA_VALUES).forEach(([key, value]) => {
					this.removeRequestData.params.set(key, value);
				});
			}
		}
		return this.removeRequestData;
	}

	_manageCode(requestURL, bodyObj) {
		return fetch(requestURL, {
			method: 'POST',
			credentials: 'include',
			headers: this.MANAGE_CODE_HEADERS,
			body: bodyObj,
		}).then(response => response.json());
	}

	_parseTotal(htmlString) {
		if (htmlString) {
			const data = htmlString.cart_grand_total;
			return toFloat(data.match(this.TOTAL_SELECTOR_REGEX)[0]);
		}
		return toFloat(document.querySelector(this.TOTAL_SELECTOR).textContent);
	}
};

export default {
	name: 'SHRM',
	domain: 'shrm.org',
	Driver,
	isSinglePageApp: true,
	completionAction: completionActions.none,
	isOnCartUrl() {
		return false;
	},
	// no need to write isCartEmpty() because user can`t go to the checkout page without goods
};
